<template>
    <div class="card">
        <div class="card-header">
            <!--<i class="fa fa-align-justify"></i>-->
            공고문 목록
            <span class="badge list-count">총개수 {{pagination.totalCount}}</span>
            <!--<div style="display: inline;padding-left: 70%;">
                <span>
                    <input type="radio" name="view-type" style="margin-right: 5px;" checked/>
                    리스트로보기
                </span>
                <span style="padding-left: 3rem;">
                    <input type="radio" name="view-type" style="margin-right: 5px;" disabled/>
                    아이콘으로보기
                </span>
            </div>-->
        </div>
        <div class="animated fadeIn">
        <div class="card-body">
            <table class="table table-striped">
                <thead>
                    <tr>
                        <th><input type="checkbox" v-model="allChecked"/></th>
                        <!-- <th>TTS
                        </th> -->
                        <th>공고문명
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.title) + '-circle' + ' sort-icon'" @click="changeSorting('title')" :style="sorting.reqSortingCol === 'title' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th aria-label="Status: activate to sort column ascending">
                            게시시작일
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.postingStart) + '-circle' + ' sort-icon'" @click="changeSorting('postingStart')" :style="sorting.reqSortingCol === 'postingStart' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th aria-label="Status: activate to sort column ascending">
                            게시종료일
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.postingEnd) + '-circle' + ' sort-icon'" @click="changeSorting('postingEnd')" :style="sorting.reqSortingCol === 'postingEnd' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th aria-label="Status: activate to sort column ascending">
                            게시위치
                            <!--<span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.postingEnd) + 'sort-icon'"></i>
                            </span>-->
                        </th>
                        <th aria-label="Status: activate to sort column ascending">
                            중요도
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.priority) + '-circle' + ' sort-icon'" @click="changeSorting('priority')" :style="sorting.reqSortingCol === 'priority' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th aria-label="Status: activate to sort column ascending">
                            장수
                            <!--<span>
                                <i class="icon-arrow-down sort-icon"></i>
                            </span>-->
                        </th>
                        <th aria-label="Status: activate to sort column ascending">
                            게시자
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.publisher) + '-circle' + 'sort-icon'" @click="changeSorting('publisher')" :style="sorting.reqSortingCol === 'publisher' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th aria-label="Status: activate to sort column ascending">
                            태그
                            <!--<span>
                                <i class="icon-arrow-down sort-icon"></i>
                            </span>-->
                        </th>
                        <th colspan="2" aria-label="Status: activate to sort column ascending">
                            원본파일명
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.originFile) + '-circle' + ' sort-icon'" @click="changeSorting('originFile')" :style="sorting.reqSortingCol === 'originFile' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="noti in notiList">
                        <td><input type="checkbox" v-model="noti.checked"/></td>
                        <!-- <td>
                          <i v-if="noti.contents.length>0 && noti.contents[0].ttsInfo.length > 0" class="icon-speech" style="padding-right: 3px;color : green"></i>
                        </td> -->
                        <td>
                          <a class="btn btn-ghost-normal mr-1 text-primary"" :class="(noti.remainDay < 0 ? 'text-secondary' : '')" :title="'생성일: ' + noti.createDate" data-toggle="modal" data-target="#preview-modal"  @click="openPreviewModal(noti)">
                            <i class="fa fa-file-pdf-o" style="padding-right: 3px;color : primary"></i>
                            <strong> {{noti.title}} </strong>
                          </a>
                        </td>
                        <td :class="(noti.remainDay < 0 ? 'text-secondary' : '')">
                          <i class="fa fa-calendar-check-o" style="padding-right: 3px"></i>
                          {{dateToFormat(noti.postingStart)}}
                          </a>
                        </td>
                        <td :class="(noti.remainDay < 0 ? 'text-secondary' : '')">
                          <i class="fa fa-calendar-times-o" style="padding-right: 3px"></i>
                          {{dateToFormat(noti.postingEnd)}}
                        </td>
                        <td>
                          <!-- <i class="fa fa-sitemap" style="padding-right: 3px" data-toggle="tooltip" title="" data-original-title={{locationDisplay}}></i> -->
                          <!-- <button class="btn btn-secondary" type="button" data-toggle="tooltip" data-placement="bottom" title="" data-original-title="Tooltip on bottom">Tooltip on bottom</button> -->
                          <!-- <a href="#" data-toggle="tooltip" title="" data-original-title="test"> -->

                          <!-- {{noti.location && noti.location[0] ? noti.location[0].name : ''}} -->

                          <!-- </a> -->
                          <div class="dropdown">
                          <button class="btn btn-ghost-info dropdown-toggle col-offset-6" id="alllocations" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">{{noti.location && noti.location[0] ? noti.location[0].name : ''}}</button>
                          <div class="dropdown-menu" x-placement="top-start" style="position: absolute; will-change: transform; top: 0px; left: 0px; transform: translate3d(0px, -356px, 0px);">
                          <!-- <a class="dropdown-item">게시위치</a> -->
                          <!-- <form class="px-4 py-3"> -->
                            <textarea class="form-control" id="textarea-input" name="textarea-input" rows="4" placeholder="게시위치" readonly>{{getLocationDisplay(noti)}}</textarea>
                          <!-- </form> -->
                          </div>
                          </div>

                        </td>
                        <td :style="noti.priority == '1' ? 'color: red;' : ''">{{getPriority(noti.priority)}}</td>
                        <td>
                          <a class="btn btn-ghost-normal mr-1" data-toggle="modal" data-target="#preview-modal"  @click="openPreviewModal(noti)">
                            <i :class="noti.contents.length == 1 ? 'fa fa-file-o' : 'fa fa-files-o'" style="padding-right: 3px"></i>
                            {{noti.contents.length}}
                          </a>
                          <i v-if="noti.contents.length>0 && noti.contents[0].ttsInfo.length > 0" class="icon-speech" style="padding-right: 3px;color : green"></i>
                        </td>
                        <td>{{noti.publisher}}</td>
                        <td>
                          <i class="fa fa-hashtag" style="padding-right: 3px"></i>
                          {{noti.tags}}
                        </td>
                        <td style="color: orange;">{{noti.originFile}}</td>
                        <td>
                            <a class="btn btn-success mr-1" data-toggle="modal" data-target="#preview-modal" @click="openPreviewModal(noti)">
                                <i class="fa fa-search-plus"></i>
                            </a>
                            <a class="btn btn-info mr-1" @click="updateNoti(noti)">
                                <i class="fa fa-edit"></i>
                            </a>
                            <a class="btn btn-danger mr-1" @click="delOneContent(noti)">
                                <i class="fa fa-trash-o"></i>
                            </a>
                        </td>
                    </tr>
                </tbody>
            </table>
            <pagination ref="pagination" :pagination="pagination" @selectPage="searchNoti"></pagination>
            <div>
                <button class="btn btn-danger btn-delete" style="margin-right: 1rem; width: 77px;" @click="removeNoti">삭제</button>
                <button v-if="addButton" class="btn btn-primary btn-pill btn-sm" style="width: 77px;" @click="addNoti">등록</button>
            </div>
        </div>
        </div>
    </div>
</template>

<script>
    import request from '../../../lib/request'
    import utils from '../../../lib/utils'

    import BOARD_TYPE from '../../code/boardType'
    import SORTING_TYPE from '../../code/sortingType'

    import Pagination from '../Pagination.vue'

    const ADD_NOTI_PAGE_ROUTE_NAME = 'notiAdd',
        UPDATE_NOTI_PAGE_ROUTE_PATH = '/noti/update'

  $(function () {
    $('[data-toggle="tooltip"]').tooltip()
  })

    export default {
        components: {
            Pagination
        },
        data () {
            return  {
                allChecked: false,
                pagination: {
                    pageNum: 1,
                    pageCount: 10,
                    totalCount: 1
                },
                sorting: {
                    title: SORTING_TYPE.DESC,
                    postingStart: SORTING_TYPE.DESC,
                    postingEnd: SORTING_TYPE.DESC,
                    priority: SORTING_TYPE.DESC,
                    publisher: SORTING_TYPE.DESC,
                    originFile: SORTING_TYPE.DESC,
                    reqSortingCol: '',
                    reqSortingType: SORTING_TYPE.DESC
                },
                notiList: []
            }
        },
        watch: {
            allChecked (val) {
                this.notiList.forEach(noti => {
                    noti.checked = val
                })
            }
        },
        props: {
            addButton: {
                type: Boolean,
                default: true,
                required: false
            },
            searchCondition: {
                type: Object,
                default: null,
                required: false
            }
        },
        computed: {
            locationDisplay () {
                return this.location.map(location => noti.location.name).join(',')
            }
        },
        methods: {
          getLocationDisplay (notice) {
              return notice.location.map(location => location.name).join(',')
          },
            delOneContent (content) {
                let deleteContentList

                if (confirm('해당 컨텐츠를 삭제하시겠습니까?')) {

                  deleteContentList = [content.contentId]

                  request.noti.removeNoti(deleteContentList)
                      .then(result => {
                          if (result.data.result === 200) {
                              // deleteContentList.forEach(contentId => this.notiList.some((noti, index, notiList) => {
                              //     if (noti.contentId === contentId) {
                              //         notiList.splice(index, 1)
                              //
                              //         return true
                              //     }
                              // }))
                              //
                              // this.pagination.totalCount -= deleteContentList.length

                              confirm('선택한 컨텐츠 한개를 삭제했습니다.')

                              this.searchNoti(1)  // reload

                          } else {
                              this.$log.error('컨텐츠 삭제를 실패했습니다. ', result)
                              alert(result.data.resultMsg)
                          }
                      })
                      .catch(err => {
                          this.$log.error('컨텐츠 삭제를 실패했습니다. err :', err)
                          alert('컨텐츠 삭제를 실패했습니다.\n(' + err.message + ')')
                      })

                } else {
                    this.$log.debug('컨텐츠 삭제 안함')
                }
            },
            removeNoti () {
                let deleteContentList

                deleteContentList = this.notiList.reduce(
                    (deleteContentList, noti) => {
                        if (noti.checked) {
                            deleteContentList.push(noti.contentId)
                        }

                        return deleteContentList
                    },
                    [])

                // 선택된 것이 없음
                if(deleteContentList.length < 1)
                {
                  alert('선택된 것이 없습니다.')
                  return
                }

                if (confirm('선택한 공고문을 삭제하시겠습니까?')) {

                    this.$log.debug('deleteNoti deleteContentList :', deleteContentList)

                    request.noti.removeNoti(deleteContentList)
                        .then(result => {
                            if (result.data.result === 200) {
                                // deleteContentList.forEach(contentId => this.notiList.some((noti, index, notiList) => {
                                //     if (noti.contentId === contentId) {
                                //         notiList.splice(index, 1)
                                //
                                //         return true
                                //     }
                                // }))
                                //
                                // this.pagination.totalCount -= deleteContentList.length

                                confirm('선택한 공고문을 삭제했습니다.')

                                this.searchNoti(1)  // reload

                            } else {
                                this.$log.error('공고문 삭제를 실패했습니다. ', result)
                                alert(result.data.resultMsg)
                            }
                        })
                        .catch(err => {
                            this.$log.error('공고문 삭제를 실패했습니다. err :', err)
                            alert('공고문 삭제를 실패했습니다.\n(' + err.message + ')')
                        })
                }
            },
            addNoti () {
                //if (confirm('공고문 올리기 페이지로 이동하시겠습니까?')) {
                    this.$log.debug('공고문 올리기 페이지 이동')
                    this.$router.push({
                        name: ADD_NOTI_PAGE_ROUTE_NAME
                    })
                // } else {
                //     this.$log.debug('공고문 올리기 페이지 이동 안함')
                // }
            },
            updateNoti (notice) {
                //if (confirm('공고문을 수정하시겠습니까?')) {
                    this.$log.debug('공고문 수정 페이지로 이동', notice)
                    this.$router.push({
                        path: UPDATE_NOTI_PAGE_ROUTE_PATH,
                        query: {
                            contentId: notice.contentId
                        }
                    })
                // } else {
                //     this.$log.debug('공고문 수정 안함')
                // }
            },
            searchNoti (pageNum) {
                this.$log.debug('searchNoti ', this.$refs.pagination)
                let condition = {
                    pageNum: pageNum,
                    pageCount: this.pagination.pageCount,
                    // sorting conditions
                    // sortingTitle: this.sorting.title,
                    // sortingPostingStart: this.sorting.postingStart,
                    // sortingPostingEnd: this.sorting.postingEnd,
                    // sortingPriority: this.sorting.priority,
                    // sortingPublisher: this.sorting.publisher,
                    // sortingOriginFile: this.sorting.originFile
                    sortingCol: this.sorting.reqSortingCol,
                    sortingType: this.sorting.reqSortingType
                }

                if (this.searchCondition) {
                    condition = Object.assign(
                        condition,
                        // this.searchCondition.startDate ? {startDate: utils.date.dateToFormat(this.searchCondition.startDate)} : {},
                        // this.searchCondition.endDate ? {endDate: utils.date.dateToFormat(this.searchCondition.endDate)} : {},
                        this.searchCondition.startDate ? {startDate: this.searchCondition.startDate} : {},
                        this.searchCondition.endDate ? {endDate: this.searchCondition.endDate} : {},
                        this.searchCondition.title ? {title: this.searchCondition.title} : {},
                        this.searchCondition.tags ? {tags: this.searchCondition.tags} : {})
                }

                request.noti.searchNoti(condition)
                    .then(result => {
                        this.$log.debug('sccess get noti list ', result)

                        if (result.data.result === 200) {
                            this.notiList = result.data.list.map(content => {
                                content.checked = false

                                return content
                            })

                            this.$refs.pagination.updatePagination({
                                pageNum: pageNum,
                                totalCount: result.data.totalCount
                            })
                        } else {
                            alert(result.data.resultMsg)
                        }
                    })
                    .catch(err => {
                        alert('공고문 목록을 조회 해오는 것을 실패했습니다.\n(' + err.message + ')')
                    })
            },
            getSortingClass (sortingType) {
                let className

                switch (sortingType) {
                    case SORTING_TYPE.DESC:
                        className = 'down'
                        break
                    case SORTING_TYPE.ASCE:
                    default:
                        className = 'up'

                }

                return className
            },
            changeSorting (attrName) {
                this.sorting[attrName] = this.sorting[attrName] === SORTING_TYPE.ASCE ? SORTING_TYPE.DESC : SORTING_TYPE.ASCE

                this.sorting.reqSortingCol = attrName
                this.sorting.reqSortingType = this.sorting[attrName]

                this.searchNoti(this.pagination.pageNum)
            },
            getPriority (priority) {
                let displayStr = '일반'

                switch (priority) {
                    case 1:
                        displayStr = '중요'
                        break
                }

                return displayStr
            },
            dateToFormat (date) {
                return utils.date.dateToFormat(date)
            },
            openPreviewModal (data) {
                this.$log.debug('openPreviewModal ', data)

                var tmpDate = new Date();

                this.$store.dispatch(
                    'preview/setData',
                    {
                        title: '공고문 미리보기 : ' + data.title,
                        contentPath: data.contentPath,
                        contents: data.contents,
                        boardType: BOARD_TYPE.ANNOUNCE
                    })
            }
        }
    }
</script>

<style>
    span.list-count {
        padding-left: 18.2;
        margin-left: 15px;
        font-size: inherit;
    }

    button.btn-update {
        margin-right: 10px;
    }

    i.sort-icon {
        cursor: pointer;
    }

    a.page-link {
        cursor: pointer;
    }

    a.btn {
        cursor: pointer;
    }

    tbody tr:hover {
        background-color: lightblue;
    }
</style>
