export default {
    namespaced: true,
    state: {
        title: '',
        contentPath: '',
        contents: [],
        boardType: '',
        contentType: ''
    },
    mutations: {
        setData (state, data) {
            if (data) {
                state.boardType = data.boardType
                state.contentType = data.contentType
                state.title = data.title
                state.contentPath = data.contentPath
                state.contents = data.contents
            }
        },
        clear (state) {
            console.log('preview clear')
            state.boardType = ''
            state.contentType = ''
            state.title = ''
            state.contentPath = ''
            state.contents = []
        }
    },
    actions: {
        setData (context, data) {
            context.commit('setData', data)
        },
        clear (context) {
            context.commit('clear')
        }
    }
}
