<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">클라이언트 관리</li>
        </ol>
        <div class="container-fluid">
            <div class="row">
              <div class="col">
                <div class="card" style="width: 100%;">
                  <div class="card-header">
                    <i class="icon-note"></i><strong>클라이언트 목록</strong>
                    <div class="card-header-actions">
                      <button class="btn btn-primary" @click="goSchedule"><i class="icon-settings" style="padding-right: 3px"></i>동작스케쥴설정</button>
                    </div>
                  </div>
                  <div class="card-body" id="clients-grop"style="min-height: 620px; max-height: 620px; overflow-x: auto; overflow-y: auto;">

                    <div class="row">
                      <div data-spy="scroll" data-target="#clients-grop" data-offset="0" class="col">
                        <div class="card-group">
                          <div class="col-sm-4 col-md-3" v-for="client in clients">
                            <div class="card" :class="getStatusCardType(client)">

                              <div class="card-header bg-dark">
                                {{client.clientName}}
                                <div class="card-header-actions">
                                  <a class="card-header-action btn-minimize" href="javascript:void(0);" @click="delClient(client)">
                                    <i class="icon-close"></i>
                                  </div>
                                </div>

                                <div class="card-body row">
                                  <div class="col-8">
                                  <p class="card-title">{{getBoardComposition(client)}}</p>
                                  <p class="card-text">
                                    <small class="text-muted">
                                      소속지점 : {{client.organName}}({{client.organId}})<br/>
                                      클라이언트 ID : {{client.clientId}}<br/>
                                      모니터 수 : {{client.boards.length}}개<br/>
                                      모니터 형태 : {{getLayoutTypeName(client)}}<br/>
                                      모니터별 해상도 : {{getResolution(client)}}<br/>
                                      전체해상도 : {{getTotalResolution(client)}}<br/>
                                      IP : {{client.clientIp}}<br/>
                                      동작여부 :
                                      </small>
                                    <small v-if="client.status > 0" :class="getStatusTextType(client)">
                                      {{getStatusDisplayName(client)}}<br/>
                                    </small>
                                    <small v-else class="text-muted">
                                      {{getStatusDisplayName(client)}}<br/>
                                    </small>
                                  </p>
                                  <div class="row">
                                      <div class="col-5">
                                          활성화
                                      </div>
                                      <div class="col">
                                          <label class="switch switch-pill switch-label switch-outline-primary">
                                            <input type="checkbox" class="switch-input" :checked="client.checked" @click="changeStatus(client)">
                                              <!-- <input v-if="client.status === 1" type="checkbox" class="switch-input checked" v-model="client.checked" @change="changeStatus(client)">
                                              <input v-else type="checkbox" class="switch-input" v-model="client.checked" @change="changeStatus(client)"> -->
                                              <span class="switch-slider" data-checked="On" data-unchecked="Off"></span>
                                          </label>
                                      </div>
                                  </div>
                                  </div>
                                  <div class="col">
                                  <div class="row justify-content-end">
                                    <button class="btn btn-outline-success btn-sm w-100 p-2" @click="settingBoard(client)">게시판설정</button>
                                  </div>
                                  <div class="row justify-content-end">
                                    <button class="btn btn-outline-secondary btn-sm w-100 p-2" @click="playAndStop(client)">재생  중지</button>
                                  </div>
                                  <div class="row justify-content-end">
                                    <button class="btn btn-outline-secondary btn-sm w-100 p-2" @click="viewLog(client)">로그 보기 </button>
                                  </div>
                                  <div class="row justify-content-end">
                                      <!-- <button class="btn btn-light btn-client-poweron" @click="clientOn(client)">원격ON</button> -->
                                      <button class="btn btn-outline-secondary btn-sm w-100 p-2" @click="clientReboot(client)">원격재시작</button>
                                    </div>
                                    <div class="row justify-content-end">
                                      <button class="btn btn-outline-secondary btn-sm w-100 p-2" @click="clientOff(client)">원격 종료 </button>
                                  </div>
                                </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
            </div>
            </div>
        </div>
      </div>
  </div>
    </main>
</template>

<script>
    import Dropdown from '../Dropdown.vue'
    import request from '../../../lib/request'
    import BOARD_TYPE from '../../code/boardType'
    import CLIENT_STATUS from '../../code/clientStatus'
    import CLIENT_ACTION from '../../code/clientAction'

    const SETTING_BOARD_PAGE_ROUTE_PATH = '/client/dboard'
    const SETTING_CLIENT_SCHEDULE_PAGE_ROUTE_PATH = '/client/schedule'


    export default {
        data () {
            return {
                clients: [],
            }
        },
        methods: {
          getStatusCardType(client)
          {
            if(client.status == CLIENT_STATUS.INACTIVE.value) // 비 활성화
              return "card-accent-secondary"
            else if(client.status == CLIENT_STATUS.ACTIVE.value) // 활성화
                return "card-accent-primary"
            else if(client.status == CLIENT_STATUS.PLAY.value) // 재생
                return "card-accent-success"
            else if(client.status == CLIENT_STATUS.STOP.value) // 중지
                return "card-accent-warning"
            else if(client.status == CLIENT_STATUS.ERROR.value) // 오류
                return "card-accent-danger"
          },
          getStatusTextType(client)
          {
            if(client.status == CLIENT_STATUS.INACTIVE.value) // 비 활성화
              return "text-secondary"
            else if(client.status == CLIENT_STATUS.ACTIVE.value) // 활성화
                return "text-primary"
            else if(client.status == CLIENT_STATUS.PLAY.value) // 재생
                return "text-success"
            else if(client.status == CLIENT_STATUS.STOP.value) // 중지
                return "text-warning"
            else if(client.status == CLIENT_STATUS.ERROR.value) // 오류
                return "text-danger"
          },
          goSchedule(){
              this.$log.debug('클라이언트 동작 설정 페이지로 이동')

              this.$router.push({
                path: SETTING_CLIENT_SCHEDULE_PAGE_ROUTE_PATH,
                query: {
                }
              })
            //}
          },
          getClients()
          {
            request.client.getClient()
                .then(result => {
                    this.$log.debug('클라이언트 조회 ', result)

                    if (result.data.result === 200) {
                        this.clients = result.data.list

                        this.clients.forEach(_client => {
                          if(_client.status == CLIENT_STATUS.INACTIVE.value)
                          {
                            _client.checked = false
                          }
                          else {
                            _client.checked = true
                          }
                        })
                    } else {
                        alert('클라이언트 조회를 실패했습니다 (' + result.data.resultMsg + ')')
                    }
                })
                .catch(err => {
                    alert('클라이언트 조회를 실패했습니다 (' + err.message + ')')
                })
            },
          delClient(client)
          {
            if (confirm('클라이언트를 삭제하시겠습니까?')) {
              // alert('클라이언트 삭제', client.clientId)
              this.$log.debug('delete Client ', client.clientId)

              request.client.deleteClient(client.clientId)
              .then(result => {
                this.$log.debug('delete Client result => ', result)

                if (result.data.result === 200) {
                  this.$log.debug('delete Client result : 성공 ')
                  alert('클라이언트가 삭제되었습니다')
                } else {
                  throw new Error(result.data.resultMsg)
                }
              })
              .catch(err => {
                this.$log.error('delete Client error ', err)
                alert('클라이언트 삭제에 실패했습니다\n(' + err.message + ')')
              })

              // 바로 조회하면 삭제안된 기존 리스트로 응답을 받음
              // 타이머로 500ms 후에 조회하면 정상적으로 삭제된 리스트 받음
                setTimeout(
                    () => {
                this.getClients()
              },
              500)
            }
            else {
              this.$log.debug('클라이언트 삭제 안함 : [' + client.clinetName + ',(' + client.clientId + ')]')
            }
          },
            changeStatus (client) {
                // //e.srcElement.checked  = !e.srcElement.checked

                if (client.status === CLIENT_STATUS.INACTIVE.value) {
                    // 동작하게
                    if (confirm('활성화 하시겠습니까?')) {
                         client.status = CLIENT_STATUS.ACTIVE.value
                         client.checked = true
                     }
                    else {
                      // client.status = CLIENT_STATUS.INACTIVE.value
                      client.checked = false
                    }
                } else if (client.status > CLIENT_STATUS.INACTIVE.value) {
                    // 중지하게
                    if (confirm('비활성화 하시겠습니까?')) {
                        client.status = CLIENT_STATUS.INACTIVE.value
                        client.checked = false
                    }
                    else {
                      //client.status = CLIENT_STATUS.ACTIVE.value
                      client.checked = true
                    }
                }
            },
            getStatusDisplayName (client) {
                let status = Object.keys(CLIENT_STATUS).find(status => CLIENT_STATUS[status].value === client.status)

                return status ? CLIENT_STATUS[status].displayName : ''
            },
            getLayoutTypeName (client) {
                return client.boards.length > 0 ? (client.boards[0].layoutType == 0 ? '가로형' : '세로형') : '설정된 모니터없음'
            },
            getResolution (client) {
                return client.boards.length > 0 ? (client.boards[0].width + 'x' + client.boards[0].height) : '설정된 모니터없음'
            },
            getTotalResolution (client) {
                let result, message

                if (client.boards.length > 0) {
                    result = client.boards.reduce(
                        (result, board) => {
                            result.width += board.width

                            return result
                        },
                        {
                            width: 0,
                            height: client.boards[0].height
                        })

                    message = result.width + 'x' + result.height
                } else {
                    message = '설정된 모니터없음'
                }

                return message
            },
            getBoardComposition (client) {
                let result = client.boards.reduce(
                    (result, board) => {
                        switch (board.boardType) {
                            case BOARD_TYPE.ANNOUNCE:
                                result.announce++;
                                break
                            case BOARD_TYPE.PROMOTION:
                                result.promotion++;
                                break
                        }

                        return result
                    },
                    {
                        announce: 0,
                        promotion: 0
                    })

                return '공고문(' + result.announce + '개), 홍보용 (' + result.promotion + '개)'
            },
            settingBoard (client) {
                //if (confirm('게시판 설정 메뉴로 이동하시겠습니까?')) {
                    this.$log.debug('게시판 설정 페이지로 이동', client)

                    this.$router.push({
                        path: SETTING_BOARD_PAGE_ROUTE_PATH,
                        query: {
                            clientId: client.clientId
                        }
                    })
                // } else {
                //     this.$log.debug('게시판 설정 안함')
                // }
            },
            playAndStop (client) {
                this.$log.debug('playAndStop', client.status)

                let activeYn = client.boards[0].activeYn,
                    action, confirmMsg, successMsg, errMsg

                switch (true) {
                    case /^Y$/i.test(activeYn):
                        action = CLIENT_ACTION.STOP
                        confirmMsg = '클라이언트를 중지하시겠습니까?'
                        successMsg = '클라이언트를 중지했습니다.'
                        errMsg = '클라이언트 중지를 실패했습니다.'
                        break
                    case /^N$/i.test(activeYn):
                        action = CLIENT_ACTION.PLAY
                        confirmMsg = '클라이언트를 재생하시겠습니까?'
                        successMsg = '클라이언트를 재생했습니다.'
                        errMsg = '클라이언트 재생을 실패했습니다.'
                        break
                }

                if (confirm(confirmMsg)) {
                    request.client.controlClient(client.clientId, action)
                        .then(result => {
                            this.$log.debug(action, 'result :', result)

                            if (result.status == 200) {
                                alert(successMsg)

                                // 바로 조회하면 변경안된 기존 리스트로 응답을 받음
                                // 타이머로 500ms 후에 조회하면 정상적으로 변경된 리스트 받음
                                  setTimeout(
                                      () => {
                                  this.getClients()
                                },
                                500)

                            } else {
                                throw new Error(result.data.resultMsg)
                            }
                        })
                        .catch(err => {
                            this.$log.error(errMsg, err)
                            alert(errMsg + ' (' + err.message + ')')
                        })
                }
            },
            viewLog (client) {
                if(client.status == CLIENT_STATUS.PLAY.value) // 동작중일때
                {

                }
            },
            clientOn (client) {
                if(client.status == CLIENT_STATUS.PLAY.value) // 동작중일때
                {
                    var ext = window.external

                    this.$log.debug('로컬 시스템 ON : systemOn')

                    ext.SystemOn();
                }
            },
            clientOff (client) {
                if (confirm('클라이언트를 종료하시겠습니까?')) {
                    request.client.controlClient(client.clientId, CLIENT_ACTION.POWEROFF)
                        .then(result => {
                            this.$log.debug('poweroff result :', result)

                            if (result.data.result === 200) {
                                alert('클라이언트를 종료했습니다.')
                            } else {
                                new Error(result.data.resultMsg)
                            }
                        })
                        .catch(err => {
                            this.$log.error('클라이언트 종료를 실패했습니다.', err)
                            alert('클라이언트 종료를 실패했습니다. (' + err.message + ')')
                        })
                }
//                if(client.status == CLIENT_STATUS.PLAY.value) // 동작중일때
//                {
//                    var ext = window.external
//
//                    this.$log.debug('로컬 시스템 ON : systemOff')
//
//                    ext.SystemOff();
//                }
            },
            clientReboot(client){
                if (confirm('클라이언트를 재시작하시겠습니까?')) {
                    request.client.controlClient(client.clientId, CLIENT_ACTION.REBOOT)
                        .then(result => {
                            this.$log.debug('reboot result :', result)

                            if (result.data.result === 200) {
                                alert('클라이언트를 재시작했습니다.')
                            } else {
                                new Error(result.data.resultMsg)
                            }
                        })
                        .catch(err => {
                            this.$log.error('클라이언트 재시작을 실패했습니다.', err)
                            alert('클라이언트 재시작을 실패했습니다. (' + err.message + ')')
                        })
                }
//                if(client.status == CLIENT_STATUS.PLAY.value) // 동작중일때
//                {
//                    var ext = window.external
//
//                    this.$log.debug('로컬 시스템 ON : SystemReboot')
//
//                    ext.SystemReboot();
//                }
            }
        },
        created () {
          this.getClients()
        },
        components: {
        }
    }
</script>

<style>
    button.btn-client {
        margin: 5px 10px 5px 10px;
    }

    button.btn-client-poweron {
        float: left;
        margin-left: 10px;
        margin-top: 5px;
        margin-bottom: 5px;
    }

    button.btn-client-poweroff {
        float: right;
        margin-right: 10px;
        margin-top: 5px;
        margin-bottom: 5px;
    }

    button.btn-client-powerreboot {
        float: left;
        margin-left: 10px;
        margin-top: 5px;
        margin-bottom: 5px;
    }
</style>
