<template>
    <div class="card">
        <div class="card-header">
            <!--<i class="fa fa-align-justify"></i>-->
            홍보물 컨텐츠 목록
            <!--<span class="badge badge-success list-count">총개수 1</span>-->
            <span class="badge list-count">총개수 {{pagination.totalCount}}</span>
            <div v-if="displayContentsTypeFilter" style="display: inline;padding-left: 5%;">
                <span v-for="contentType in contentTypes" style="padding-left: 3rem;">
                    <input type="checkbox" style="margin-right: 5px;" v-model="contentType.checked" @change="checkContentType(contentType, contentType.checked)"/>
                    <i :class="getContentTypeIcon(contentType)" style="padding-left: 3px"></i>
                    {{contentType.displayName}}
                </span>
            </div>
        </div>
        <div class="card-body">
            <table class="table table-striped">
                <thead>
                    <tr>
                        <th><input type="checkbox" v-model="allChecked"/></th>
                        <th>
                            홍보물명
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.title) + '-circle' + ' sort-icon'" @click="changeSorting('title')" :style="sorting.reqSortingCol === 'title' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th>
                            게시시작일
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.postingStart) + '-circle' + ' sort-icon'" @click="changeSorting('postingStart')" :style="sorting.reqSortingCol === 'postingStart' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th>
                            게시종료일
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.postingEnd) + '-circle' + ' sort-icon'" @click="changeSorting('postingEnd')" :style="sorting.reqSortingCol === 'postingEnd' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th>
                            게시위치
                            <!--<span>
                                <i class="icon-arrow-down sort-icon"></i>
                            </span>-->
                        </th>
                        <th>
                            타입
                            <!--<span>
                                <i class="icon-arrow-down sort-icon"></i>
                            </span>-->
                        </th>
                        <th>
                            게시자
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.publisher) + '-circle' + 'sort-icon'" @click="changeSorting('publisher')" :style="sorting.reqSortingCol === 'publisher' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th>
                            태그
                            <!--<span>
                                <i class="icon-arrow-down sort-icon"></i>
                            </span>-->
                        </th>
                        <th colspan="2">
                            원본파일명
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.originFile) + '-circle' + ' sort-icon'" @click="changeSorting('originFile')" :style="sorting.reqSortingCol === 'originFile' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="promotion in promotionList">
                        <td><input type="checkbox" v-model="promotion.checked"/></td>
                        <td>
                          <a class="btn btn-ghost-normal mr-1 text-primary" :class="(promotion.remainDay < 0 ? 'text-secondary' : '')" :title="'생성일: ' + promotion.createDate" data-toggle="modal" data-target="#preview-modal"  @click="openPreviewModal(promotion)">
                            <i :class="getDisplayContentTypeIcon(promotion)" style="padding-right: 3px;color : primary"></i>
                            <strong>{{promotion.title}}</strong>
                          </a>
                        </td>
                        <td :class="(promotion.remainDay < 0 ? 'text-secondary' : '')">
                          <i class="fa fa-calendar-check-o" style="padding-right: 3px"></i>
                          {{dateToFormat(promotion.postingStart)}}
                        </td>
                        <td :class="(promotion.remainDay < 0 ? 'text-secondary' : '')">
                          <i class="fa fa-calendar-times-o" style="padding-right: 3px"></i>
                          {{dateToFormat(promotion.postingEnd)}}
                        </td>
                        <td>
                          <!-- <i class="fa fa-sitemap" style="padding-right: 3px" data-toggle="tooltip" title="" data-original-title={{locationDisplay}}></i> -->
                          <!-- {{promotion.location && promotion.location[0] ? promotion.location[0].name : ''}} -->
                          <div class="dropdown">
                          <button class="btn btn-ghost-primary dropdown-toggle col-offset-6" id="alllocations" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">{{promotion.location && promotion.location[0] ? promotion.location[0].name : ''}}</button>
                          <div class="dropdown-menu" x-placement="top-start" style="position: absolute; will-change: transform; top: 0px; left: 0px; transform: translate3d(0px, -356px, 0px);">
                          <!-- <a class="dropdown-item">게시위치</a> -->
                          <!-- <form class="px-4 py-3"> -->
                            <textarea class="form-control" id="textarea-input" name="textarea-input" rows="4" placeholder="게시위치" readonly>{{getLocationDisplay(promotion)}}</textarea>
                          <!-- </form> -->
                          </div>
                          </div>

                        </td>
                        <td :style="isUrgentData(promotion) == true ? 'color: red;' : ''">
                          {{getDisplayContentTypeName(promotion)}}
                        </td>

                        <td>{{promotion.publisher}}</td>
                        <td>
                          <i class="fa fa-hashtag" style="padding-right: 3px"></i>
                          {{promotion.tags}}
                        </td>
                        <td style="color: orange;">{{promotion.originFile}}</td>
                        <td>
                            <a class="btn btn-success mr-1" data-toggle="modal" data-target="#preview-modal" @click="openPreviewModal(promotion)">
                                <i class="fa fa-search-plus"></i>
                            </a>
                            <a class="btn btn-info mr-1" @click="updatePromotion(promotion)">
                                <i class="fa fa-edit"></i>
                            </a>
                            <a class="btn btn-danger mr-1" @click="delOneContent(promotion)">
                                <i class="fa fa-trash-o"></i>
                            </a>
                        </td>
                    </tr>
                </tbody>
            </table>
            <pagination ref="pagination" :pagination="pagination" @selectPage="searchPromotion"></pagination>
            <div>
                <button class="btn btn-danger btn-delete" style="margin-right: 1rem; width: 77px;" @click="removePromotion">삭제</button>
                <button v-if="addButton" class="btn btn-primary btn-pill btn-sm" style="width: 77px;" @click="addPromotion">등록</button>
            </div>
        </div>
    </div>
</template>

<script>
    import request from '../../../lib/request'
    import utils from '../../../lib/utils'

    import Pagination from '../Pagination.vue'

    import BOARD_TYPE from '../../code/boardType'
    import CONTENT_TYPES from '../../code/contentType'
    import SORTING_TYPE from '../../code/sortingType'

    const ADD_PROMOTION_PAGE_ROUTE_NAME = 'promotionAdd',
        UPDATE_PROMOTION_PAGE_ROUTE_PATH = '/promotion/update/'

    export default {
        components: {
            Pagination
        },
        data () {
            return {
                allChecked: false,
                pagination: {
                    pageNum: 1,
                    pageCount: 10,
                    totalCount: 1
                },
                promotionList: [],
                searchCondition: {
                    startDate: '',
                    endDate: '',
                    tags: '',
                    title: '',
                    contentType: CONTENT_TYPES.map(contentType => {
                        return contentType
                    })
                },
                contentTypes: CONTENT_TYPES.map(contentType => {
                    contentType.checked = true

                    return contentType
                }),
                sorting: {
                    title: SORTING_TYPE.DESC,
                    postingStart: SORTING_TYPE.DESC,
                    postingEnd: SORTING_TYPE.DESC,
                    priority: SORTING_TYPE.DESC,
                    publisher: SORTING_TYPE.DESC,
                    originFile: SORTING_TYPE.DESC,
                    reqSortingCol: '',
                    reqSortingType: SORTING_TYPE.DESC
                }
            }
        },
        watch: {
            allChecked (val) {
                this.promotionList.forEach(promotion => {
                    promotion.checked = val
                })
            }
        },
        props: {
            addButton: {
                type: Boolean,
                default: true,
                required: false
            },
            displayContentsTypeFilter: {
                type: Boolean,
                default: false,
                required: false
            },
            // searchCondition: {
            //     type: Object,
            //     default: null,
            //     required: false
            // }
        },
        methods: {
          getLocationDisplay(promotion)
          {
            // alert('getLocationDisplay')

            return promotion.location.map(location => location.name).join(',')
          },
          delOneContent (content) {
              let deleteContentList

              if (confirm('해당 컨텐츠를 삭제하시겠습니까?')) {

                deleteContentList = [content.contentId]

                  request.promotion.removePromotion(deleteContentList)
                    .then(result => {
                        if (result.data.result === 200) {
                            // deleteContentList.forEach(contentId => this.promotionList.some((promotion, index, promotionList) => {
                            //     if (content.contentId === contentId) {
                            //         promotionList.splice(index, 1)
                            //
                            //         return true
                            //     }
                            // }))
                            //
                            // this.pagination.totalCount -= deleteContentList.length

                            confirm('선택한 컨텐츠를 삭제했습니다.')

                            this.searchPromotion(1)  // reload

                        } else {
                            this.$log.error('컨텐츠 삭제를 실패했습니다. ', result)
                            alert(result.data.resultMsg)
                        }
                    })
                    .catch(err => {
                        this.$log.error('컨텐츠 삭제를 실패했습니다. err :', err)
                        alert('컨텐츠 삭제를 실패했습니다.\n(' + err.message + ')')
                    })

              } else {
                  this.$log.debug('컨텐츠 삭제 안함')
              }
          },
            removePromotion () {
                let removePromotionList

                removePromotionList = this.promotionList.reduce(
                    (removePromotionList, promotion) => {
                        if (promotion.checked) {
                            removePromotionList.push(promotion.contentId)
                        }

                        return removePromotionList
                    },
                    [])

                // 선택된 것이 없음
                if(removePromotionList.length < 1)
                {
                  alert('선택된 것이 없습니다.')
                  return
                }

                if (confirm('선택한 항목을 삭제하시겠습니까?')) {
                    request.promotion.removePromotion(removePromotionList)
                        .then(result => {
                            if (result.data.result === 200) {
                                // removePromotionList.forEach(contentId => this.promotionList.some((promotion, index, promotionList) => {
                                //     if (promotion.contentId === contentId) {
                                //         promotionList.splice(index, 1)
                                //
                                //         return true
                                //     }
                                // }))
                                //
                                // this.pagination.totalCount -= removePromotionList.length

                                confirm('선택한 홍보물을 삭제했습니다.')

                                this.searchPromotion(1)  // reload
                            } else {
                                this.$log.error('홍보물 삭제를 실패했습니다. ', result)
                                alert(result.data.resultMsg)
                            }
                        })
                        .catch(err => {
                            this.$log.error('홍보물 삭제를 실패했습니다. err :', err)
                            alert('홍보물 삭제를 실패했습니다.\n(' + err.message + ')')
                        })
                }
            },
            addPromotion () {
                //if (confirm('홍보물 컨텐츠 올리기 페이지로 이동하시겠습니까?')) {
                    this.$log.debug('홍보물 컨텐츠 올리기 페이지 이동')
                    this.$router.push({
                        name: ADD_PROMOTION_PAGE_ROUTE_NAME
                    })
                // } else {
                //     this.$log.debug('홍보물 컨텐츠 올리기 페이지 이동 안함')
                // }
            },
            searchPromotion (pageNum) {
                this.$log.debug('searchCondition ', this.searchCondition)
                let condition = {
                    pageNum: pageNum,
                    pageCount: this.pagination.pageCount,

                    // sorting conditions
                    // sortingTitle: this.sorting.title,
                    // sortingPostingStart: this.sorting.postingStart,
                    // sortingPostingEnd: this.sorting.postingEnd,
                    // sortingPriority: this.sorting.priority,
                    // sortingPublisher: this.sorting.publisher,
                    // sortingOriginFile: this.sorting.originFile

                    sortingCol: this.sorting.reqSortingCol,
                    sortingType: this.sorting.reqSortingType
                }

                if (this.searchCondition) {
                    condition = Object.assign(
                        condition,
                        // this.searchCondition.startDate ? {startDate: utils.date.dateToFormat(this.searchCondition.startDate)} : {},
                        // this.searchCondition.endDate ? {endDate: utils.date.dateToFormat(this.searchCondition.endDate)} : {},
                        this.searchCondition.startDate ? {startDate: this.searchCondition.startDate} : {},
                        this.searchCondition.endDate ? {endDate: this.searchCondition.endDate} : {},
                        this.searchCondition.tags ? {tags: this.searchCondition.tags} : {},
                        this.searchCondition.title ? {title: this.searchCondition.title} : {},
                        this.searchCondition.contentType.length > 0 ? {searchContentType: this.searchCondition.contentType.map(contentType => contentType.value)} : {})
                }

                request.promotion.searchPromotion(condition)
                    .then(result => {
                        this.$log.debug('success get promotion list ', result)

                        if (result.data.result === 200) {
                            this.promotionList = result.data.list.map(content => {
                                content.checked = false

                                return content
                            })

                            this.$refs.pagination.updatePagination({
                                pageNum: pageNum,
                                totalCount: result.data.totalCount
                            })
                        } else {
                            alert(result.data.resultMsg)
                        }
                    })
                    .catch(err => {
                        alert('홍보물 목록을 조회 해오는 것을 실패했습니다.\n(' + err.message + ')')
                    })
            },
            updatePromotion (promotion) {
                //if (confirm('해당 홍보물 컨텐츠를 수정하시겠습니까?')) {
                    this.$log.debug('홍보물 컨텐츠 수정 페이지로 이동', promotion, this.$root.$refs)
                    this.$router.push({
                        path: UPDATE_PROMOTION_PAGE_ROUTE_PATH,
                        query: {
                            contentId: promotion.contentId
                        }
                    })
                // } else {
                //     this.$log.debug('홍보물 컨텐츠 수정 안함')
                // }
            },
            dateToFormat (date) {
                return utils.date.dateToFormat(date)
            },
            getContentTypeIcon(data)
            {
              switch (data.value) {
                case '001':
                  return "fa fa-file-image-o" // 이미지
                  break;
                case '002':
                  return "fa fa-file-movie-o" // 동영상
                  break;
                case '003':
                  return "fa fa-file-text-o" // 자막
                  break;
                case '004':
                  return "fa fa-file-word-o" // web url
                  break;
                default:
                  return "fa fa-file-image-o"
              }
            },
            getDisplayContentTypeIcon(data)
            {
              switch (data.contentType) {
                case '001':
                  return "fa fa-file-image-o" // 이미지
                  break;
                case '002':
                  return "fa fa-file-movie-o" // 동영상
                  break;
                case '003':
                  return "fa fa-file-text-o" // 자막
                  break;
                case '004':
                  return "fa fa-file-word-o" // web url
                  break;
                default:
                  return "fa fa-file-image-o"
              }
            },
            getSortingClass (sortingType) {
                let className

                switch (sortingType) {
                    case SORTING_TYPE.DESC:
                        className = 'down'
                        break
                    case SORTING_TYPE.ASCE:
                    default:
                        className = 'up'

                }

                return className
            },
            changeSorting (attrName) {
                this.sorting[attrName] = this.sorting[attrName] === SORTING_TYPE.ASCE ? SORTING_TYPE.DESC : SORTING_TYPE.ASCE

                this.sorting.reqSortingCol = attrName
                this.sorting.reqSortingType = this.sorting[attrName]

                this.searchPromotion(this.pagination.pageNum)
            },
            openPreviewModal (data) {
                this.$log.debug('openPreviewModal ', data)

                //alert('미리보기')

                this.$store.dispatch(
                    'preview/setData',
                    {
                        title: '홍보물 컨텐츠 미리보기 : ' + data.title,
                        boardType: BOARD_TYPE.PROMOTION,
                        contentType: data.contentType,
                        contentPath: data.contentType === CONTENT_TYPES[2].value || data.contentType === CONTENT_TYPES[3].value ? data.contentInfo : data.contentPath + '/' + data.contentName
                    })
            },
            getDisplayContentTypeName (promotion) {
                //this.$log.debug('CONTENT_TYPES ', CONTENT_TYPES)
                let contentType = CONTENT_TYPES.find(contentType => contentType.value == promotion.contentType)
                return contentType ? (promotion.urgentYn == 'Y' ? contentType.displayName + '(긴급)' : contentType.displayName) : ''
            },
            isUrgentData (promotion) {
                return promotion.urgentYn == 'Y' ? true : false
            },
            checkContentType (contentType, checked) {
                this.$log.debug('checkContentType ', contentType, checked)
                this.$log.debug('searchCondition ', this.searchCondition)

                if (this.searchCondition)
                {
                  if (checked) {
                      this.searchCondition.contentType.push(contentType)
                  } else {
                      this.searchCondition.contentType.some((_contentType, _index) => {
                          if (_contentType.value === contentType.value) {
                              this.searchCondition.contentType.splice(_index, 1)

                              return true
                          }
                      })
                  }
              }

              this.searchPromotion(1)
            }
        }
    }
</script>

<style>
    span.list-count {
        padding-left: 18.2;
        margin-left: 15px;
        font-size: inherit;
    }

    a.list-title {
        cursor: pointer;
        color : red;
    }

    i.sort-icon {
        cursor: pointer;
    }

    img.update-icon {
        margin-left: 5px;
        cursor: pointer;
    }

    a.page-link {
        cursor: pointer;
    }

    a.btn {
        cursor: pointer;
    }

    tbody tr:hover {
        background-color: darkgray;
    }
</style>
