<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">서버 모니터링</li>
        </ol>
        <div class="container-fluid">
            <div class="row" style="font-weight: bold; font-size: medium;">
                시스템 사양
            </div>
            <div class="row">
                <ul>
                    <li>OS : {{systemResources.os.name}}</li>
                    <li>CPU : {{systemResources.cpu.name}}</li>
                    <li>MEMORY : {{systemResources.memory.total}} GB</li>
                    <li>DISK : {{systemResources.disk.total}} GB</li>
                    <li>Ethernet : {{systemResources.nic.name}} {{systemResources.nic.speed}}</li>
                </ul>
            </div>
            <div class="row mb-3">
                <pie-chart
                    chart-width="400px"
                    chart-height="400px"
                    :labels="cpuChart.labels"
                    :chart-data="cpuChart.data"
                    ref="cpuPieChart"
                    ></pie-chart>

                <pie-chart
                    chart-width="400px"
                    chart-height="400px"
                    :labels="memoryChart.labels"
                    :chart-data="memoryChart.data"
                    ref="memoryPieChart"
                    ></pie-chart>

                    <pie-chart
                        chart-width="400px"
                        chart-height="400px"
                        :labels="diskChart.labels"
                        :chart-data="diskChart.data"
                        ref="diskPieChart"
                        ></pie-chart>
            </div>
        </div>
    </main>
</template>

<script>

    import request from '../../../lib/request'
    const AUTO_UPDATE_SYSTEM_RESOURCES_INTERVAL = 30000

    import PieChart from '../PieChart.vue'

    export default {
        data () {
            return {
              updateTimer: null,
              systemResources: {
                os: {
                  name: 'windows'
                },
                cpu: {
                  name: 'Xeon',
                  cores: '12',
                  usage: '5.5'
                },
                memory: {
                  total: '1234',
                  usage: '5',
                  used: '5678'
                },
                disk: {
                  total: '1234',
                  usage: '40',
                  used: '5678'
                },
                nic: {
                  name: 'intel gigabit',
                  speed: '5G'
                }
              },

                cpuChart: {
                    labels: [
                        {
                            label: 'CPU 사용률',
                            backgroundColor: 'rgba(54, 162, 235, 0.2)',
                            borderColor: 'rgba(54, 162, 235, 1)'
                        },
                        {
                            label: '여유'
                        }
                    ],
                    data: [12, 88]
                },
                diskChart: {
                    labels: [
                        {
                            label: 'DISK 사용률',
                            backgroundColor: 'rgba(54, 162, 235, 0.2)',
                            borderColor: 'rgba(54, 162, 235, 1)'
                        },
                        {
                            label: '여유'
                        }
                    ],
                    data: [12, 88]
                },
                memoryChart: {
                    labels: [
                        {
                            label: 'MEM 사용률',
                            backgroundColor: 'rgba(54, 162, 235, 0.2)',
                            borderColor: 'rgba(54, 162, 235, 1)'
                        },
                        {
                            label: '여유'
                        }
                    ],
                    data: [12, 88]
                }
            }
        },
        components: {
            PieChart
        },
        methods: {
          getSystemResources()
          {
            this.$log.debug('getSystemResources -> ')

            this.cpuChart.data.splice(0, this.cpuChart.data.length)
            this.diskChart.data.splice(0, this.diskChart.data.length)
            this.memoryChart.data.splice(0, this.memoryChart.data.length)

            request.systemresource.getSystemResources()
                .then(result => {
                    let err

                    if (result.status === 200) {
                        this.$log.debug('서버 시스템 정보 조회 성공 ', result.data)

                        this.systemResources = result.data

                        this.cpuChart.data.push(this.systemResources.cpu.usage)
                        this.cpuChart.data.push(100 - parseInt(this.systemResources.cpu.usage))

                        this.diskChart.data.push(this.systemResources.disk.usage)
                        this.diskChart.data.push(100 - parseInt(this.systemResources.disk.usage))

                        this.memoryChart.data.push(this.systemResources.memory.usage)
                        this.memoryChart.data.push(100 - parseInt(this.systemResources.memory.usage))

                        this.$refs.cpuPieChart.updateChart(this.cpuChart)
                        this.$refs.diskPieChart.updateChart(this.diskChart)
                        this.$refs.memoryPieChart.updateChart(this.memoryChart)

                    } else {
                        err = new Error(result.data.resultMsg)
                        err.resultData = result.data
                        throw err
                    }
                })
                .catch(err => {
                    this.$log.error('서버 시스템 정보 조회 실패 ', err)
                    alert('서버 시스템 정보 조회에 실패했습니다.\n' + err.message)
                })
          }
        },
        created()
        {
          this.getSystemResources()

          this.updateTimer = setInterval(
              () => {
                this.getSystemResources()
            },
            AUTO_UPDATE_SYSTEM_RESOURCES_INTERVAL)
        },
        destroyed()
        {
          clearInterval(this.updateTimer)
          this.updateTimer = null
        }
    }
</script>
