<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">홍보물 검색</li>
            <!-- Breadcrumb Menu-->
            <!--<li class="breadcrumb-menu d-md-down-none">
                <div class="btn-group" role="group" aria-label="Button group">
                    <a class="btn" href="#">
                        <i class="icon-settings"></i> 설정</a>
                </div>
            </li>-->
        </ol>

        <div class="container-fluid">
		<div class="card">
		    <div class="card-header">
			<i class="icon-note"></i><strong>검색 조건</strong>
			<div class="card-header-actions">
				<a class="card-header-action btn-minimize" href="#" data-toggle="collapse" data-target="#searchArea" aria-expanded="true">
				<i class="icon-arrow-up"></i>
				</a>
			</div>
		    </div>
		    <div class="collapse show" id="searchArea">
			    <div class="card-body">
            <!-- 게시날짜 -->
            <div class="row mb-3">
            <div class="col-1">
                게시날짜
            </div>
            <div class="col-4">
              <!-- <div class="form-group"> -->
									<div class="input-group">
										<span class="input-group-prepend">
											<span class="input-group-text">
												<i class="fa fa-calendar"></i>
											</span>
										</span>
										<input class="form-control" name="daterange" type="text"/>
									</div>
							<!-- </div> -->
						</div>
            </div>
            <!-- 제목 -->
            <div class="row mb-3">
            <div class="col-1">
                제목
            </div>
            <div class="col-4">
									<div class="input-group">
										<span class="input-group-prepend">
											<span class="input-group-text">
												<i class="fa fa-newspaper-o"></i>
											</span>
										</span>
										<input type="text" class="form-control" placeholder="제목을 입력하세요" v-model="searchCondition.title" @keyup.enter="searchPromotion"/>
									</div>
							</div>
						</div>
            <!-- 태그 -->
            <div class="row mb-3">
            <div class="col-1">
                태그
            </div>
            <div class="col-4">
                  <div class="input-group">
                    <span class="input-group-prepend">
                      <span class="input-group-text">
                        <i class="fa fa-hashtag"></i>
                      </span>
                    </span>
                    <input type="text" class="form-control" placeholder="#홍보" v-model="searchCondition.tags" @keyup.enter="searchPromotion"/>
                  </div>
              </div>
            </div>
            <!-- 컨텐츠 타입  -->
            <div class="row mb-3">
                <div class="col-1">
                    컨텐츠 타입
                </div>
                <div v-for="contentType in contentTypes" class="col-1">
                    <input type="checkbox" style="margin-right: 5px;" v-model="contentType.checked" @change="checkContentType(contentType, contentType.checked)"/>
                    <i :class="getContentTypeIcon(contentType)" style="padding-left: 3px"></i>
                    {{contentType.displayName}}
                </div>
            <!-- </div>
            <div class="row mb-3">
                <div class="col offset-5"> -->
                    <div class="col">
					    <button class="btn btn-primary" style="width: 100px;margin-left: 10px;" @click="searchPromotion">
                <i class="fa fa-search"></i>
                검색
              </button>
					</div>
				    </div>
			    </div>
		    </div>
	    </div>

            <promotion-list ref="promotionList" :add-button="false" :search-condition="searchCondition"></promotion-list>
        </div>
    </main>
</template>

<script>
    import PromotionList from './PromotionList.vue'
    import LinkedDatePicker from '../LinkedDatePicker.vue'

    import dateUtils from '../../../lib/date'

    import CONTENT_TYPES from '../../code/contentType'

    export default {
        data () {
            return {
                searchCondition: {
                    startDate: dateUtils.toFormat('YYYYMMDD000000'),
                    endDate: dateUtils.toFormat('YYYYMMDD235959'),
                    tags: '',
                    title: '',
                    contentType: CONTENT_TYPES.map(contentType => {
                        return contentType
                    })
                },
                contentTypes: CONTENT_TYPES.map(contentType => {
                    contentType.checked = true

                    return contentType
                })
            }
        },
        components: {
            PromotionList,
            LinkedDatePicker
        },
        props: {
        },
        mounted () {
            this.$log.debug('!@#!# ', $('input[name="daterange"]'))
            $('input[name="daterange"]').daterangepicker();
        },
        methods: {
          getContentTypeIcon(data)
          {
            switch (data.value) {
              case '001':
                return "fa fa-file-image-o" // 이미지
                break;
              case '002':
                return "fa fa-file-movie-o" // 동영상
                break;
              case '003':
                return "fa fa-file-text-o" // 자막
                break;
              case '004':
                return "fa fa-file-word-o" // web url
                break;
              default:
                return "fa fa-file-image-o"
            }
          },
            checkContentType (contentType, checked) {
                this.$log.debug('checkContentType ', contentType, checked)

                if (checked) {
                    this.searchCondition.contentType.push(contentType)
                } else {
                    this.searchCondition.contentType.some((_contentType, _index) => {
                        if (_contentType.value === contentType.value) {
                            this.searchCondition.contentType.splice(_index, 1)

                            return true
                        }
                    })
                }
            },
            searchPromotion () {
              // promotionList에도 searchCondition이 있어서, PromotionSearch의 searchCondition값을 전달해줌
                this.$refs.promotionList.searchCondition = this.searchCondition
                this.$refs.promotionList.searchPromotion(1)
            }
        },
          mounted () {
            var options = {};
            options.alwaysShowCalendars = true;
            options.locale = {
          format: 'YYYY.MM.DD',
              daysOfWeek: ['일', '월', '화', '수', '목', '금','토'],
                  monthNames: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
              };
              options.ranges = {
                  '오늘 날짜': [moment(), moment()]
                  /*,
                'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
                'Last 7 Days': [moment().subtract(6, 'days'), moment()],
                'Last 30 Days': [moment().subtract(29, 'days'), moment()],
                'This Month': [moment().startOf('month'), moment().endOf('month')],
                'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
                */
              };

              $('input[name="daterange"]').daterangepicker(
                  options,
                  (start, end, label) => {
                      this.searchCondition.startDate = start.format('YYYYMMDD000000')
                      // this.$log.debug(this.searchCondition.startDate)
                      this.searchCondition.endDate = end.format('YYYYMMDD235959')
                      // this.$log.debug(this.searchCondition.endDate)
                  })
          }
    }
</script>

