<script>
    import NotiList from './NotiList.vue'
    import LinkedDatePicker from '../LinkedDatePicker.vue'
    import utils from '../../../lib/utils'
    import dateUtils from '../../../lib/date'

    export default {
        data () {
            return {
                searchCondition: {
                    startDate: dateUtils.toFormat('YYYYMMDD000000'),
                    endDate: dateUtils.toFormat('YYYYMMDD235959'),
                    title: '',
                    tags: ''
                }
            }
        },
        components: {
          LinkedDatePicker,
          NotiList
        },
        props: {
        },
        mounted () {
            this.$log.debug('!@#!# ', $('input[name="daterange"]'))
            $('input[name="daterange"]').daterangepicker();
        },
        methods: {
            searchNoti () {
              // this.$log.debug(this.searchCondition.startDate)
              // this.$log.debug(this.searchCondition.endDate)
                this.$refs.notiList.searchNoti(1)
            }
        },
          mounted () {
            var options = {};
            options.alwaysShowCalendars = true;
            options.locale = {
          format: 'YYYY.MM.DD',
              daysOfWeek: ['일', '월', '화', '수', '목', '금','토'],
                  monthNames: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
              };
              options.ranges = {
                  '오늘 날짜': [moment(), moment()]
                  /*,
                'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
                'Last 7 Days': [moment().subtract(6, 'days'), moment()],
                'Last 30 Days': [moment().subtract(29, 'days'), moment()],
                'This Month': [moment().startOf('month'), moment().endOf('month')],
                'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
                */
              };

              $('input[name="daterange"]').daterangepicker(
                  options,
                  (start, end, label) => {
                      this.searchCondition.startDate = start.format('YYYYMMDD000000')
                      // this.$log.debug(this.searchCondition.startDate)
                      this.searchCondition.endDate = end.format('YYYYMMDD235959')
                      // this.$log.debug(this.searchCondition.endDate)
                  })
          }
    }
</script>
