<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">로그</li>
        </ol>
        <div class="container-fluid">
          <div class="card" style="width: 100%;">
            <div class="card-header">
              <i class="icon-note"></i><strong>검색 조건</strong>
            </div>
            <div class="card-body">
            <div class="form row mb-3">
  							<div class="col-sm-6 col-md-6">
  								<div class="form-group">
  									<label>게시날짜</label>
  									<div class="input-group">
  										<span class="input-group-prepend">
  											<span class="input-group-text">
  												<i class="fa fa-calendar"></i>
  											</span>
  										</span>
  										<input class="form-control" name="daterange" type="text"/>
  									</div>
  								</div>
                </div>
                <div class="col align-self-center">
                    <button class="btn btn-primary" style="width: 120px; margin-left: 10px;" @click="searchLog(1)">
                      <i class="fa fa-archive mx-1"></i>로그보기</button>
                </div>
            </div>

            <div class="card">
                <div class="card-header">
                    <!--<i class="fa fa-align-justify"></i>-->
                    로그
                    <span class="badge list-count">총개수 {{pagination.totalCount}}</span>
                </div>
                <div class="card-body">
                    <table class="table table-striped">
                      <thead>
                        <tr>
                          <th>타입</th>
                          <th>제목</th>
                          <th>날짜
                            <!-- <span>
                                <i class="icon-arrow-down sort-icon"></i>
                            </span> -->
                          </th>
                          <th>대상</th>
                          <th>내용</th>
                          <th>구분</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="log in logs">
                          <td>{{log.type}}</td>
                          <td>{{log.title}}</td>
                          <td>{{log.date}}</td>
                          <td>{{log.messageTarget}}</td>
                          <td>{{log.message}}</td>
                          <td :class="log.messageCode == '1' ? 'text-success' : log.messageCode == '2' ? 'text-danger' : '' ">
                            {{log.messageType}}
                          </td>
                        </tr>
                      </tbody>
                    </table>

                    <pagination ref="pagination" :pagination="pagination" @selectPage="searchLog"></pagination>

                    <!-- <div>
                        <button class="btn btn-danger btn-delete" style="margin-right: 1rem; width: 77px;">삭제</button>
                    </div> -->
                </div>
            </div>
          </div>
      </div>
    </div>
  </main>
</template>

<script>
    import request from '../../../lib/request'
    import utils from '../../../lib/utils'
    import dateUtils from '../../../lib/date'
    import Pagination from '../Pagination.vue'
    import LinkedDatePicker from '../LinkedDatePicker.vue'

    export default {
        data () {
            return {
                allChecked: false,
                pagination: {
                    pageNum: 1,
                    pageCount: 10,
                    totalCount: 1
                },
                searchCondition: {
                    postingStart: dateUtils.toFormat('YYYYMMDD'),
                    postingEnd: dateUtils.toFormat('YYYYMMDD'),
                    type: '',
                    title: '',
                    messageCode: '',
                    messageTarget: ''
                },
                postingStart: dateUtils.toFormat('YYYYMMDD'),
                postingEnd: dateUtils.toFormat('YYYYMMDD'),
                logType: "ERR", //"INFO"
                logInfo: [
                  {
                    type: '로그',
                    title: '권한요청',
                    message: '1234님이 권한요청을 하였습니다.',
                    messageType: '요청',
                    messageCode: '1',
                    messageTarget: 'CMS',
                    date: '2019-11-20'
                  },
                  {
                    type: '로그',
                    title: '시스템로그',
                    message: '시스템 디스크가 부족합니다.',
                    messageType: '오류',
                    messageCode: '2',
                    messageTarget: 'CMS',
                    date: '2019-11-20'
                  },
                  {
                    type: '로그',
                    title: '동작로그',
                    message: '1234님이 클라이언트1을 중지하였습니다.',
                    messageType: '오류',
                    messageCode: '3',
                    messageTarget: 'CMS',
                    date: '2019-11-20'
                  },
                  {
                    type: '로그',
                    title: '클라이언트로그',
                    message: '클라이언트1이 중지되었습니다.',
                    messageType: '일반',
                    messageCode: '3',
                    messageTarget: '클라이언트1',
                    date: '2019-11-20'
                  }
                ]
            }
        },
        components: {
            LinkedDatePicker,
            Pagination
        },
        created()
        {
          //this.searchLog(1)
        },
        mounted () {
            //this.$log.debug('!@#@! ', moment('20191110', 'YYYYMMDD'))
            var options = {};
            options.alwaysShowCalendars = true;
            options.locale = {
                format: 'YYYY.MM.DD',
                daysOfWeek: ['일', '월', '화', '수', '목', '금','토'],
                monthNames: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
            };
            options.ranges = {
              '지난 1달': [moment().subtract(29, 'days'), moment()],
              '오늘 날짜': [moment(), moment()]/*,
              'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
              'Last 7 Days': [moment().subtract(6, 'days'), moment()],
              'Last 30 Days': [moment().subtract(29, 'days'), moment()],
              'This Month': [moment().startOf('month'), moment().endOf('month')],
              'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
                                                  */
            };

            $('input[name="daterange"]').daterangepicker(
                options,
                (start, end, label) => {
                    this.postingStart = start.format('YYYYMMDD')
                    this.postingEnd = end.format('YYYYMMDD')
                });
        },
        watch:
        {

        },
        methods:
        {
          searchLog(pageNum)
          {
            let condition = {
                pageNum: pageNum,
                pageCount: this.pagination.pageCount,
                startDate: this.postingStart,
                endDate: this.postingEnd,
                logType: this.logType
                }

                this.$log.debug('searchCondition ', condition)

                request.log.searchLog(condition)
                    .then(result => {
                        let err

                        if (result.data.result === 200) {
                            this.$log.debug('로그 정보 조회 성공 ', result.data)
                            this.logInfo = result.data.list

                            this.$refs.pagination.updatePagination({
                                pageNum: pageNum,
                                totalCount: result.data.totalCount
                            })

                        } else {
                            err = new Error(result.data.resultMsg)
                            err.resultData = result.data
                            throw err
                        }
                    })
                    .catch(err => {
                        this.$log.error('로그 정보 조회 실패 ', err)
                        alert('로그 정보 조회에 실패했습니다.\n' + err.message)
                    })

          }
        }
    }
</script>
