<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">통계보기</li>
        </ol>
        <div class="container-fluid">

          <div class="card" style="width: 100%;">
            <div class="card-header">
              <i class="icon-note"></i><strong>통계</strong>
            </div>
            <div class="card-body">
              <div class="form row mb-3">
                  <div class="col-sm-6 col-md-6">
                    <div class="form-group">
                      <label>게시날짜</label>
                      <div class="input-group">
                        <span class="input-group-prepend">
                          <span class="input-group-text">
                            <i class="fa fa-calendar"></i>
                          </span>
                        </span>
                        <input class="form-control" name="daterange" type="text"/>
                      </div>
                    </div>
                  </div>
                </div>

                <!-- 컨텐츠 타입  -->
                <div class="row mb-3">
                  <div class="col-1">
                    컨텐츠 타입
                  </div>
                  <div v-for="contentType in contentTypes" class="col-1">
                    <input type="checkbox" style="margin-right: 5px;" v-model="contentType.checked" @change="checkContentType(contentType, contentType.checked)"/>
                    <i :class="getContentTypeIcon(contentType)" style="padding-left: 3px"></i>
                    {{contentType.displayName}}
                  </div>

                  <div class="col">
                    <button class="btn btn-primary" style="width: 120px;margin-left: 10px;" @click="searchStatistic()">
                      <i class="icon-pie-chart mx-1"></i>
                      통계보기
                    </button>
                  </div>
                </div>

              </div>
            </div>

            <div class="row mb-3 justify-content-start">
              <!-- data -->
              <div class="col-sm-2 col-md-2">
                <ul class="list-group" v-for="chartData, index in chart.labels">
                    <li  class="list-group-item d-flex list-group-item-action justify-content-between align-items-center" href="javascript:void(0);">
                        {{chartData.label}}
                        <span class="badge badge-info badge-pill mx-1">{{chart.data[index]}}</span>
                    </li>
                </ul>
              </div>

              <!-- chart -->
              <!-- <div class="col"> -->
                <pie-chart
                    chart-width="400px"
                    chart-height="400px"
                    :labels="chart.labels"
                    :chart-data="chart.data"
                    ref="pieChart"
                    ></pie-chart>
              <!-- </div> -->
            </div>
        </div>
    </main>
</template>

<script>
    import request from '../../../lib/request'
    import LinkedDatePicker from '../LinkedDatePicker.vue'
    import PieChart from '../PieChart.vue'
    import dateUtils from '../../../lib/date'
    import CONTENT_TYPES from '../../code/contentTypeAll'

    const CHART_LABELS = [
        {
            label: '공고문',
            contentType: '005',
            backgroundColor: 'rgba(255, 99, 132, 0.2)',
            borderColor: 'rgba(255, 99, 132, 1)'
        },
        {
            label: '이미지',
            contentType: '001',
            backgroundColor: 'rgba(54, 162, 235, 0.2)',
            borderColor: 'rgba(54, 162, 235, 1)'
        },
        {
            label: '동영상',
            contentType: '002',
            backgroundColor: 'rgba(255, 206, 86, 0.2)',
            borderColor: 'rgba(255, 206, 86, 1)'
        },
        {
            label: '자막',
            contentType: '003',
            backgroundColor: 'rgba(75, 192, 192, 0.2)',
            borderColor: 'rgba(75, 192, 192, 1)'
        },
        {
            label: '웹URL',
            contentType: '004',
            backgroundColor: 'rgba(153, 102, 255, 0.2)',
            borderColor: 'rgba(153, 102, 255, 1)'
        }
    ]

    export default {
        components: {
            LinkedDatePicker,
            PieChart
        },
        data () {
            return {
                postingStart: dateUtils.toFormat('YYYYMMDD'),
                postingEnd: dateUtils.toFormat('YYYYMMDD'),
                chart: {
                    labels: [],
                    data: [],
                    totalCount: 0
                },
                contentTypes: CONTENT_TYPES.map(contentType => {
                    contentType.checked = true

                    return contentType
                })
            }
        },
        methods: {
            getContentTypeIcon(data)
            {
              switch (data.value) {
                case '001':
                return "fa fa-file-image-o" // 이미지
                break;
                case '002':
                return "fa fa-file-movie-o" // 동영상
                break;
                case '003':
                return "fa fa-file-text-o" // 자막
                break;
                case '004':
                return "fa fa-file-word-o" // web url
                break;
                case '005':
                return "fa fa-file-pdf-o" // 공고문
                break;
                default:
                return "fa fa-file-image-o"
              }
            },
            checkContentType (contentType, checked) {
              this.$log.debug('checkContentType ', contentType, checked)



            },
            searchStatistic() {
                let condition = {
                    startDate: this.postingStart,
                    endDate: this.postingEnd
                }

                this.$log.debug('searchStatistic condition : ', condition)

                request.statistic.searchStatistic(condition)
                    .then(result => {
                        let err

                        if (result.data.result === 200) {
                            this.$log.debug('통계 정보 조회 성공 ', result.data)
                            this.chart.totalCount = result.data.totalCount
                            this.chart.data.splice(0, this.chart.data.length)
                            this.chart.labels.splice(0, this.chart.labels.length)
                            //this.chart.labels.splice(0,this.chart.labels.length)

                            result.data.list.forEach((content, index) => {
                                let _IsContentChecked = this.contentTypes.find(_contentType => _contentType.value == content.contentType),
                                    chartLabel
                                // 체크되어 있는 컨텐츠 타입만 차트로 표시
                                if(_IsContentChecked && _IsContentChecked.checked == true)
                                {
                                    chartLabel = CHART_LABELS.find(chartLabel => chartLabel.contentType == content.contentType)
                                    this.chart.labels.push(chartLabel)
                                    this.chart.data.push(content.count)
//                                    this.chart.labels[index].label = content.contentTypeName
//                                    this.chart.labels[index].contentType = content.contentType
//                                    this.chart.data[index] = content.count

                                    // this.chart.labels.push({
                                    //   label: content.contentTypeName,
                                    //   contentType: content.contentType,
                                    //   backgroundColor: 'rgba(255, 99, 132, 0.2)',
                                    //   borderColor: 'rgba(255, 99, 132, 1)'
                                    // })
                                    //this.chart.data[index] = content.count

                                    //this.$log.debug('통계 정보 -> ', index, this.chart.labels.length, this.chart.labels[index].label, this.chart.labels[index].contentType, this.chart.data[index])
                                }
                            })

                            this.$refs.pieChart.updateChart(this.chart)
                        } else {
                            err = new Error(result.data.resultMsg)
                            err.resultData = result.data
                            throw err
                        }
                    })
                    .catch(err => {
                        this.$log.error('통계 정보 조회 실패 ', err)
                        alert('통계 정보 조회에 실패했습니다.\n' + err.message)
                    })
            }
        },
          mounted () {

            // this.$log.debug('Here daterange picker mounted.....')

            var options = {};
            options.alwaysShowCalendars = true;
            options.locale = {
              format: 'YYYY.MM.DD',
              daysOfWeek: ['일', '월', '화', '수', '목', '금','토'],
              monthNames: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
            };
            options.ranges = {
              '지난 1달': [moment().subtract(29, 'days'), moment()],
              '오늘 날짜': [moment(), moment()]
              /*,
              'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
              'Last 7 Days': [moment().subtract(6, 'days'), moment()],
              'Last 30 Days': [moment().subtract(29, 'days'), moment()],
              'This Month': [moment().startOf('month'), moment().endOf('month')],
              'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
              */
            };

            options.startDatePlaceholder = '게시시작일'
            options.endDatePlaceholder = '게시종료일'

            $('input[name="daterange"]').daterangepicker(
              options,
              (start, end, label) => {
                //this.$log.debug(label)
                this.postingStart = start.format('YYYYMMDD000000')
                //this.$log.debug(this.postingStart)
                this.postingEnd = end.format('YYYYMMDD235959')
                //this.$log.debug(this.postingEnd)
              })
          },
          created() {
            this.searchStatistic()
          }
    }
</script>
