<template>
    <div :class="chartRootClass">
        <canvas :width="chartWidth" :height="chartHeight" ref="chartCanvas"></canvas>
    </div>
</template>

<script>
    import Chart from 'chart.js'

    export default {
        props: {
            chartType: {
                type: String,
                require: false,
                default: 'pie'
            },
            chartRootClass: {
                type: String,
                require: false,
                default: ''
            },
            chartWidth: {
                type: String,
                require: false,
                default: '400px'
            },
            chartHeight: {
                type: String,
                require: false,
                default: '400px'
            },
            borderWidth: {
                type: Number,
                require: false,
                default: 1
            },
            labels: {
                type: Array,
                require: true
            },
            borderWidth: {
                type: Number,
                require: false,
                default: 1
            },
            chartData: {
                type: Array,
                require: true
            }
        },
        data () {
            return {
                chart: null
            }
        },
        methods: {
            updateChart (data) {
                this.chart.data.labels.splice(0, this.chart.data.labels.length)
                this.chart.data.datasets.splice(0, this.chart.data.datasets.length)

                this.chart.data.labels = data.labels.map(label => label.label)
                this.chart.data.datasets.push({
                    data: data.data,
                    backgroundColor: data.labels.map(label => label.backgroundColor),
                    borderColor: data.labels.map(label => label.borderColor),
                    borderWidth: this.borderWidth
                })

                this.chart.update()
            }
        },
        mounted () {
            this.chart = new Chart(this.$refs.chartCanvas, {
                type: this.chartType,
                data: {
                    labels: this.labels.map(label => label.label),
                    //['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],
                    datasets: [{
                        //label: '# of Votes',
                        data: this.chartData,
                        backgroundColor: this.labels.map(label => label.backgroundColor),
                        borderColor: this.labels.map(label => label.borderColor),
                        borderWidth: this.borderWidth
//                        backgroundColor: [
//                            'rgba(255, 99, 132, 0.2)',
//                            'rgba(54, 162, 235, 0.2)',
//                            'rgba(255, 206, 86, 0.2)',
//                            'rgba(75, 192, 192, 0.2)',
//                            'rgba(153, 102, 255, 0.2)',
//                            'rgba(255, 159, 64, 0.2)'
//                        ],
//                        borderColor: [
//                            'rgba(255, 99, 132, 1)',
//                            'rgba(54, 162, 235, 1)',
//                            'rgba(255, 206, 86, 1)',
//                            'rgba(75, 192, 192, 1)',
//                            'rgba(153, 102, 255, 1)',
//                            'rgba(255, 159, 64, 1)'
//                        ],
                    }]
                }/*,
                options: {
                    scales: {
                        yAxes: [{
                            ticks: {
                                beginAtZero: true
                            }
                        }]
                    }
                }*/
            });
        }
    }
</script>
