<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">
              <i class="icon-note"></i>
              클라이언트 동작 스케쥴</li>
        </ol>
        <div class="container-fluid">
          <div class="row">
      			<div class="col-sm-8">
                <div class="card">
                    <div class="card-header">
                        <i class="icon-note"></i><strong>클라이언트 동작 시간 설정</strong>
                    </div>
                    <div class="card-body" style="min-height: 620px; max-height: 620px; overflow-x: auto; overflow-y: auto;">
                        <table class="table table-hover">
                            <thead>
                                <tr>
                                    <th><input type="checkbox" v-model="allChecked"/></th>
                                    <th>
                                        요일
                                    </th>
                                    <th>
                                        클라이언트목록
                                    </th>
                                    <th>
                                        시작시간
                                    </th>
                                    <th>
                                        종료시간
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                              <tr v-for="schedule in schedulesAndHolidays.weekSchedule">
                                  <td><input type="checkbox" v-model="schedule.checked"/></td>
                                  <td v-if="schedule.weekDay == '7'" class="text-primary">{{schedule.name}}</td>
                                  <td v-else-if="schedule.weekDay == '1'" class="text-danger">{{schedule.name}}</td>
                                  <td v-else class="text-light">
                                    {{schedule.name}}
                                  </td>
                                  <td class="text-primary text-strong">
                                    <span v-bind:title="allClients">
                                      모든클라이언트
                                    </span>
                                  </td>
                                  <td>
                                    <dropdown
                                        scroll
                                        ref="startTimeDropdown"
                                        :items="times"
                                        :defaultSelectedIndex="schedule.startTime-1"
                                        @selectedItem="function (time) { schedule.startTime = time.value }"
                                        >
                                        </dropdown>
                                  </td>
                                  <td>
                                    <dropdown
                                        scroll
                                        ref="endTimeDropdown"
                                        :items="times"
                                        :defaultSelectedIndex="schedule.endTime-1"
                                        @selectedItem="function (time) { schedule.endTime = time.value }"
                                        >
                                        </dropdown>
                                  </td>
                              </tr>
                            </tbody>
                        </table>

                        <div class="row">
                          <div class="col-sm-3 justify-content-end">
                            <button class="btn btn-primary" @click="setSchedules">
                              <i class="fa fa-send mx-1">적용</button>
                          </div>
                      </div>

                    </div>
                </div>
                </div>
                <div class="col">
                  <div class="card">
                    <div class="card-header">
                      <i class="icon-note"></i><strong> 휴일 동작 설정</strong>
                    </div>
                    <div class="card-body" style="min-height: 620px; max-height: 620px; overflow-x: auto; overflow-y: auto;">

                    <div class="row">
                        <span style="padding-left: 3em;">
                          <input class="form-check-input" id="check1" type="checkbox" v-model="schedulesAndHolidays.holidayOffChecked">
                           <!-- @click="checkHoliday"> -->
                          <label class="form-check-label" for="check1">공휴일에 동작하지 않음</label>
                        </span>
                    </div>
                    <div class="row py-3">
                        <span style="padding-left: 3em;">
                          <input class="form-check-input" id="check1" type="checkbox" v-model="schedulesAndHolidays.customHolidayOffChecked">
                           <!-- @click="checkExtraHoliday"> -->
                          <label class="form-check-label" for="check1">지정휴일에 동작하지 않음</label>
                        </span>
                    </div>
                    <div class="row mx-2">
                      <div class="col-md-8">
                          <div class="input-group">
                            <input type="text" class="form-control" v-model="extraDateTitle"/>
                          </div>
                          <div class="input-group">
                            <input v-model="extraDate" class="form-control" id="date-input" ref="extraDatePicker" type="date" name="date-input" placeholder="date">
                          </div>
                        </div>
                      <div class="col-md-4 align-items-center">
                          <button class="btn btn-primary w-100 p-2" @click="setExtraHoliday">
                            <i class="icon-cup mx-1"></i> 휴일지정
                          </button>
                      </div>
                    </div>


                    <div class="row py-3">
                    <div class="col-sm">
                    <div class="card" style="max-height: 450px; overflow-y: auto;">
                        <div class="card-header" id="extraHolidays">
                            <h6 class="mb-0">
                                <button class="btn btn-link collapsed" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                    지정 휴일 목록
                                </button>
                                <span class="badge list-count">총개수 {{getCustomHolidaysCount()}}</span>
                            </h6>
                        </div>
                        <div id="collapseOne" class="collapse show" aria-labelledby="headingOne" data-parent="#extraHolidays">
                            <div class="card-body">
                              <div data-spy="scroll" data-target="#extraHolidays" data-offset="0" class="scrollextraHolidays">
                                  <ul class="list-group" v-for="extraDate in schedulesAndHolidays.customHolidays">
                                      <li  class="list-group-item d-flex list-group-item-action justify-content-between align-items-center" href="javascript:void(0);">
                                        {{extraDate.exceptDay}}
                                          <span class="badge badge-info badge-pill mx-1">{{extraDate.dayTitle}}</span>
                                          <button type="button" class="close" aria-label="Close" @click="delExtraHoliday(extraDate)">
                                            <span aria-hidden="true">&times;</span>
                                          </button>
                                      </li>
                                  </ul>
                              </div>
                            </div>
                        </div>
                    </div>
                  </div>
                  </div>

                  <div class="row">
                    <div class="col-sm-3 justify-content-end">
                      <button class="btn btn-primary" @click="setHolidays">
                        <i class="fa fa-send mx-1">적용</button>
                    </div>
                </div>


                </div>
                </div>
                </div>
                </div>
                </div>
            </div>
            </div>
        </div>
    </main>
</template>

<script>
    import request from '../../../lib/request'

    import Dropdown from '../Dropdown.vue'

    export default {
        data () {
            return {
                allChecked: false,
                allClients: '등록된 모든 클라이언트',
                // isNotWorkingAtHoliday: true,
                // isNotWorkingAtExtraHoliday: true,
                extraDate: '',
                extraDateTitle: '임시휴일',
                // clientSchedule: [
                //   {
                //     startTime: '9',
                //     endTime: '18',
                //     weekOf: '1',
                //     checked: true,
                //     name: '월'
                //   },
                //   {
                //     startTime: '9',
                //     endTime: '18',
                //     weekOf: '2',
                //     checked: true,
                //     name: '화'
                //   },
                //   {
                //     startTime: '9',
                //     endTime: '18',
                //     weekOf: '3',
                //     checked: true,
                //     name: '수'
                //   },
                //   {
                //     startTime: '9',
                //     endTime: '18',
                //     weekOf: '4',
                //     checked: true,
                //     name: '목'
                //   },
                //   {
                //     startTime: '9',
                //     endTime: '18',
                //     weekOf: '5',
                //     checked: true,
                //     name: '금'
                //   },
                //   {
                //     startTime: '9',
                //     endTime: '18',
                //     weekOf: '6',
                //     checked: false,
                //     name: '토'
                //   },
                //   {
                //     startTime: '9',
                //     endTime: '18',
                //     weekOf: '7',
                //     checked: false,
                //     name: '일'
                //   }
                // ],
                times: Array.from(
                    {
                        length: 24
                    },
                    (k, v) => {
                        return {
                            displayName: (v < 12 ? '오전 ' : '오후 ') + (v+1) + '시',
                            value: (v+1)
                        }
                    }),
                // extraHolidays:[
                //   // {
                //   //   date: '2019-11-30',
                //   //   title: '임시휴일'
                //   // }
                // ],
                // clients: []
                schedulesAndHolidays: {

                }
            }
        },
        watch: {
            allChecked (val) {
                this.clientSchedule.forEach(schedule => {
                    schedule.checked = val
                })
            }
        },
        methods: {
          getCustomHolidaysCount()
          {
            if(this.schedulesAndHolidays.customHolidays == null)
              return 0
            return this.schedulesAndHolidays.customHolidays.length
          },
          setExtraHoliday()
          {
            //alert('setExtraHoliday')
            this.$log.debug('지정 휴일 추가 -> ', this.extraDate, this.extraDateTitle)
            if(this.extraDate)
            {
              // 이미 있는지 확인
              let isExist = false
              this.schedulesAndHolidays.customHolidays.some((_extra, _index) => {

                this.$log.debug('이미 지정된 휴일여부 체크 -> ', this.extraDate, _extra.exceptDay)
                
                  if (_extra.exceptDay == this.extraDate) {
                      alert('이미 지정된 휴일입니다.')
                      this.$log.debug('이미 지정된 휴일입니다. -> ', this.extraDate, this.extraDateTitle)

                      isExist = true
                      return true
                  }
              })

              if(isExist == false)
                this.schedulesAndHolidays.customHolidays.push({exceptDay: this.extraDate, dayTitle: this.extraDateTitle})
            }
          },
          delExtraHoliday(extraDate)
          {
            this.$log.debug('지정 휴일 삭제 -> ', extraDate.exceptDay, extraDate.dayTitle)
            if(extraDate)
            {
              this.schedulesAndHolidays.customHolidays.some((_extra, _index) => {
                  if (_extra.exceptDay == extraDate.exceptDay &&
                    _extra.dayTitle == extraDate.dayTitle) {
                      this.schedulesAndHolidays.customHolidays.splice(_index, 1)
                      return true
                  }
              })
            }
          },
          getSchedulesAndHolidays()
          {
            request.client.getSchedulesAndHolidays()
                .then(result => {
                    this.$log.debug('클라이언트 동작스케쥴 & 휴일스케쥴 조회 ', result)

                    if (result.data.result === 200) {
                        this.schedulesAndHolidays = result.data.data

                        // 동작 스케쥴
                        this.schedulesAndHolidays.weekSchedule.forEach(_schedule => {
                          _schedule.checked =  _schedule.useYn == 'Y' ? true : false

                          switch(_schedule.weekDay)
                          {
                            case 2:
                            _schedule.name = '월'
                            break
                            case 3:
                            _schedule.name = '화'
                            break
                            case 4:
                            _schedule.name = '수'
                            break
                            case 5:
                            _schedule.name = '목'
                            break
                            case 6:
                            _schedule.name = '금'
                            break
                            case 7:
                            _schedule.name = '토'
                            break
                            case 1:
                            _schedule.name = '일'
                            break
                          }
                        })

                        // 휴일스케쥴
                        this.schedulesAndHolidays.holidayOffChecked = this.schedulesAndHolidays.holidayOff == 'Y' ? true : false
                        this.schedulesAndHolidays.customHolidayOffChecked = this.schedulesAndHolidays.customHolidayOff == 'Y' ? true : false


                    } else {
                        alert('클라이언트 동작스케쥴 & 휴일스케쥴를 실패했습니다 (' + result.data.resultMsg + ')')
                    }
                })
                .catch(err => {
                    alert('클라이언트 동작스케쥴 & 휴일스케쥴를 실패했습니다 (' + err.message + ')')
                })
          },
          setSchedules()
          {
            // 동작 스케쥴
            this.schedulesAndHolidays.weekSchedule.forEach(_schedule => {
              _schedule.useYn = _schedule.checked == true ? 'Y' : 'N'
            })

            request.client.updateSchedules(this.schedulesAndHolidays.weekSchedule)
                .then(result => {
                    this.$log.debug('클라이언트 동작스케쥴 업데이트 ', result)

                    if (result.data.result === 200) {
                      alert('클라이언트 동작스케쥴 업데이트를 완료하였습니다.')


                    } else {
                        alert('클라이언트 동작스케쥴 업데이트를 실패했습니다 (' + result.data.resultMsg + ')')
                    }
                })
                .catch(err => {
                    alert('클라이언트 동작스케쥴 업데이트를 실패했습니다 (' + err.message + ')')
                })
          },
          setHolidays()
          {
            // 휴일
            this.schedulesAndHolidays.holidayOff = this.schedulesAndHolidays.holidayOffChecked == true ? 'Y' : 'N'
            this.schedulesAndHolidays.customHolidayOff = this.schedulesAndHolidays.customHolidayOffChecked == true ? 'Y' : 'N'

            request.client.updateHolidays(this.schedulesAndHolidays)
                .then(result => {
                    this.$log.debug('휴일스케쥴 업데이트 ', result)

                    if (result.data.result === 200) {
                      alert('휴일스케쥴 업데이트를 완료하였습니다.')

                    } else {
                        alert('휴일스케쥴 업데이트를 실패했습니다 (' + result.data.resultMsg + ')')
                    }
                })
                .catch(err => {
                    alert('휴일스케쥴 업데이트를 실패했습니다 (' + err.message + ')')
                })
          }

          // checkHoliday()
          // {
          //   if(isNotWorkingAtHoliday)
          //     isNotWorkingAtHoliday = false
          //   else
          //     isNotWorkingAtHoliday = true
          // },
          // checkExtraHoliday()
          // {
          //   if(isNotWorkingAtExtraHoliday)
          //     isNotWorkingAtExtraHoliday = false
          //   else
          //     isNotWorkingAtExtraHoliday = true
          // }
        },
        created () {
          this.getSchedulesAndHolidays()
        },
        mounted()
        {
        },
        components: {
           Dropdown
        }
    }
</script>

<style>
    button.btn-client {
        margin: 5px 10px 5px 10px;
    }

    button.btn-client-poweron {
        float: left;
        margin-left: 10px;
        margin-top: 5px;
        margin-bottom: 5px;
    }

    button.btn-client-poweroff {
        float: right;
        margin-right: 10px;
        margin-top: 5px;
        margin-bottom: 5px;
    }

    button.btn-client-powerreboot {
        float: left;
        margin-left: 10px;
        margin-top: 5px;
        margin-bottom: 5px;
    }
</style>
