<template>
    <div class="container">
        <vuejs-datepicker
            input-class="form-control"
            :wrapper-class="startDateWrapperClass"
            :placeholder="startDatePlaceholder"
            :format="dateFormat"
            @selected="selectStartDate"
            :disabled-dates="startDateDisabledDates"
            style="color: black;"
            v-model="startDate"
            ></vuejs-datepicker>
        ~
        <vuejs-datepicker
            input-class="form-control"
            :wrapper-class="endDateWrapperClass"
            :placeholder="endDatePlaceholder"
            :format="dateFormat"
            @selected="selectEndDate"
            :disabled-dates="endDateDisabledDates"
            style="color: black;"
            v-model="endDate"
            ></vuejs-datepicker>
    </div>
</template>

<script>
    import vuejsDatepicker from 'vuejs-datepicker'

    export default {
        data () {
            return {
                startDate: null,
                endDate: null,
                startDateDisabledDates: {
                    from: null
                },
                endDateDisabledDates: {
                    to: null
                }
            }
        },
        props: {
            startDatePlaceholder: {
                type: String,
                required: false,
                default: '시작일'
            },
            endDatePlaceholder: {
                type: String,
                required: false,
                default: '종료일'
            },
            dateFormat: {
                type: String,
                required: false,
                default: 'yyyy.MM.dd'
            },
            startDateWrapperClass: {
                type: String,
                required: false,
                default: 'col-3'
            },
            endDateWrapperClass: {
                type: String,
                required: false,
                default: 'col-3'
            }
        },
        components: {
            vuejsDatepicker
        },
        methods: {
            selectStartDate (selectedDate) {
                this.endDateDisabledDates.to = selectedDate
                // toFormat
                this.$emit('selectStartDate', selectedDate)
            },
            selectEndDate (selectedDate) {
                this.startDateDisabledDates.from = selectedDate
                this.$emit('selectEndDate', selectedDate)
            }
        }
    }
</script>
