<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">{{viewTitle}}</li>
            <!-- Breadcrumb Menu-->
            <!--<li class="breadcrumb-menu d-md-down-none">
                <div class="btn-group" role="group" aria-label="Button group">
                    <a class="btn" href="#">
                        <i class="icon-settings"></i> 설정</a>
                </div>
            </li>-->
        </ol>
	<div class="container-fluid">
		<div class="row">
			<div class="col-sm-6">
				<div class="card">
					<div class="card-header">
						<i class="icon-note"></i><strong>공고문</strong>

            <button class="btn btn-primary" style="float: right;" @click="goPrev">
              <i class="icon-action-undo"></i>
              이전 페이지로 돌아가기
            </button>
					</div>
					<div class="card-body">
						<div class="row">
							<div class="col-sm-12">
								<div class="form-group">
									<label for="name">제목</label>
									<div class="input-group">
										<span class="input-group-prepend">
											<span class="input-group-text">
												<i class="fa fa-newspaper-o"></i>
											</span>
										</span>
										<input type="text" class="form-control" placeholder="제목을 입력하세요" v-model="title"/>
									</div>
								</div>
							</div>
						</div>
						<div class="row">
							<div class="col-sm-12">
								<div class="form-group">
									<label>공고문</label>
									<div class="input-group">
										<span class="input-group-prepend">
											<span class="input-group-text">
												<i class="fa fa-calendar"></i>
											</span>
										</span>
										<input type="file" id="customFile" accept=".pdf" ref="file" class="custom-file-input" @change="handleFileUpload()"/>
										<label for="customFile" class="custom-file-label">{{fileName}}</label>
									</div>
								</div>
							</div>
						</div>
						<div class="row">
							<div class="col-sm-12">
								<div class="form-group">
									<label>게시날짜</label>
									<div class="input-group">
										<span class="input-group-prepend">
											<span class="input-group-text">
												<i class="fa fa-calendar"></i>
											</span>
										</span>
										<input class="form-control" name="daterange" type="text" autocomplete="off"/>
									</div>
								</div>
							</div>
						</div>
						<div class="row">
							<div class="col-sm-12">
								<div class="form-group">
									<label>게시 위치</label>
									<span style="padding-left: 3em;">
										<input class="form-check-input" id="check1" type="checkbox" v-model="isAllSelectLocation">
										<label class="form-check-label" for="check1">할당된 모든 지점</label>
									</span>
									<div class="input-group">
										<span class="input-group-prepend">
											<span class="input-group-text">
												<i class="fa fa-sitemap"></i>
											</span>
										</span>
                      <textarea class="form-control" id="textarea-input" name="textarea-input" rows="9" placeholder="게시위치" readonly>{{locationDisplay}}</textarea>
									</div>
								</div>
							</div>
						</div>
						<div class="row">
							<div class="form-group col-sm-7">
								<label for="city">게시완료 파일</label>
								<div class="col-md-9 col-form-label">
									<div class="form-check form-check-inline mr-1">
										<input class="form-check-input" type="radio" name="isSave" value="Y" v-model="delExpired"/>
										<label class="form-check-label" for="check1">삭제</label>
									</div>
									<div class="form-check form-check-inline mr-1">
										<input class="form-check-input" type="radio" name="isSave" value="N" v-model="delExpired">
										<label class="form-check-label" for="check2">유지</label>
									</div>
								</div>
							</div>
							<div class="form-group col-sm-4">
								<label for="postal-code">중요도</label>
								<select class="form-control" id="select1" name="select1" v-model="priority">
									<option v-for="p in priorities" :value="p.value">{{p.displayName}}</option>
								</select>
							</div>
						</div>
						<div class="row">
							<div class="col-sm-12">
								<div class="form-group">
									<label>태그</label>
									<div class="input-group">
										<span class="input-group-prepend">
											<span class="input-group-text">
												<i class="fa fa-hashtag"></i>
											</span>
										</span>
										<input type="text" class="form-control" placeholder="#공고문" v-model="tags"/>
									</div>
								</div>
							</div>
						</div>
                        <div class="row">
                            <div class="col-sm-9">
                                <div v-if="uploadPercentage > 0" class="progress">
                                      <div class="progress-bar" role="progressbar" :style="'width: ' + uploadPercentage + '%'" :aria-valuenow="uploadPercentage" aria-valuemin="0" aria-valuemax="100"></div>
                                </div>
                            </div>
                            <div class="col-sm-3">
                                <button v-if="isUpdate" :disabled="isUploading==true" class="btn btn-primary" style="float: right;" @click="updateNoti">
                                  <i class="fa fa-eraser"></i>
                                  공고문 수정
                                </button>
                                <button v-else :disabled="isUploading==true" class="btn btn-primary" style="float: right;" @click="addNoti">
                                  <i class="fa fa-cloud-upload"></i>
                                  공고문 올리기
                                </button>
                            </div>
                        </div>
					</div>
				</div>
			</div>
			<div class="col-sm-6">
				<div class="card">
					<div class="card-header">
						<i class="icon-note"></i>
                        <strong> 공고문 미리보기</strong>
					</div>
					<div class="card-body">
						<div style="height:713px">
							<div v-if="contents.length > 0" id="carouselContentIndicators" class="carousel slide" data-ride="carousel" data-interval="false">
							    <ol class="carousel-indicators">
                                    <span class="badge rounded-pill bg-warning">{{contents.length}}</span>
								     <li v-for="(content, index) in contents" data-target="#carouselContentIndicators" :data-slide-to="index"></li>
							    </ol>
							    <div class="carousel-inner">
								<div v-for="content in contents" class="carousel-item" :class="{active: content.isActive}">
                      <button v-if="content.ttsInfo.length > 0" class="btn btn-success" style="margin-left: 300px" @click="textToSpeech(content)">
                          <i class="icon-speech" style="padding-right: 10px"> 읽어주기
                      </button>

								    <img class="img-fluid" style="display:block;" :src="content.contentImgPath">
								</div>
							    </div>
							    <a v-if="contents.length > 1" class="carousel-control-prev" href="#carouselContentIndicators" role="button" data-slide="prev" @click="actionPrevNext">
								<span class="carousel-control-prev-icon" aria-hidden="true"></span>
								<span class="sr-only">Previous</span>
							    </a>
							    <a v-if="contents.length > 1" class="carousel-control-next" href="#carouselContentIndicators" role="button" data-slide="next" @click="actionPrevNext">
								<span class="carousel-control-next-icon" aria-hidden="true"></span>
								<span class="sr-only">Next</span>
							    </a>
							</div>

						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</main>
</template>

<script>
    import request from '../../../lib/request'
    import utils from '../../../lib/utils'
    import dateUtils from '../../../lib/date'

    import LinkedDatePicker from '../LinkedDatePicker.vue'
    import TreeList from '../TreeList.vue'

    const PRIORITY = [
        {
            displayName: '일반',
            value: 0
        },
        {
            displayName: '중요',
            value: 1
        }
    ]

    export default {
        data () {
            return {
                isAllSelectLocation: true,
                viewTitle: '공고문 등록',
                priorities: PRIORITY,
                file: null,
                modifyfile: null,
                uploadPercentage: 0,
                contentPath: '',
                contents: [],
                locations: {
                    checked: false,
                    name: '할당된 모든 지점',
                    children: []
                },
                isUploading: false,
                isUpdate: false,
                // 공고문 데이터
                title: '',
//                postingStart: dateUtils.toFormat('YYYYMMDD000000'),
//                postingEnd: dateUtils.toFormat('YYYYMMDD235959'),
                postingStart: null,
                postingEnd: null,
                location: [],
                tags: '#공고문',
                delExpired: 'Y',
                priority: PRIORITY[0].value,
                prevRoute: null,
                // 임시 데이터
                dates: ''
            }
        },
        beforeRouteEnter(to, from, next) {
          next(vm => {
            vm.prevRoute = from
          })
        },
        watch: {
            isAllSelectLocation (val) {
              this.$log.debug('isAllSelectLocation :  ', val)
              if (val) {
                  this.locations.children.forEach(location => location.checked = true)
                  this.location.splice(0, this.location.length)
                  this.location.splice(0, 0, ...this.locations.children)
              } else {
                  this.locations.children.forEach(location => location.checked = false)
                  this.location.splice(0, this.location.length)
              }
            },
            'locations.checked': function (newVal, oldVal) {
                this.$log.debug('locations.checked ', newVal)

                if (newVal) {
                    this.locations.children.forEach(location => location.checked = true)
                    this.location.splice(0, this.location.length)
                    this.location.splice(0, 0, ...this.locations.children)
                } else {
                    this.locations.children.forEach(location => location.checked = false)
                    this.location.splice(0, this.location.length)
                }
            }
        },
        props: {
            contentId: {
                type: String,
                default: null,
                required: false
            }
        },
        created () {
            this.$log.debug('notice contentId ', this.$route.query.contentId, this.$route)
            let promises = [
              // 등록시 계정에 할당된 지점만 보이도록 하기 위해서 수정
                // request.organ.getLocation()
                request.organ.getAllowedLocation()
                    .then(result => {
                        this.$log.debug('get allowed location ', result)
                        if (result.data.result === 200) {
                            result.data.list.forEach(location => {
                                location.checked = false
                            })

                            this.locations.children.splice(0, 0, ...result.data.list)
                            //this.location.splice(0, 0, ...result.data.list)
                        } else {
                            throw new Error(result.data.resultMsg)
                        }

                        return result.data
                    })
                    .catch(err => {
                        throw new Error('공고문 게시 위치를 조회하는 것을 실패했습니다.\n(' + err.message + ')')
                    })
            ]

            if (this.$route.query.contentId) {
                // 수정 공고문 상세 조회
                this.isUpdate = true
                this.viewTitle = '공고문 수정'
                this.isAllSelectLocation = false

                promises.push(request.noti.searchNotiDetail(this.$route.query.contentId)
                    .then(result => {
                        if (result.data.result === 200) {
                            this.$log.info('공고문 조회 성공 ', result)

                            this.title = result.data.data.title
                            //this.file = result.data.data.originFile
                            this.modifyfile = result.data.data.originFile

                            this.postingStart = result.data.data.postingStart + '000000'
                            this.postingEnd = result.data.data.postingEnd + '000000'
                            $('input[name="daterange"]').data('daterangepicker').setStartDate(utils.date.toDate(result.data.data.postingStart))
                            $('input[name="daterange"]').data('daterangepicker').setEndDate(utils.date.toDate(result.data.data.postingEnd))
                            $('input[name="daterange"]').val(
                                utils.date.toFormat(utils.date.toDate(result.data.data.postingStart), 'YYYY.MM.DD')
                                + ' - ' +
                                utils.date.toFormat(utils.date.toDate(result.data.data.postingEnd), 'YYYY.MM.DD'))
                            this.location = result.data.data.location ? result.data.data.location : []
                            //this.locations.children.splice(0, 0, ...result.data.data.location)
                            this.tags = result.data.data.tags
                            this.delExpired = result.data.data.delExpired
                            //this.priority = PRIORITY.find(priority => priority.value == result.data.data.priority)
                            this.priority = result.data.data.priority

                            this.contents = result.data.data.contents
                            this.contentPath = result.data.data.contentPath

                            this.contents.forEach((content, index) => {
                                if (index === 0) {
                                    content.isActive = true
                                } else {
                                    content.isActive = false
                                }

                                // 이미지 캐시를 막기 위해 경로뒤에 변화되는 값인 getTime()을 붙여줌
                                {
                                  var tmpDate = new Date();
                                  content.contentImgPath = this.contentPath + '/' + content.fileName + '?' + tmpDate.getTime()
                                }
                            })

                            // this.$log.debug('공고문 컨텐츠 경로 ', this.contentPath)
                            // this.$log.debug('공고문 컨텐츠 파일 ', this.contents.length, this.contents[0].fileName)

                        } else {
                            throw new Error(result.data.resultMsg)
                        }

                        return result.data
                    })
                    .catch(err => {
                        this.$log.error('공고문 조회를 실패했습니다. err :', err)
                        throw new Error('공고문 조회를 실패했습니다.\n(' + err.message + ')')
                    }))
            }

            Promise.all(promises)
                .then(result => {

                    if(this.isUpdate == false)
                    {
                       // 신규등록이면 할당된 지점 모두 추가함
                        this.$log.debug('신규등록이라서 할당된 모든 지점 추가함')
                        this.locations.children.forEach(location => location.checked = true)
                        this.location.splice(0, this.location.length)
                        this.location.splice(0, 0, ...this.locations.children)
                    }

                    if (result.length === 2) {
                        this.$log.debug('수정이라서 지정된 게시 위치 얻어옴')

                        if (Array.isArray(result[1].data.location)) {
                            this.locations.children.forEach(location => {
                                result[1].data.location.some(organId => {
                                    if (organId === location.organId) {
                                        location.checked = true
                                        this.$log.debug('NotiAdd -> created () -> location : ', organId, location.organId, location.checked)
                                        return true
                                    }
                                })
                            })
                        }

                        // 모든 지점이 할당된 상태이면
                        if(this.locations.children.length == result[1].data.location.length)
                        {
                          this.isAllSelectLocation = true
                          this.$log.debug('수정이지만 모든 지점 할당된 상태임')
                        }
                      }
                })
                .catch(err => alert(err.message))
        },
        components: {
            LinkedDatePicker,
            TreeList
        },
        methods: {
            checkLocation (location, checked) {
                this.$log.debug('checkLocation ', location, checked)

                if (checked) {
                    this.location.push(location)
                } else {
                    this.location.some((_location, _index) => {
                        if (_location.organId === location.organId) {
                            this.location.splice(_index, 1)
                            return true
                        }
                    })
                }
            },
            addNoti () {
                let formData

                // Check validation
                if (this.title === '') {
                  alert('제목을 입력하세요')
                  return;
                }

                  if(this.file === null || this.file.name === '') {
                    alert('공고문을 선택하세요')
                    return;
                  }

                  if (this.postingStart === null || this.postingEnd === null) {
                    alert('게시날짜를 선택하세요')
                    return;
                  }

                // Upload
                //if (confirm('공고문을 등록하시겠습니까?')) {
                        formData = this.notiFormData

                        request.noti.notiAdd(
                            formData,
                            progressEvent => {
                                this.isUploading = true
                                this.uploadPercentage = parseInt( Math.round( ( progressEvent.loaded * 100 ) / progressEvent.total ) );
                            })
                            .then(result => {
                                this.$log.debug('공고문 등록 요청 결과 ', result)

                                this.isUploading = false

                                if (result.data.result === 200) {
                                    this.$log.info('공고문 등록 완료')
                                    confirm('공고문 등록을 완료했습니다.')

                                    this.contents = result.data.contents
                                    this.contentPath = result.data.contentPath
                                    this.uploadPercentage = 0

                                    this.contents.forEach((content, index) => {
                                        if (index === 0) {
                                            content.isActive = true
                                        } else {
                                            content.isActive = false
                                        }

                                        // 이미지 캐시를 막기 위해 경로뒤에 변화되는 값인 getTime()을 붙여줌
                                        {
                                          var tmpDate = new Date();
                                          content.contentImgPath = this.contentPath + '/' + content.fileName + '?' + tmpDate.getTime()
                                        }
                                    })

                                    //this.$router.go(-1); // 이전 페이지로

                                } else {
                                    alert(result.data.resultMsg)
                                }
                            })
                            .catch(err => {
                                this.uploadPercentage = 0
                                this.isUploading = false
                                this.$log.error('공고문 등록 실패 %s', err.stack)
                                alert('공고문 등록을 실패했습니다.\n(' + err.message + ')')
                            })
                    //}
            },
            updateNoti () {
                let formData

                // Check validation
                if (this.title === '') {
                      alert('제목을 입력하세요')
                      return;
                  }

                 if (this.postingStart === null || this.postingEnd === null) {
                    alert('게시날짜를 선택하세요')
                    return;
                  }


                // Upload
                //if (confirm('공고문을 수정하시겠습니까?')) {
                        formData = this.notiFormData

                        request.noti.updateNoti(
                            this.$route.query.contentId,
                            formData,
                            progressEvent => {
                              this.isUploading = true
                                this.uploadPercentage = parseInt( Math.round( ( progressEvent.loaded * 100 ) / progressEvent.total ) );
                            })
                            .then(result => {
                                this.$log.debug('공고문 수정 요청 결과 ', result)

                                this.isUploading = false

                                if (result.data.result === 200) {
                                    this.$log.info('공고문 수정 완료')
                                    confirm('공고문 수정을 완료했습니다.')

                                    //this.contents = result.data.contents
                                    //this.contentPath = result.data.contentPath
                                    this.contents = result.data.contents
                                    this.contentPath = result.data.contentPath
                                    this.uploadPercentage = 0



                                        this.contents.forEach((content, index) => {
                                        if (index === 0) {
                                            content.isActive = true
                                        } else {
                                            content.isActive = false
                                        }

                                        // 이미지 캐시를 막기 위해 경로뒤에 변화되는 값인 getTime()을 붙여줌
                                        {
                                          var tmpDate = new Date();
                                          content.contentImgPath = this.contentPath + '/' + content.fileName + '?' + tmpDate.getTime()
                                        }
                                    })


                                    // 이전 경로로 이동
                                    //this.$router.push(-1)

                                } else {
                                    alert(result.data.resultMsg)
                                }
                            })
                            .catch(err => {
                              this.uploadPercentage = 0
                              this.isUploading = false
                                this.$log.error('공고문 수정 실패 %s', err.stack)
                                alert('공고문 수정을 실패했습니다.\n(' + err.message + ')')
                            })
                    //}
            },
            handleFileUpload () {
                this.$log.debug('handleFileUpload file : %s', this.$refs.file.files[0])
                this.file = this.$refs.file.files[0];
            },
            selectPriority (priority) {
                this.priority = priority
            },
            goPrev()
            {
              this.stopTTS ()
              this.$router.go(-1); // 이전 페이지로
            },
            textToSpeech(content){
               this.$log.debug('[AnnounceDetail]textToSpeech : ', content.ttsInfo)
               responsiveVoice.speak(content.ttsInfo,'Korean Female');
            },
            stopTTS () {
                if (responsiveVoice.isPlaying()) {
                    responsiveVoice.cancel()
                }
            },
            actionPrevNext () {
                this.$log.debug('[NotiAdd]actionPrevNext')
                if (responsiveVoice.isPlaying())
                {
                    this.stopTTS()
                }
            },
        },
        computed: {
            fileName () {
                this.$log.debug('file.name : ', this.file ? this.file.name : '')
                this.$log.debug('modifyFile : ', this.modifyfile)
                return this.file ? this.file.name : (this.modifyfile ? this.modifyfile : '공고문을 선택하세요')
            },
            locationDisplay () {
                return this.location.map(location => location.name).join(',')
            },
            notiFormData () {
                let formData = new FormData()

                if (this.file) {
                    formData.append('file', this.file)
                }

                formData.append('title', this.title)
                formData.append('postingStart', this.postingStart)
                formData.append('postingEnd', this.postingEnd)
                formData.append('priority', this.priority)
                formData.append('tags', this.tags)
                formData.append('location', this.location.map(location => location.organId).join(','))
                formData.append('delExpired', this.delExpired)
                formData.append('contentInfo', '')

                return formData
            }
        },
        mounted () {
        	let options = {
                alwaysShowCalendars: true,
                locale: {
                    format: 'YYYY.MM.DD',
                    daysOfWeek: ['일', '월', '화', '수', '목', '금','토'],
                    monthNames: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
                },
                autoUpdateInput: false,
                ranges: {
                    '오늘 날짜': [moment(), moment()]
//                    ,
//                    'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
//                    'Last 7 Days': [moment().subtract(6, 'days'), moment()],
//                    'Last 30 Days': [moment().subtract(29, 'days'), moment()],
//                    'This Month': [moment().startOf('month'), moment().endOf('month')],
//                    'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
                }
            }

            $('input[name="daterange"]').daterangepicker(
                options,
                (start, end, label) => {
//                    this.postingStart = start.format('YYYYMMDD000000')
//                    this.postingEnd = end.format('YYYYMMDD235959')
                })
            $('input[name="daterange"]').on(
                'apply.daterangepicker',
                (ev, picker) => {
                    this.$log.debug('appay.daterangepicker', picker)
                    $('input[name="daterange"]').val(picker.startDate.format('YYYY.MM.DD') + ' - ' + picker.endDate.format('YYYY.MM.DD'))
                    this.postingStart = picker.startDate.format('YYYYMMDD000000')
                    this.postingEnd = picker.endDate.format('YYYYMMDD235959')
                })
            $('input[name="daterange"]').on(
                'cancel.daterangepicker',
                (ev, picker) => {
                    this.$log.debug('cancel.daterangepicker', picker)
                    $('input[name="daterange"]').val('')
                    this.postingStart = null
                    this.postingEnd = null
                })
            //$('input[name="daterange"]').data('daterangepicker').setStartDate('')
        },
        destroyed () {
          this.stopTTS()
        },
    }
</script>

