<template>
    <ul class="pagination">
        <li class="page-item"><a class="page-link" @click="selectPage(pagination.pageNum - 1)">Prev</a></li>
        <li v-for="n in totalPage" class="page-item" :class="{'active': pagination.pageNum === n}"><a class="page-link" @click="selectPage(n)">{{n}}</a></li>
        <li class="page-item"><a class="page-link" @click="selectPage(pagination.pageNum + 1)">Next</a></li>
    </ul>
</template>

<script>
    export default {
        props: {
            pagination: {
                type: Object,
                require: true
            }
        },
        methods: {
            selectPage (pageNum) {
                if (1 <= pageNum && pageNum <= this.totalPage) {
                    this.$emit('selectPage', pageNum)
                }
            },
            updatePagination (pagination) {
                if (pagination) {
                    if (typeof pagination.pageNum === 'number') {
                        this.pagination.pageNum = pagination.pageNum
                    }

                    if (typeof pagination.pageCount === 'number') {
                        this.pagination.pageCount = pagination.pageCount
                    }

                    if (typeof pagination.totalCount === 'number') {
                        this.pagination.totalCount = pagination.totalCount
                    }
                }
            }
        },
        computed: {
            totalPage () {
                return Math.ceil(this.pagination.totalCount / this.pagination.pageCount)
            }
        },
        mounted () {
            this.selectPage(this.pagination.pageNum)
        }
    }
</script>

