<script>
    import request from '../../../lib/request'

    const SETTING_USER_MANAGEMENT_PAGE_ROUTE_PATH = '/user'

    export default {
        data () {
            return {
              isReadOnlyMode: false,
              isAvailableNewUser: true,
              isAdaminAccount: false,
              allLocationChecked : true,
              checkedLocationCount: 0,
              thisPage: {
                  title: '',
                  userId: '',
                  prevPageName: '',
                  prevPageType: ''
              },
              aliasUserPass: '',

              user: {
                // userId: 'tempUser',
                // userName: '임시테스터',
                // organName: '노원구청',
                // email: 'xxx@xxx.com',
                // telNum: '010-xxxx-xxxx',
                // userPass: 'xxxx',
                // photoPath: '',
                // roles: [],
                // organs: []
              },
              roles: [
                {
                    roleName: '총괄관리자',
                    roleId: '1'
                },
                {
                    roleName: '공고문관리자',
                    roleId: '2'
                },
                {
                    roleName: '홍보물관리자',
                    roleId: '3'
                },
                {
                    roleName: '테스트사용자',
                    roleId: '4'
                },
                {
                    roleName: '공고문게시자',
                    roleId: '5'
                },
                {
                    roleName: '홍보물게시자',
                    roleId: '6'
                }
              ],
              locations: {
                name: '지점목록',
                children: [
                ]
              },
              selectedRoleItem: null,
              selectedMyRoleItem: null
            }
        },
        watch: {
            allLocationChecked (val) {
              this.checkedLocationCount = 0

                this.locations.children.forEach(location => {
                    location.checked = val

                    if(location.checked == true)
                      this.checkedLocationCount += 1

                    if(location.children.length > 0)
                    {
                      location.children.forEach(_location => {
                          _location.checked = val

                          if(_location.checked == true)
                            this.checkedLocationCount += 1

                        })
                    }
                })
            }
        },
        compute: {

        },
        methods: {
          locationChecked() {
            let _count = 0
            this.locations.children.forEach(location => {
                if(location.checked == true)
                  _count = _count + 1

                if(location.children.length > 0)
                {
                  location.children.forEach(_location => {
                    if(_location.checked == true)
                      _count = _count + 1
                    })
                }
            })

            this.$log.debug('location checked count :', _count)
            this.checkedLocationCount = _count
          },
          goUserManagement(){
              this.$log.debug('사용자 관리 페이지로 이동', this.$route.name)

              this.$router.push({
                path: SETTING_USER_MANAGEMENT_PAGE_ROUTE_PATH,
                query: {
                }
              })
          },
          addNewUser()
          {
            this.$log.debug('userSetting :  -> ', this.thisPage.title, this.user.userId)
            let message = this.user.userName + ' : ' + this.thisPage.title + ' 하시겠습니까?'

            if (confirm(message)) {

              // 할당된 지점 정보 가져옴
              let allowedLocationList
              // depth1
              allowedLocationList = this.locations.children.reduce(
                  (allowedLocationList, location) => {
                      if(location.children.length > 0)
                      {
                        allowedLocationList = location.children.reduce(
                            (allowedLocationList, _location) => {
                              if (_location.checked) {

                                  // this.$log.debug('addUser -> allowedLocation -> ', _location.organId, _location.name)

                                    allowedLocationList.push({
                                        organId: _location.organId,
                                        organName: _location.name
                                    })
                                }

                                return allowedLocationList
                            },
                            [])
                        }


                        if (location.checked) {
                            //this.$log.debug('addUser -> allowedLocation -> ', location.organId, location.name)

                            allowedLocationList.push({
                                organId: location.organId,
                                organName: location.name
                            })
                        }

                      return allowedLocationList
                  },
                  [])

              this.user.organs = allowedLocationList.slice()
              this.user.sabun = this.user.userId
              // this.user.userPass = this.aliasUserPass

              // 사용자 등록
              if(this.isReadOnlyMode == false || this.thisPage.prevPageName == 'organizationChart') // 조직도에서 넘어온 것이면 사용자 등록
              {
                request.user.addUser(this.user)
                .then(result => {
                  this.$log.debug('add user -> ', result)

                  if (result.data.result === 200) {
                      alert('사용자 등록이 완료되었습니다.')

                      this.goUserManagement()
                  } else {
                    alert('사용자 등록이 실패했습니다. (' + result.data.resultMsg + ')')
                    // fail
                  }
                })
                .catch(err => {
                  alert('사용자 등록이 실패했습니다. (' + err.message + ')')
                })
              }
              else { // 사용자 수정
                request.user.updateUser(this.user)
                .then(result => {
                  this.$log.debug('update user -> ', result)

                  if (result.data.result === 200) {
                      alert('사용자 수정이 완료되었습니다.')

                      this.goUserManagement()
                  } else {
                    alert('사용자 수정이 실패했습니다. (' + result.data.resultMsg + ')')
                    // fail
                  }
                })
                .catch(err => {
                  alert('사용자 수정이 실패했습니다. (' + err.message + ')')
                })
              }
            }

          },
          addRoleToMyRole(role)
          {
            if(this.user.roles)
            {
              let isExist = false

              if(this.user.roles[0])
              {
                let isExist = this.user.roles.some((_role, _index) => {
                  if (_role.roleId === role.roleId) {
                    return true
                  }
                })
              }

              if(isExist == false) // 해당 role이 없으면 추가
              {
                this.user.roles.push({roleName: role.roleName, roleId: role.roleId})
                this.$log.debug('권한 추가 (id, role) -> ', this.user.roles.length, this.thisPage.userId ? this.thisPage.userId : this.user.userId, role.roleName)
              }

              this.roles.some((_role, _index) => {
                //this.$log.debug('권한 검색 -> ', role.roleName, _role.roleName)
                if (_role.roleId === role.roleId) {
                  //this.$log.debug('권한 목록에서 삭제 -> ', this.thisPage.userId ? this.thisPage.userId : this.user.userId, _role.roleName)
                  this.roles.splice(_index, 1)
                  return true
                }
              })
            }
          },
          deleteMyRole(myRole)
          {
            this.$log.debug('권한 삭제 호출됨 -> ', myRole.roleName)
            if(this.user.roles && this.user.roles[0])
            {
              this.user.roles.some((_role, _index) => {
                this.$log.debug('권한 검색 -> ', myRole.roleName, _role.roleName)
                  if (_role.roleId === myRole.roleId) {
                      this.$log.debug('권한 삭제 -> ', this.thisPage.userId ? this.thisPage.userId : this.user.userId, _role.roleName)
                      this.user.roles.splice(_index, 1)

                      this.roles.push({roleName: myRole.roleName, roleId: myRole.roleId})
                      return true
                  }
              })
            }
          },
          getLocation () {

            this.$log.debug('userSetting : getLocation() 얻기')

            if(this.thisPage.userId)
              this.allLocationChecked = false

            request.organ.getLocation()
            .then(result => {
              this.$log.debug('get locations ', result)

              if (result.data.result === 200) {
                //this.treeData.children = result.data.list

                if(this.user.organs)
                {
                  result.data.list.forEach((location) => {
                    location.checked = false

                    this.user.organs.forEach((_myloc) => {
                      if(location.organId === _myloc.organId)
                      {
                        location.checked = true

                        this.checkedLocationCount += 1

                        // this.$log.debug('matched location -> ', location.organId)
                      }
                    })
                  })
                }

                // make locations
                // 1 depth
                let _locations_count = 0;
                this.locations.children =result.data.list.filter(location => location.depth === 1)
                .map(location => {
                  // 2 depth
                  location.children = result.data.list
                  .filter(_location => {
                    return _location.parentId === location.organId
                  })
                  .map(_location => {
                    // 3 depth
                    _location.children = result.data.list
                    .filter(__location => {
                      return __location.parantId === _location.organId
                    })

                    _locations_count++
                    return _location
                  })

                  _locations_count++
                  return location
                })

                // 할당된 지점과 지점목록의 수가 같으면 다 할당된 것으로 판단
                this.$log.debug('할당된지점수, 지점수', this.checkedLocationCount, _locations_count)
                if(this.checkedLocationCount > 0 && this.checkedLocationCount == _locations_count)
                {
                  this.allLocationChecked = true
                }


                // // check my location
                // // depth1
                // if(this.locations.children && this.locations.children.length > 0)
                // {
                //   this.locations.children.forEach((location) => {
                //     this.user.organs.forEach((_myloc) => {
                //       if(location.organId === _myloc.organId)
                //       {
                //         location.checked = true
                //
                //         this.$log.debug('mached location -> ', location.organId)
                //       }
                //       else {
                //         location.checked = false
                //       }
                //     })
                //   })
                // }
                // // Depth2
                // if(this.locations.children.children && this.locations.children.children.length > 0)
                // {
                //   this.locations.children.forEach((location) => {
                //     this.user.organs.forEach((_myloc) => {
                //       if(location.organId === _myloc.organId)
                //       {
                //         location.checked = true
                //
                //         this.$log.debug('mached location -> ', location.organId)
                //       }
                //       else {
                //         location.checked = false
                //       }
                //     })
                //   })
                // }

              } else {
                alert('지점 조회를 실패했습니다. (' + result.data.resultMsg + ')')
                // fail
              }
            })
            .catch(err => {
              alert('지점 조회를 실패했습니다. (' + err.message + ')')
            })

          },
          getUserInfoByPrevPage () {
            this.$log.debug('userSetting : getUserInfo() 이전페이지에 의해 다르게 요청함 -> ', this.thisPage.userId, this.thisPage.prevPageType)

            request.user.getUserInfo(this.thisPage.userId, this.thisPage.prevPageType)
            .then(result => {
              this.$log.debug('get usefInfo ', result)

              if (result.data.result === 200) {
                this.user = result.data.data

                // 새 암호를 입력하면 반영하기 위해 기존 암호를 초기화함
                // 새 암호를 입력하지 않으면 서버단에서 기존 암호를 그냥 사용하도록 넣어줌
                this.user.userPass = ''

                // 보유 권한 체크
                if(this.user && this.user.roles)
                {
                  if(this.user.roles[0]) // 보유 권한이 있으면
                  {
                    // this.$log.debug('보유 권한 체크')
                    this.user.roles.forEach(_myRole => this.roles.some((_role, _index) => {
                      // this.$log.debug('보유 권한 -> ', _myRole.roleName, _myRole.roleId, _role.roleName, _role.roleId)
                        if (_role.roleId === _myRole.roleId) {
                          // this.$log.debug('보유 권한이라 목록에서 삭제 -> ', this.thisPage.userId ? this.thisPage.userId : this.user.userId, _role.roleName)
                          this.roles.splice(_index, 1)
                          return true
                        }
                      }))
                    }
                    else {
                      // 보유 권한이 없는데 자리만 차지하고 있어서 삭제
                      // 빈 권한을 없애야 새로운 것을 추가할 수 있음
                      this.user.roles.splice(0, 1)
                    }
                }


              } else {
                alert('사용자 조회를 실패했습니다. (' + result.data.resultMsg + ')')
                // fail
              }
            })
            .catch(err => {
              alert('사용자 조회를 실패했습니다. (' + err.message + ')')
            })
          },

          getUserInfo () {
            this.$log.debug('userSetting : getUserInfo() 얻기 -> ', this.thisPage.userId, this.thisPage.prevPageName)

            if(this.thisPage.prevPageName == 'organizationChart') // 조직도에서 넘어온 것이면
            {
              this.thisPage.prevPageType = 0
              this.getUserInfoByPrevPage ()

            }else if(this.thisPage.prevPageName == 'userManagement') // 사용자관리에서 넘어온 것이면
            {
              this.thisPage.prevPageType = 1
              this.getUserInfoByPrevPage ()
            }
          }
        },
        created () {
          this.$log.debug('userId : ', this.$route.query.userId, this.$route.query.prevPage)

          if(this.$route.query.userId) // 있는 사용자 조회이면
          {
            if(this.$route.query.userId === 'admin') // admin 계정인지 체크 -> 암호만 바꿀 수 있음
            {
              this.isAdaminAccount = true;
            }

            if(this.$route.query.prevPage == 'organizationChart') // 조직도에서 넘어온 것이면 사용자 등록
            {
              this.thisPage.title = '사용자등록'
            }
            else { // 사용자 관리에서 넘어온 것이면 사용자 수정
              this.thisPage.title = '사용자수정'
            }
            this.isReadOnlyMode = true
            this.thisPage.userId = this.$route.query.userId
            this.thisPage.prevPageName = this.$route.query.prevPage
            this.getUserInfo(this.thisPage.userId)
          }else{
            //신규 사용자 등록을 막음
            // {{
            this.isReadOnlyMode = true
            this.isAvailableNewUser = false
            //}}

            this.thisPage.title = '사용자등록'
            this.isReadOnlyMode = false
            this.allLocationChecked = false
            this.thisPage.prevPageName = this.$route.query.prevPage
            this.user =   {
                            userId: '',
                            userName: '',
                            organName: '노원구',
                            email: '',
                            telNum: '',
                            userPass: '',
                            photoPath: '',
                            roles: [],
                            organs: [],
                            organId: '3100000',
                            department: '노원구',
                            userInfo: '',
                            sabun: ''
                          }
          }

          this.getLocation()
        },
        mounted()
        {
        },
        components: {
        }
    }
</script>

