export default {
    namespaced: true,
    state: {
        locations: [],
        selectedLocations: []
    },
    mutations: {
        setData (state, data) {
            if (data) {
                state.locations = data.locations
                state.selectedLocations = data.selectedLocations
            }
        }
    },
    actions: {
        setData (context, data) {
            context.commit('setData', data)
        }
    }
}
