<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">사용자관리</li>
        </ol>
        <div class="container-fluid">
            <div class="card">
                <div class="card-header">
                    <!--<i class="fa fa-align-justify"></i>-->
                    <i class="icon-people mx-1"> 목록
                    <!--<span class="badge badge-success list-count">총개수 1</span>-->
                    <span class="badge list-count">총개수 {{userList.length}}</span>

                    <div class="card-header-actions">
                      <button class="btn btn-primary" @click="goRequestPermission()"><i class="icon-paper-plane" style="padding-right: 3px"></i>권한요청 내역</button>
                    </div>

                </div>
                <div class="card-body">
                    <table class="table table-striped">
                        <thead>
                            <tr>
                                <th><input type="checkbox" v-model="allChecked"/></th>
                                <th>
                                  사진
                                </th>
                                <th>
                                    ID
                                </th>
                                <th>
                                    이름
                                    <!-- <span>
                                        <i class="icon-arrow-down sort-icon"></i>
                                    </span> -->
                                </th>
                                <th>
                                    소속
                                </th>
                                <th>
                                    연락처
                                </th>
                                <th>
                                    E-Mail
                                </th>
                                <th>
                                    권한
                                </th>
                                <th>
                                    할당된 지점
                                </th>
                                <th>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="user in userList">
                                <td><input type="checkbox" v-model="user.checked"/></td>
                                <td>
                                  <i class="fa fa-photo" style="padding-right: 3px"></i>
                                  <img :src="user.img" alt=""/>
                                </td>
                                <td :style="user.userId != 'admin' ? 'color: orange;' : 'color: red;'">
                                  <i class="fa fa-user" style="padding-right: 3px"></i>
                                  {{user.userId}}
                                </td>
                                <td>
                                  <a class="text-primary">
                                    <i class="fa fa-tag" style="padding-right: 3px; color: primary;"></i>
                                    <strong>{{user.userName}}</strong>
                                  </a>
                                </td>
                                <td>
                                  <i v-show="user.organName && user.organName.length > 0" class="fa fa-sitemap" style="padding-right: 3px"></i>
                                  {{user.organName}}
                                </td>
                                <td>
                                  <i v-show="user.telNum && user.telNum.length > 0" class="fa fa-phone" style="padding-right: 3px"></i>
                                  {{user.telNum}}
                                </td>
                                <td>
                                  <i v-show="user.email && user.email.length > 0" class="fa fa-envelope" style="padding-right: 3px"></i>
                                  {{user.email}}
                                </td>
                                <td>
                                  <i :class="getDisplayRolesIcon(user)" style="padding-right: 3px;color: red;"></i>
                                  {{getDisplayRoles(user)}}
                                </td>
                                <td>
                                  <div v-show="user.organs && user.organs.length > 0" class="dropdown">
                                    <button class="btn btn-ghost-info dropdown-toggle col-offset-6" id="alllocations" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                      {{user.organs && user.organs[0] ? user.organs[0].organName : ''}}
                                    </button>
                                    <div class="dropdown-menu" x-placement="top-start" style="position: absolute; will-change: transform; top: 0px; left: 0px; transform: translate3d(0px, -356px, 0px);">
                                      <textarea class="form-control" id="textarea-input" name="textarea-input" rows="4" placeholder="할당된 지점" readonly>{{getLocationDisplay(user)}}</textarea>
                                    </div>
                                  </div>
                                </td>
                                 <td>
                                    <button class="btn btn-outline-warning btn-update btn-sm" @click="goAllowRole(user)">
                                        권한변경
                                    </button>
                                    <button v-show="user.userId != 'admin'" class="btn btn-outline-danger btn-sm" @click="deleteUser(user)">
                                        삭제
                                    </button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                    <!--<ul class="pagination">
                        <li class="page-item"><a class="page-link" @click="searchNoti(pagination.pageNum - 1)">Prev</a></li>
                        <li v-for="n in totalPage" class="page-item" :class="{'active': pagination.pageNum === n}"><a class="page-link" @click="searchNoti(n)">{{n}}</a></li>
                        <li class="page-item"><a class="page-link" @click="searchNoti(pagination.pageNum + 1)">Next</a></li>
                    </ul>
                    <div>
                        <button class="btn btn-danger btn-delete" style="margin-right: 1rem; width: 77px;" @click="removeNoti">삭제</button>
                        <button v-if="addButton" class="btn btn-primary btn-add" style="width: 77px;" @click="addNoti">등록</button>
                    </div>-->
                </div>
            </div>
        </div>
    </main>
</template>

<script>
    import request from '../../../lib/request'

    const SETTING_ALLLOW_ROLE_PAGE_ROUTE_PATH = '/user/setting'
    const SETTING_RESQUEST_PERMISSION_PAGE_ROUTE_PATH = '/user/reqestPermission'

    export default {
        data () {
            return {
                allChecked: false,
                pagination: {
                    pageNum: 1,
                    pageCount: 10,
                    totalCount: 1
                },
                userList: [
                    {
                        img: '',
                        name: '사용자1',
                        location: '',
                        phone: '010-xxxx-xxxx',
                        email: 'xxx@gmail.com',
                        permissions: '공,홈'
                    }
                ]
            }
        },
        computed: {
            totalPage () {
                return Math.ceil(this.pagination.totalCount / this.pagination.pageCount)
            }
        },
        created () {
          this.getUser()
        },
        methods: {
          goRequestPermission(){
            //if (confirm('권한 요청 내역 메뉴로 이동하시겠습니까?')) {
              this.$log.debug('권한 요청 내역 페이지로 이동')

              this.$router.push({
                path: SETTING_RESQUEST_PERMISSION_PAGE_ROUTE_PATH,
                query: {
                }
              })
            //}
          },
          goAllowRole(user){
            //if (confirm('권한 변경 메뉴로 이동하시겠습니까?')) {
              //this.$log.debug('권한 변경 페이지로 이동', user, this.$route.name)

              this.$router.push({
                path: SETTING_ALLLOW_ROLE_PAGE_ROUTE_PATH,
                query: {
                  userId: user.userId,
                  prevPage: this.$route.name
                }
              })
            //}
          },
          getUser()
          {
            request.user.getUser()
                .then(result => {
                    this.$log.debug('get user result ', result)

                    if (result.data.result === 200) {
                        this.userList = result.data.list
                    } else {
                        throw new Error(result.data.resultMsg)
                    }
                })
                .catch(err => {
                    alert('사용자 정보를 조회하는 것을 실패했습니다\n(' + err.message + ')')
                })
          },
          deleteUser(user)
          {
            let message = '사용자[' + user.userName + ']를 삭제하시겠습니까?'
            if (confirm(message)) {
            request.user.deleteUser(user.userId)
                .then(result => {
                    this.$log.debug('delete user result ', result)

                    if (result.data.result === 200) {

                      alert('사용자 삭제를 완료하였습니다.')

                      this.getUser()

                    } else {
                        throw new Error(result.data.resultMsg)
                    }
                })
                .catch(err => {
                    alert('사용자 삭제를 실패했습니다\n(' + err.message + ')')
                })
              }
          },
          getDisplayRolesIcon(user)
          {
            // this.$log.debug('getDisplayRolesIcon -> ', user)

            let role;
            if(user.roles != null)
            {
              role = user.roles.map(role => {
                if(role != null)
                  return role.roleName
                else
                  return ''}).join(',')

              if(role != '')
              {
                return "fa fa-key"
              }
            }
            return ''
          },
          getDisplayRoles (user) {
            if(user.roles != null)
            {
              return user.roles.map(role => {
                if(role != null)
                  return role.roleName
                else
                  return ''}).join(',')
            }
            else{
              return ''
            }
          },
          getLocationDisplay(user)
          {
            // this.$log.debug('getLocationDisplay -> organs len= ', user.organs.length)
            //
            // if(user.organs.length > 0)
            // this.$log.debug('getLocationDisplay -> organs[0]= ', user.organs[0].organName)

            if(user.organs != null)
            {
              return user.organs.map(location => location.organName).join(',')
            }
            else{
              return ''
            }
          }
        }
    }
</script>

<style>
button.btn-update {
    margin-right: 10px;
}
</style>
