											    @selectedItem="selectContentType"
											    ></dropdown>
											<div v-if="contentType === contentTypes[0]" class="col-9 custom-file" style="margin-left: 15px;">
											    <input type="file" id="customFile" ref="file" class="custom-file-input" :accept="contentType.mediaType" @change="handleFileUpload()"/>
											    <label for="customFile" class="custom-file-label" key="type1.value">{{fileName}}</label>
											</div>
                      <div v-else-if="contentType === contentTypes[1]" class="col-9 custom-file" style="margin-left: 15px;">
                          <input type="file" id="customFile" ref="fileMovie" class="custom-file-input" :accept="contentType.mediaType" @change="handleMovieFileUpload()"/>
                          <label for="customFile" class="custom-file-label" key="type2.value">{{fileMovieName}}</label>
                      </div>
											<div v-else-if="contentType === contentTypes[2]" class="col-9">
											    <input type="text" class="form-control" placeholder="자막을 입력하세요" v-model="contentType.name" key="type3.value"/>
											</div>
                          <div v-else-if="contentType === contentTypes[3]" class="col-9">
											    <input type="text" class="form-control" placeholder="컨텐츠 URL을 입력하세요.(http://www.xxxx.co.kr)" v-model="contentType.name" key="type4.value"/>
											</div>
										</div>
									</div>
								</div>
							</div>
							<div class="row">
								<div class="col-sm-12">
									<div class="form-group">
										<label>게시날짜</label>
										<div class="input-group">
											<span class="input-group-prepend">
												<span class="input-group-text">
													<i class="fa fa-calendar"></i>
												</span>
											</span>
											<input class="form-control" name="daterange" type="text" autocomplete="off"/>
										</div>
									</div>
								</div>
							</div>
							<div class="row">
								<div class="col-sm-12">
									<div class="form-group">
										<label>게시 위치</label>
										<span style="padding-left: 3em;">
											<input class="form-check-input" id="check1" type="checkbox" v-model="isAllSelectLocation">
											<label class="form-check-label" for="check1">할당된 모든 지점</label>
										</span>
										<div class="input-group">
											<span class="input-group-prepend">
												<span class="input-group-text">
													<i class="fa fa-sitemap"></i>
												</span>
											</span>
                                            <textarea class="form-control" id="textarea-input" name="textarea-input" rows="9" placeholder="게시위치" readonly>{{locationDisplay}}</textarea>
										</div>
									</div>
								</div>
							</div>
							<div class="row">
								<div class="form-group col-sm-7">
									<label for="city">게시완료 파일</label>
									<div class="col-md-9 col-form-label">
										<div class="form-check form-check-inline mr-1">
											<input class="form-check-input" type="radio" name="isSave" value="Y" v-model="delExpired"/>
											<label class="form-check-label" for="check1">삭제</label>
										</div>
										<div class="form-check form-check-inline mr-1">
											<input class="form-check-input" type="radio" name="isSave" value="N" v-model="delExpired">
											<label class="form-check-label" for="check2">유지</label>
										</div>
									</div>
								</div>
								<!--<div class="form-group col-sm-4">

                                </div>-->
							</div>
							<div class="row">
								<div class="col-sm-12">
									<div class="form-group">
										<label>태그</label>
										<div class="input-group">
											<span class="input-group-prepend">
												<span class="input-group-text">
													<i class="fa fa-hashtag"></i>
												</span>
											</span>
											<input type="text" class="form-control" placeholder="#공문#복지#지원금" v-model="tags"/>
										</div>
									</div>
								</div>
							</div>
							<div class="row">
                                <div class="col-sm-9">
                                    <div v-if="uploadPercentage > 0" class="progress">
                                        <div class="progress-bar" role="progressbar" :style="'width: ' + uploadPercentage + '%'" :aria-valuenow="uploadPercentage" aria-valuemin="0" aria-valuemax="100"></div>
                                    </div>
                                </div>
							    <div class="col-sm-3">
                                  <button v-if="isUpdate" :disabled="isUploading==true" class="btn btn-primary" style="float: right;" @click="updatePromotion">
                                    <i class="fa fa-eraser"></i>
                                      홍보물 수정
                                    </button>
                                    <button v-else :disabled="isUploading==true" class="btn btn-primary" style="float: right;" @click="addPromotion">
                                      <i class="fa fa-cloud-upload"></i>
                                      홍보물 올리기
                                    </button>
							    </div>
							</div>
						</div>
					</div>
				</div>
				<div class="col-sm-6">
					<div class="card">
						<div class="card-header">
							<i class="icon-note"></i><strong> 홍보물 미리보기</strong>
						</div>
						<div class="card-body">
							<div style="height:685px">
                <div class="col">
                                <!-- 이미지 -->
                                <div v-if="contentPath && contentType === contentTypes[0]" id="carouselContentIndicators" class="carousel slide" data-ride="carousel" data-interval="false">
                                    <!-- <img class="d-block" :src="contentPath"> -->
                                    <img class="img-fluid img-thumbnail" :src="contentPath" style="display:block; width: 100%; height: auto; max-height: 660px;">
                                </div>
                                <!-- 동영상 -->
                                <div v-else-if="contentPath && contentType === contentTypes[1]" id="carouselContentIndicators" class="carousel slide" data-ride="carousel" data-interval="false">
                                    <video class="d-block embed-responsive embed-responsive-16by9" style="width:385px !important;" :src="contentPath" controls autoplay loop  />
                                </div>
                                <!-- 자막 -->
                                <div v-else-if="contentPath && contentType === contentTypes[2]" id="carouselContentIndicators" class="carousel slide" data-ride="carousel" data-interval="false">
                                    <div>{{contentPath}}</div>
                                </div>
                                <!-- 웹URL -->
                                <div v-else-if="contentPath && contentType === contentTypes[3]" id="carouselContentIndicators" class="carousel slide" data-ride="carousel" data-interval="false">
                                    <iframe :src="contentPath" style="display:block; width: 100%; height: 640px"/>
                                </div>
                </div>

							</div>
						</div>
					</div>
				</div>
        	</div>
        </div>
    </main>
</template>

<script>
    import LinkedDatePicker from '../LinkedDatePicker.vue'
    import Dropdown from '../Dropdown.vue'
    import TreeList from '../TreeList.vue'

    import request from '../../../lib/request'
    import utils from '../../../lib/utils'
    import dateUtils from '../../../lib/date'

    import CONTENT_TYPE from '../../code/contentType'

    export default {
        data () {
            return {
                isUrgentData: false,
                isAllSelectLocation: true,
                viewTitle: '홍보물 등록',
                isUpdate: false,
                isUploading: false,
                file: null,
                fileMovie: null,
                modifyFile: '파일명',
                uploadPercentage: 0,
                contentTypes: CONTENT_TYPE,
                locations: {
                    checked: false,
                    name: '할당된 모든 지점',
                    children: []
                },
                // 컨텐츠 데이터
                title: '',
//                postingStart: dateUtils.toFormat('YYYYMMDD000000'),
//                postingEnd: dateUtils.toFormat('YYYYMMDD235959'),
                postingStart: null,
                postingEnd: null,
                tags: '#홍보',
                location: [],
                delExpired: 'Y',
                textPosition: '',
                contentType: CONTENT_TYPE[0],
                contentInfo: '',
                // 미리보기
                contentPath: '',
                contents: []
            }
        },
        watch: {
            isAllSelectLocation (val) {
              this.$log.debug('isAllSelectLocation :  ', val)
              if (val) {
                  this.locations.children.forEach(location => location.checked = true)
                  this.location.splice(0, this.location.length)
                  this.location.splice(0, 0, ...this.locations.children)
              } else {
                  this.locations.children.forEach(location => location.checked = false)
                  this.location.splice(0, this.location.length)
              }
            },
            'locations.checked': function (newVal, oldVal) {
                this.$log.debug('locations.checked ', newVal)

                if (newVal) {
                    this.locations.children.forEach(location => location.checked = true)
                    this.location.splice(0, this.location.length)
                    this.location.splice(0, 0, ...this.locations.children)
                } else {
                    this.locations.children.forEach(location => location.checked = false)
                    this.location.splice(0, this.location.length)
                }
            }
        },
        mounted () {
            this.$log.debug('!@#!# ', $('input[name="daterange"]'))
            $('input[name="daterange"]').daterangepicker();
        },
        created () {
            this.$log.debug('promotion contentId ', this.$route.query.contentId)

            // 리셋 컨텐츠타입 이름
            this.resetContentTypeName()

            let promises = [
              // 등록시 계정에 할당된 지점만 보이도록 하기 위해서 수정
                // request.organ.getLocation()
                request.organ.getAllowedLocation()
                    .then(result => {
                        this.$log.debug('get allowed location ', result)
                        if (result.data.result === 200) {
                            result.data.list.forEach(location => {
                                location.checked = false
                            })

                            this.locations.children.splice(0, 0, ...result.data.list)
                            //this.location.splice(0, 0, ...result.data.list)
                        } else {
                            throw new Error(result.data.resultMsg)
                        }

                        return result.data
                    })
                    .catch(err => {
                        throw new Error('홍보물 게시 위치를 조회하는 것을 실패했습니다.\n(' + err.message + ')')
                    })
            ]

            if (this.$route.query.contentId) {
                this.isUpdate = true
                this.viewTitle = '홍보물 수정'
                this.isAllSelectLocation = false

                promises.push(request.promotion.searchPromotionDetail(this.$route.query.contentId)
                    .then(result => {
                        this.$log.debug('get promotion detail ', result)

                        if (result.data.result === 200) {
                            this.$log.info('홍보물 조회 성공 ', result)

                            this.title = result.data.data.title
                            this.modifyFile = result.data.data.originFile
                            this.postingStart = result.data.data.postingStart + '000000'
                            this.postingEnd = result.data.data.postingEnd + '000000'
                            $('input[name="daterange"]').data('daterangepicker').setStartDate(utils.date.toDate(result.data.data.postingStart))
                            $('input[name="daterange"]').data('daterangepicker').setEndDate(utils.date.toDate(result.data.data.postingEnd))
                            $('input[name="daterange"]').val(
                                utils.date.toFormat(utils.date.toDate(result.data.data.postingStart), 'YYYY.MM.DD')
                                + ' - ' +
                                utils.date.toFormat(utils.date.toDate(result.data.data.postingEnd), 'YYYY.MM.DD'))
                            this.location = result.data.data.location ? result.data.data.location : []
                            this.tags = result.data.data.tags
                            this.delExpired = result.data.data.delExpired

                            // 컨텐츠 타입에 따라 경로명 설정이 다름
                            // this.contentPath = result.data.data.contentPath
                            // this.contentName = result.data.data.contentName

                            this.contentPath = ((result.data.data.contentType === this.contentTypes[2].value) || (result.data.data.contentType === this.contentTypes[3].value)) ? result.data.data.contentInfo : result.data.data.contentPath + '/' + result.data.data.contentName
                            this.contentInfo = result.data.data.contentInfo
                            this.contentTypes.forEach((_contentType, _index) => {
                              if(_contentType.value === result.data.data.contentType)
                              {
                                this.contentType = _contentType
                                if(_index == 0 || _index == 1)
                                {
                                  this.contentType.name = this.modifyFile
                                }
                                else {
                                  this.contentType.name = this.contentInfo
                                }

                                return
                              }
                            })
                            this.$refs.contentTypeDropdown.selectItem(this.contentTypes.find(_contentType => _contentType.value === result.data.data.contentType))

                            this.$log.debug('originFile : ', this.modifyFile, result.data.data.originFile)
                            this.$log.debug('contentInfo : ', this.contentInfo, result.data.data.contentInfo)

                            // 이미지 캐시를 막기 위해 경로뒤에 변화되는 값인 getTime()을 붙여줌
                            if(this.contentType.value === this.contentTypes[0].value)
                            {
                              var tmpDate = new Date();
                              this.contentPath = this.contentPath + '?' + tmpDate.getTime()
                            }

                            // 긴급 데이터처리
                            this.isUrgentData = this.urgentYn == 'Y' ? true : false

                        } else {
                            throw new Error(result.data.resultMsg)
                        }

                        return result.data
                    })
                    .catch(err=> {
                        this.$log.error('홍보물 조회를 실패했습니다. err :', err)
                        throw new Error('홍보물 조회를 실패했습니다.\n(' + err.message + ')')
                    }))
            }

            Promise.all(promises)
                .then(result => {
                  if(this.isUpdate == false)
                  {
                     // 신규등록이면 할당된 지점 모두 추가함
                      this.$log.debug('신규등록이라서 할당된 모든 지점 추가함')
                      this.locations.children.forEach(location => location.checked = true)
                      this.location.splice(0, this.location.length)
                      this.location.splice(0, 0, ...this.locations.children)
                  }

                    if (result.length === 2) {
                        this.$log.debug('수정이라서 지정된 게시 위치 얻어옴')

                        if (Array.isArray(result[1].data.location)) {
                            this.locations.children.forEach(location => {
                                result[1].data.location.some(organId => {
                                    if (organId === location.organId) {
                                        location.checked = true
                                        return true
                                    }
                                })
                            })

                            this.$log.debug('!@#!@ ', this.locations)
                        }

                      // 모든 지점이 할당된 상태이면
                      if(this.locations.children.length == result[1].data.location.length)
                      {
                        this.isAllSelectLocation = true
                        this.$log.debug('수정이지만 모든 지점 할당된 상태임')
                      }
                    }
                })
                .catch(err => alert(err.message))
        },
        components: {
            LinkedDatePicker,
            Dropdown,
            TreeList
        },
        methods: {
          getContentType(type)
          {
             this.$log.debug('getContentType ', type)

              this.contentTypes.forEach(contentType =>
                {
                  if(contentType.value === type)
                  {
                    this.$log.debug('getContentType matched : ', contentType.value)
                    return contentType
                  }
                })

                return this.contentTypes[0]
          },
          resetContentTypeName()
          {
            this.contentTypes.forEach(contentType =>
              {
                  this.$log.debug('resetContentTypeName() contentType.name : ', contentType.name)
                  contentType.name = ''
                })
          },
            checkLocation (location, checked) {
                this.$log.debug('checkLocation ', location, checked)

                if (checked) {
                    this.location.push(location)
                } else {
                    this.location.some((_location, _index) => {
                        if (_location.organId === location.organId) {
                            this.location.splice(_index, 1)
                            return true
                        }
                    })
                }
            },
            handleFileUpload () {
                this.$log.debug('handleFileUpload file : ', this.$refs.file.files[0])
                this.file = this.$refs.file.files[0];

//                 if(window.FileReader){ //FileReader를 지원하는 브라우저의 경우 IE 10이상, 크롬..
//
//                   this.$log.debug('handleFileUpload FileReader : ', this.$refs.file.value)
//
//                    var reader = new FileReader();
//                        reader.onload = function (e) {
// //this.$log.debug('handleFileUpload FileReader onload() : ', e.target.result)
//                            $('body').append('<img src="" id="temp_img" style="display:none;" />');  //보이지 않는 임시 img 태그를 생성.
//                            var img = $('#temp_img').attr('src', e.target.result);                          //파일을 선택했을 경우 정보를 $img 객체에 저장
// alert('이미지 크기.('+img.width() + 'x'+ img.height() +')');
//
//                            // if($img.width() != $width || $img.height() != $height){                  //가로 세로 사이즈 비교 후 반환
//                            //      alert('지정된 크기와 맞지 않습니다.('+$width + 'x'+ $height +')');
//                            //      $target.val('');
//                            //      $('#temp_img').remove(); //위에서 생성한 임시 img 태그 삭제
//                            //      return;
//                            //  }
//                       };
//                       reader.readAsDataURL(this.$refs.file.files[0]);  //파일을 img 태그에 보여줄 수 있도록 base64로 url을 생성합니다.
//                       $('#temp_img').remove();
//                  }

                this.$log.debug('handleFileUpload org contentType.name : ', this.contentType.name)
                this.contentType.name = this.file ? this.file.name : this.modifyFile
                this.$log.debug('handleFileUpload contentType.name : ', this.contentType.name)
            },
            handleMovieFileUpload () {
                this.$log.debug('handleFileUpload file : %s', this.$refs.fileMovie.files[0])
                this.fileMovie = this.$refs.fileMovie.files[0];

                this.$log.debug('handleFileUpload org contentType.name : ', this.contentType.name)
                this.contentType.name = this.fileMovie ? this.fileMovie.name : this.modifyFile
                this.$log.debug('handleFileUpload contentType.name : ', this.contentType.name)
            },
            addPromotion () {
                let formData

                if (this.title ==='')
                {
                  alert('제목을 입력하세요')
                  return;
                }

                if(this.contentType == this.contentTypes[0]) // 이미지
                {
                   if(this.file === null || this.file.name === '') {
                     alert('이미지 컨텐츠를 선택하세요')
                     return;
                   }
                }
                else if(this.contentType == this.contentTypes[1]) // 동영상
                {
                   if(this.fileMovie === null || this.fileMovie.name === '') {
                     alert('동영상 컨텐츠를 선택하세요')
                     return;
                   }
                }
                else if(this.contentType == this.contentTypes[2]) // 자막
                {
                  this.contentInfo = this.contentType.name
                   if(this.contentInfo == '') {
                     alert('자막을 입력하세요')
                     return;
                   }
                }
                else if(this.contentType == this.contentTypes[3]) // 웹URL
                {
                  this.contentInfo = this.contentType.name
                   if(this.contentInfo == '') {
                     alert('웹 URL을 입력하세요')
                     return;
                   }
                }


                if (this.postingStart === null || this.postingEnd === null) {
                  alert('게시날짜를 선택하세요')
                  return;
                }

                //if (confirm('홍보용 컨텐츠를 등록하시겠습니까?')) {
                    formData = this.promotionFormData

                    request.promotion.promotionAdd(
                        formData,
                        progressEvent => {
                            this.isUploading = true
                            this.uploadPercentage = parseInt( Math.round( ( progressEvent.loaded * 100 ) / progressEvent.total ) );
                        })
                        .then(result => {
                            this.$log.debug('홍보물 등록 요청 결과 ', result)

                            this.isUploading = false

                            if (result.data.result === 200) {
                                this.$log.info('홍보물 등록 완료 ', result)
                                confirm('홍보물 등록을 완료했습니다.')

                                //this.contents = result.data.contents
                                //this.contentPath = result.data.contentPath
                                this.contentPath = ''
                                this.contentPath = ((this.contentType.value === this.contentTypes[2].value) || (this.contentType.value === this.contentTypes[3].value)) ? this.contentInfo : result.data.contentPath
                                this.uploadPercentage = 0

                                this.$log.error('홍보물 등록 완료 컨텐츠 경로 : ', this.contentType.value, this.contentPath)

                                // 이미지 캐시를 막기 위해 경로뒤에 변화되는 값인 getTime()을 붙여줌
                                if(this.contentType.value === this.contentTypes[0].value)
                                {
                                  var tmpDate = new Date();
                                  this.contentPath = this.contentPath + '?' + tmpDate.getTime()
                                }

                            } else {
                                alert(result.data.resultMsg)
                            }
                        })
                        .catch(err => {
                            this.$log.error('홍보물 등록 실패', err.stack)
                            this.uploadPercentage = 0
                            this.isUploading = false
                            alert('홍보물 등록을 실패했습니다.\n(' + err.message + ')')
                        })
                //}
            },
            updatePromotion () {
                let formData

                if (this.title ==='')
                {
                  alert('제목을 입력하세요')
                  return;
                }

                if (this.postingStart === null || this.postingEnd === null) {
                  alert('게시날짜를 선택하세요')
                  return;
                }

                //if (confirm('홍보용 컨텐츠를 수정하시겠습니까?')) {
                    formData = this.promotionFormData

                    request.promotion.updatePromotion(
                        this.$route.query.contentId,
                        formData,
                        progressEvent => {
                          this.isUploading = true
                            this.uploadPercentage = parseInt( Math.round( ( progressEvent.loaded * 100 ) / progressEvent.total ) );
                        })
                        .then(result => {
                            this.$log.debug('홍보물 수정 요청 결과 ', result)

                        this.isUploading = false

                            if (result.data.result === 200) {
                                this.$log.info('홍보물 수정 완료 ', result)
                                confirm('홍보물 수정을 완료했습니다.')

                                //this.contentPath = result.data.contentPath
                                this.uploadPercentage = 0
                                this.contentPath = ((this.contentType.value === this.contentTypes[2].value) || (this.contentType.value === this.contentTypes[3].value)) ? this.contentInfo : result.data.contentPath

                                // 이미지 캐시를 막기 위해 경로뒤에 변화되는 값인 getTime()을 붙여줌
                                if(this.contentType.value === this.contentTypes[0].value)
                                {
                                  var tmpDate = new Date();
                                  this.contentPath = this.contentPath + '?' + tmpDate.getTime()
                                }


                              //   // 서버에서 파일 변경하는 동안 시간이 걸림
                              //   setTimeout(
                              //       () => {
                              //         this.contentPath = ((this.contentType.value === this.contentTypes[2].value) || (this.contentType.value === this.contentTypes[3].value)) ? this.contentInfo : result.data.contentPath
                              //         this.$log.error('홍보물 수정 완료 컨텐츠 경로 : ', this.contentType.value, this.contentPath)
                              // },
                              // 5000)

                            } else {
                                alert(result.data.resultMsg)
                            }
                        })
                        .catch(err => {
                          this.uploadPercentage = 0
                          this.isUploading = false
                            this.$log.error('홍보물 수정 실패', err.stack)
                            alert('홍보물 수정을 실패했습니다.\n(' + err.message + ')')
                        })
                //}
            },
            selectContentType (contentType) {
                if (contentType) {
                    this.contentType = contentType
                }
            },
            goPrev()
            {
              this.$router.go(-1); // 이전 페이지로
            }

        },
        computed: {
           fileName () {

              this.$log.debug('fileName () file : ', this.file? this.file.name : '')
              this.contentType.name = this.file && this.file.name != this.contentType.name ? this.file.name : this.contentType.name
              this.$log.debug('fileName () contentType.name : ', this.contentType.name)

              this.modifyFile = this.contentType.name
              this.$log.debug('fileName () modifyFile : ', this.modifyFile, this.contentType.name)

              return (this.contentType.name ? this.contentType.name : '이미지 컨텐츠를 선택하세요')
           },
           fileMovieName () {

              this.contentType.name = this.fileMovie && this.fileMovie.name != this.contentType.name ? this.fileMovie.name : this.contentType.name
              this.modifyFile = this.contentType.name
              this.$log.debug('modifyFile : ', this.modifyFile, this.contentType.name)

              return (this.contentType.name ? this.contentType.name : '동영상 컨텐츠를 선택하세요')
           },
            locationDisplay () {
                let arr = this.location.map(location => location.name)
                this.$log.debug('arr ', arr)
                return arr.join(',')
            },
            promotionFormData () {
                let formData = new FormData()

                if (this.isFile) {
                    if(this.contentType === this.contentTypes[0]) // 이미지
                    {
                      if(this.file)
                        formData.append('file', this.file)
                    }
                    else {
                      // 동영상
                      if(this.fileMovie)
                        formData.append('file', this.fileMovie)
                    }
                } else {
                    this.contentInfo = this.contentType.name
                    formData.append('contentInfo', this.contentInfo)
                }

                formData.append('title', this.title)
                formData.append('postingStart', this.postingStart)
                formData.append('postingEnd', this.postingEnd)
                formData.append('contentType', this.contentType.value)
                formData.append('tags', this.tags)
                formData.append('location', this.location.map(location => location.organId).join(','))
                formData.append('delExpired', this.delExpired)

                // 긴급 데이터처리
                formData.append('urgentYn', this.isUrgentData == true ? 'Y' : 'N')

                return formData
            },
            isFile () {
                return this.contentType === this.contentTypes[0] || this.contentType === this.contentTypes[1]
            }
        },
        mounted () {
            //this.$log.debug('!@#@! ', moment('20191110', 'YYYYMMDD'))
//            var options = {};
//            options.alwaysShowCalendars = true;
//            options.locale = {
//                format: 'YYYY.MM.DD',
//                daysOfWeek: ['일', '월', '화', '수', '목', '금','토'],
//                monthNames: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
//            };
//            options.ranges = {
//                '오늘 날짜': [moment(), moment()]/*,
//              'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
//              'Last 7 Days': [moment().subtract(6, 'days'), moment()],
//              'Last 30 Days': [moment().subtract(29, 'days'), moment()],
//              'This Month': [moment().startOf('month'), moment().endOf('month')],
//              'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
//                                                  */
//            };
//
//            $('input[name="daterange"]').daterangepicker(
//                options,
//                (start, end, label) => {
//                    this.postingStart = start.format('YYYYMMDD000000')
//                    this.postingEnd = end.format('YYYYMMDD235959')
//                });

        	let options = {
                alwaysShowCalendars: true,
                locale: {
                    format: 'YYYY.MM.DD',
                    daysOfWeek: ['일', '월', '화', '수', '목', '금','토'],
                    monthNames: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
                },
                autoUpdateInput: false,
                ranges: {
                    '오늘 날짜': [moment(), moment()]
//                    ,
//                    'Yesterday': [moment().subtract(1, 'days'), moment().subtract(1, 'days')],
//                    'Last 7 Days': [moment().subtract(6, 'days'), moment()],
//                    'Last 30 Days': [moment().subtract(29, 'days'), moment()],
//                    'This Month': [moment().startOf('month'), moment().endOf('month')],
//                    'Last Month': [moment().subtract(1, 'month').startOf('month'), moment().subtract(1, 'month').endOf('month')]
                }
            }

            $('input[name="daterange"]').daterangepicker(
                options,
                (start, end, label) => {
//                    this.postingStart = start.format('YYYYMMDD000000')
//                    this.postingEnd = end.format('YYYYMMDD235959')
                })
            $('input[name="daterange"]').on(
                'apply.daterangepicker',
                (ev, picker) => {
                    this.$log.debug('appay.daterangepicker', picker)
                    $('input[name="daterange"]').val(picker.startDate.format('YYYY.MM.DD') + ' - ' + picker.endDate.format('YYYY.MM.DD'))
                    this.postingStart = picker.startDate.format('YYYYMMDD000000')
                    this.postingEnd = picker.endDate.format('YYYYMMDD235959')
                })
            $('input[name="daterange"]').on(
                'cancel.daterangepicker',
                (ev, picker) => {
                    this.$log.debug('cancel.daterangepicker', picker)
                    $('input[name="daterange"]').val('')
                    this.postingStart = null
                    this.postingEnd = null
                })
        }
    }
</script>

