// index 컴포넌트 라우팅 정의
import Vue from 'vue'
import Router from 'vue-router'

import Home from '../../components/index/Home.vue'

import NotiManagement from '../../components/index/noti/NotiManagement.vue'
import NotiSearch from '../../components/index/noti/NotiSearch.vue'
import NotiAdd from '../../components/index/noti/NotiAdd.vue'

import PromotionManagement from '../../components/index/promotion/PromotionManagement.vue'
import PromotionAdd from '../../components/index/promotion/PromotionAdd.vue'
import PromotionSearch from '../../components/index/promotion/PromotionSearch.vue'
import PromotionTemplateSetting from '../../components/index/promotion/PromotionTemplateSetting.vue'

import ContentsManagement from '../../components/index/contents/ContentsManagement.vue'

import OrganizationChart from '../../components/index/organization/OrganizationChart.vue'

import OfficeManagement from '../../components/index/office/OfficeManagement.vue'

import UserManagement from '../../components/index/user/UserManagement.vue'
import UserSetting from '../../components/index/user/UserSetting.vue'
import RequirePermission from '../../components/index/user/RequirePermission.vue'

import ClientManagement from '../../components/index/client/ClientManagement.vue'
import DBoardSetting from '../../components/index/client/DBoardSetting.vue'
import ClientSchedule from '../../components/index/client/ClientSchedule.vue'

import Log from '../../components/index/system/Log.vue'
import ServerMonitor from '../../components/index/system/ServerMonitor.vue'
import Statistics from '../../components/index/system/Statistics.vue'
import Notification from '../../components/index/system/Notification.vue'


const routes = [
    {
        path: '/',
        name: 'home',
        component: Home
    },
    {
        path: '/noti',
        name: 'notiManagement',
        component: NotiManagement
    },
    {
        path: '/noti/search',
        name: 'notiSearch',
        component: NotiSearch
    },
    {
        path: '/noti/add',
        name: 'notiAdd',
        component: NotiAdd,
        alias: ['/noti/update']
    },
    {
        path: '/promotion',
        name: 'promotionManagement',
        component: PromotionManagement
    },
    {
        path: '/promotion/add',
        name: 'promotionAdd',
        component: PromotionAdd,
        alias: ['/promotion/update']
    },
    {
        path: '/promotion/search',
        name: 'promotionSearch',
        component: PromotionSearch
    },
    {
        path: '/promotion/template',
        name: 'promotionTemplateSetting',
        component: PromotionTemplateSetting
    },
    {
        path: '/contents',
        name: 'contentsManagement',
        component: ContentsManagement
    },
    {
        path: '/organization',
        name: 'organizationChart',
        component: OrganizationChart
    },
    {
        path: '/user/reqestPermission',
        name: 'reqestPermission',
        component: RequirePermission
    },
    {
        path: '/office',
        name: 'officeManagement',
        component: OfficeManagement
    },
    {
        path: '/user',
        name: 'userManagement',
        component: UserManagement
    },
    {
        path: '/user/setting',
        name: 'userSetting',
        component: UserSetting,
        alias: ['/user/update']
    },
    {
        path: '/client',
        name: 'clientManagement',
        component: ClientManagement
    },
    {
        path: '/client/dboard',
        name: 'dBoardSetting',
        component: DBoardSetting
    },
    {
        path: '/client/schedule',
        name: 'clientSchedule',
        component: ClientSchedule
    },
    {
        path: '/system/log',
        name: 'log',
        component: Log
    },
    {
        path: '/system/monitor',
        name: 'serverMonitor',
        component: ServerMonitor
    },
    {
        path: '/system/statistics',
        name: 'statistics',
        component: Statistics
    },
    {
        path: '/system/notification',
        name: 'notification',
        component: Notification
    },
    // default route
    {
        path: '*',
        redirect: '/'
    }
]

Vue.use(Router)

export default new Router({
    mode: 'history',
    scrollBehavior: () => ({ y: 0 }),
    routes
})
