<template>
    <!-- <header class="app-header navbar nav-tabs"> -->
    <header class="app-header navbar">
        <button class="navbar-toggler sidebar-toggler d-lg-none mr-auto" type="button" data-toggle="sidebar-show">
            <span class="navbar-toggler-icon"></span>
        </button>
        <a class="navbar-brand" href="/">
            <!--<div class="navbar-brand-full" src="../asset/brand/logo.svg" width="89" height="25" alt="Nowon Logo">NOWON</div>-->
            <img class="navbar-brand-full" width="120" height="40" src="/img/logo_b.png" alt="노원구청 로고">
            <img class="navbar-brand-minimized" src="/img/logo.png" width="40" height="40" alt="노원구청 로고">
        </a>
        <button class="navbar-toggler sidebar-toggler d-md-down-none" type="button" data-toggle="sidebar-lg-show">
            <span class="navbar-toggler-icon"></span>
        </button>
        <ul class="nav navbar-nav d-md-down-none">
            <li v-for="m in (menu ? menu : [])" class="nav-item px-3">
                <router-link class="nav-link" :to="m.routePath" @click.native="selectMenu(m)" :class="{'active': $store.getters['menu/selectedMenu'].menuCode === m.menuCode}">
                <!-- <router-link class="nav-link" :to="m.routePath" @click.native="selectMenu(m)" :class="{'active': isSelectedMenu(m) }"> -->
                    {{m.menuName}}
                </router-link>
            </li>
        </ul>
        <ul class="nav navbar-nav ml-auto">
            <!--<li class="nav-item d-md-down-none">
                <a class="nav-link" href="#">
                    <i class="icon-bell"></i>
                    <span class="badge badge-pill badge-danger">5</span>
                </a>
            </li>-->
            <li class="nav-item dropdown">
                <a class="nav-link" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">
                    <!--<img class="img-avatar" src="<%= BASE_URL %>img/avatars/6.jpg" alt="admin@bootstrapmaster.com">-->
                    <label class="text-primary">{{userId}}님 로그인</label>
                </a>
                <div class="dropdown-menu dropdown-menu-right">
                    <div class="dropdown-header text-center">
                        <strong>계정</strong>
                    </div>
                    <a class="dropdown-item" href="javascript:void(0);" @click="goNotification()">
                        <i class="fa fa-envelope-o"></i> 알림
                        <span class="badge badge-success">{{getNotificationCount()}}</span>
                    </a>
                    <div class="dropdown-header text-center">
                        <strong>설정</strong>
                    </div>
                    <a class="dropdown-item" href="javascript:void(0);" @click="goMyInfo()">
                        <i class="fa fa-user"></i> 내정보</a>
                    <div class="dropdown-divider"></div>
                    <a class="dropdown-item" href="javascript:void(0);" @click="goLogout()">
                        <i class="fa fa-lock"></i> 로그아웃</a>
                </div>
            </li>
        </ul>
        <button class="navbar-toggler aside-menu-toggler d-md-down-none" type="button" >
          <!-- data-toggle="aside-menu-lg-show"> -->
            <!-- <span class="navbar-toggler-icon"></span> -->
        </button>
        <button class="navbar-toggler aside-menu-toggler d-lg-none" type="button" >
          <!-- data-toggle="aside-menu-show"> -->
            <!-- <span class="navbar-toggler-icon"></span> -->
        </button>
    </header>
</template>

<script>
    //import { createNamespacedHelpers } from 'vuex'

    import request from '../../lib/request'

    //const { mapActions } = createNamespacedHelpers('menu'),
    const menuMap = {
        '0000': {
            routePath: '/'
        },
        '1000': {
            routePath: '/noti'
        },
        '1001': {
            routePath: '/noti'
        },
        '1002': {
            routePath: '/noti/add'
        },
        '1003': {
            routePath: '/noti/search'
        },
        '2000': {
            routePath: '/promotion'
        },
        '2001': {
            routePath: '/promotion'
        },
        '2002': {
            routePath: '/promotion/add'
        },
        '2003': {
            routePath: '/promotion/search'
        },
        '2004': {
            routePath: '/promotion/template'
        },
        '3000': {
            routePath: '/contents'
        },
        '3001': {
            routePath: '/contents'
        },
        '4000': {
            routePath: '/organization'
        },
        '4001': {
            routePath: '/organization'
        },
        '5000': {
            routePath: '/office'
        },
        '5001': {
            routePath: '/office'
        },
        '6000': {
            routePath: '/user'
        },
        '6001': {
            routePath: '/user'
        },
        '6002': {
            routePath: '/user/setting'
        },
        '6003': {
            routePath: '/user/reqestPermission'
        },
        '7000': {
            routePath: '/client'
        },
        '7001': {
            routePath: '/client'
        },
        '7002': {
            routePath: '/client/dboard'
        },
        '7003': {
            routePath: '/client/schedule'
        },
        '8000': {
            routePath: '/system/log'
        },
        '8001': {
            routePath: '/system/log'
        },
        '8002': {
            routePath: '/system/statistics'
        },
        '8003': {
            routePath: '/system/monitor'
        },
        '8004': {
            routePath: '/system/notification'
        }
    }


    export default {
        name: 'header',
        data () {
            return {
                menu: [],
                userId: ''
            }
        },
        methods: {
            selectMenu (menu) {
                if (menu) {
                    this.$store.dispatch('menu/selectMenu', menu)
                }
            },
            // 메뉴 코드와 라우팅 url을 매핑하는 함수
            menuMap (menu) {
                return (function _menuMap (menu) {
                    const _menu = []

                    if (Array.isArray(menu)) {
                        menu.forEach(menu => {
                            if (menuMap[menu.menuCode]) {
                                menu.routePath = menuMap[menu.menuCode].routePath

                                if (Array.isArray(menu.subMenu)) {
                                    menu.subMenu = _menuMap.call(this, menu.subMenu)
                                }

                                _menu.push(menu)
                            } else {
                                this.$log.warn('메뉴 정보와 매핑되는 라우팅 정보가 없습니다. (menuCode : ' + menu.menuCode + ', menuMap : ' + JSON.stringify(menuMap, null, '\t') + ')')
                            }
                        })
                    } else {
                        throw new Error('전달된 메뉴 정보가 Array 타입이 아닙니다. (menu : ' + menu + ')')
                    }

                    return _menu
                }).call(this, menu)
            },
            getNotificationCount(){
              return 0
            },
            goNotification(){
              const SETTING_NOTIFICATION_PAGE_ROUTE_PATH = '/system/notification'

               //if (confirm('알림내역을 확인 하시겠습니까?')) {
                this.$log.debug('알림내역 페이지로 이동')

                this.$router.push({
                  path: SETTING_NOTIFICATION_PAGE_ROUTE_PATH,
                  query: {
                  }
                })
              //}
            },
            goMyInfo(){
              const SETTING_ALLLOW_ROLE_PAGE_ROUTE_PATH = '/user/setting'

              // if (confirm('로그 아웃 하시겠습니까?')) {
                this.$log.debug('계정 정보 페이지로 이동 : ', this.userId)

                this.$router.push({
                  path: SETTING_ALLLOW_ROLE_PAGE_ROUTE_PATH,
                  query: {
                    user: [{
                    userId: this.userId
                    }]
                  }
                })
              // }
            },
            goLogout(){
              const LOGIN_PATH_URL = '/pages/login.html'

              if (confirm('로그 아웃 하시겠습니까?')) {
                this.$log.debug('로그 아웃 => ', this.userId)

                location.href = LOGIN_PATH_URL
              }
            },
            isSelectedMenu(m)
            {
              //this.$log.debug('Header -> isSelectedMenu() : menu ->', m, this.$route.path, m.routePath)

                if(this.$route.path === m.routePath)
                {
                  //this.$log.debug('Header -> isSelectedMenu() : route path ->', this.$route.path, m.routePath)
                  return true
                }
                else if(m.subMenu)
                {
                  m.subMenu.forEach((_subMenu, _index) => {
                    //this.$log.debug('Header -> isSelectedMenu() : route path, subMenu path ->', _index, this.$route.path, _subMenu.routePath)

                    if(_subMenu.routePath === this.$route.path)
                    {
                      //this.$log.debug('Header -> isSelectedMenu() : route path, subMenu path matched ->', this.$route.path, _subMenu.routePath)

                      return true
                    }
                  })
                }

                return false
            }
        },
        created () {
            this.$log.debug('request get menu' ,this.$store.getters['menu/selectedMenu'])

            this.userId = sessionStorage.getItem('userId')

            request.menu.getMenu()
                .then(result => {
                    this.$log.debug('success get menu ', result)

                    //this.menu = this.menuMap(result.data.menu)
                    this.menu = this.menuMap(result.data)
                    this.selectMenu(this.menu[0])
                    this.$log.debug('request get menu!!' ,this.$store.getters['menu/selectedMenu'])
                })
                .catch(err => {
                    this.$log.error('failed get menu ', err)
                    alert('서버로부터 메뉴를 가져오는 것을 실패했습니다.')
                })
        }
    }
</script>
