<script>
    import request from '../../lib/request'
    import BOARD_TYPE from '../code/boardType'
    import CLIENT_STATUS from '../code/clientStatus'
    import Pagination from './Pagination.vue'

    const AUTO_UPDATE_INTERVAL = 15000
    const SETTING_RESQUEST_CLIENT_MANAGEMENT_PAGE_ROUTE_PATH = '/client'

    export default {
      components: {
        Pagination
      },
      data () {
          return {
            pagination: {
                pageNum: 1,
                pageCount: 10,
                totalCount: 1
            },
            autoUpdateInterval: null,
            clientInfo: {
            }
          }
      },
      created () {

        this.getDashboardInfo()
        // this.getLogs ()

        this.autoUpdateInterval = setInterval(
            () => {
                this.getDashboardInfo()
            },
            AUTO_UPDATE_INTERVAL)
      },
      destroyed () {
        clearInterval(this.autoUpdateInterval)
        this.autoUpdateInterval = null
      },
      methods: {
          getDashboardInfo () {

            request.dashboard.getDashboard()
                .then(result => {
                    let err

                    if (result.data.result === 200) {
                        this.$log.debug('대시보드 정보 조회 성공 ', result.data)
                        this.clientInfo = result.data.clientInfo
                    } else {
                        err = new Error(result.data.resultMsg)
                        err.resultData = result.data
                        throw err
                    }
                })
                .catch(err => {
                    this.$log.error('대시보드 정보 조회 실패 ', err)
                    alert('대시보드 정보 조회에 실패했습니다.\n' + err.message)
                })
          },
          // getLogs () {
          //
          //   request.log.getLogs()
          //       .then(result => {
          //           let err
          //
          //           if (result.data.result === 200) {
          //               this.$log.debug('대시보드 로그 정보 조회 성공 ', result.data)
          //               this.clientInfo.logInfo = result.data.list
          //           } else {
          //               err = new Error(result.data.resultMsg)
          //               err.resultData = result.data
          //               throw err
          //           }
          //       })
          //       .catch(err => {
          //           this.$log.error('대시보드 로그 정보 조회 실패 ', err)
          //           alert('대시보드 로그 정보 조회에 실패했습니다.\n' + err.message)
          //       })
          // },
          getStatusCardType(client)
          {
            if(client.status == CLIENT_STATUS.INACTIVE.value) // 비 활성화
              return "card-accent-secondary"
            else if(client.status == CLIENT_STATUS.ACTIVE.value) // 활성화
                return "card-accent-primary"
            else if(client.status == CLIENT_STATUS.PLAY.value) // 재생
                return "card-accent-success"
            else if(client.status == CLIENT_STATUS.STOP.value) // 중지
                return "card-accent-warning"
            else if(client.status == CLIENT_STATUS.ERROR.value) // 오류
                return "card-accent-danger"
          },
          getStatusTextType(client)
          {
            if(client.status == CLIENT_STATUS.INACTIVE.value) // 비 활성화
              return "text-secondary"
            else if(client.status == CLIENT_STATUS.ACTIVE.value) // 활성화
                return "text-primary"
            else if(client.status == CLIENT_STATUS.PLAY.value) // 재생
                return "text-success"
            else if(client.status == CLIENT_STATUS.STOP.value) // 중지
                return "text-warning"
            else if(client.status == CLIENT_STATUS.ERROR.value) // 오류
              return "text-danger"
          },
          getStatusDisplayName (client) {
              let status = Object.keys(CLIENT_STATUS).find(status => CLIENT_STATUS[status].value === client.status)

              return status ? CLIENT_STATUS[status].displayName : ''
          },
          getBoardStausTextType(board)
          {
            switch (board.activeYn) {
                case 'Y':
                    return "text-success"
                    break
                case 'N':
                    return "text-secondary"
                    break
            }
          },
          getBoardTypeName(board)
          {
            switch (board.boardType) {
                case BOARD_TYPE.ANNOUNCE:
                    return '공'
                    break
                case BOARD_TYPE.PROMOTION:
                    return '홍'
                    break
            }
          },
          goClientManagement()
          {
            this.$log.debug('클라이언트 관리 페이지로 이동')

            this.$router.push({
              path: SETTING_RESQUEST_CLIENT_MANAGEMENT_PAGE_ROUTE_PATH,
              query: {
              }
            })
          },
          searchLog()
          {

          }
        }
    }
