<template>
    <main class="main">
        <!-- Breadcrumb-->
        <ol class="breadcrumb">
            <li class="breadcrumb-item">권한 요청</li>
        </ol>
        <div class="container-fluid">
            <div class="card" style="width: 100%;">
              <div class="card-header">
                <i class="icon-note"></i><strong>권한 요청 정보</strong>
                <span class="badge list-count">총개수 {{requestedPermission.length}}</span>

                <div class="card-header-actions">
                  <button class="btn btn-primary" @click="refreshRequestPermission()"><i class="icon-refresh" style="padding-right: 3px"></i>갱신</button>
                </div>

              </div>

              <div class="card-body" style="min-height: 620px; max-height: 620px; overflow-x: auto; overflow-y: auto;">

                <div class="row" >
                <div class="col-sm-4 col-md-2" v-for="(user,index) in requestedPermission">
                  <div class="card card-accent-warning">
                      <!-- style="min-width: 200px"> -->
                    <div class="card-header">
                      <span class="badge rounded-pill badge-success float-right">{{index+1}}</span>
                      <strong>{{getRequestedPermission(user)}}</strong>
                    </div>
                    <div class="card-body py-1 ml-2">
                      <div class="row">
							<div class="justify-content-start align-self-center">
							  <a class="card-title text-primary">{{user.userName}}</a><br/>
							  <!-- 사번 : {{requestPermission.sabun}}<br/> -->
							  <small class="text-muted">
							    <!-- 사용자 암호 : xxxxxx<br/> -->
							    ID : {{user.userId}}<br/>
								소속 : {{user.organName}}<br/>
								업무 : {{user.duty}}<br/>
								신청일: {{user.createDate}}<br/>
							  </small>
							</div>
						</div>
						<div class="row">
						    <div class="col text-center">
							      <button class="btn btn-outline-warning btn-sm mx-2 my-2" @click="allowRole(user)">권한부여</button>
							      <button class="btn btn-outline-secondary btn-sm mx-2 my-2" @click="refuseRole(user)">요청거부</button>
							</div>
						</div>
                      </div>
                    </div>
                  </div>
                </div>
                </div>
            </div>
          </div>
        </div>
  </main>
</template>

<script>
    import request from '../../../lib/request'

    export default {
        components: {
        },
        data () {
            return {
                requestedPermission: [
                  // {
                  //   userId: "test",
                  //   userName: "test 사람",
                  //   department: "와우스",
                  //   roles: [
                  //     {
                  //       roleName: '공고문관리자',
                  //       role: '2'
                  //     },
                  //     {
                  //       roleName: '홍보물관리자',
                  //       role: '3'
                  //     },
                  //   ]
                  // },
                  // {
                  //   userId: "test",
                  //   userName: "test 사람",
                  //   department: "와우스",
                  //   roles: [
                  //     {
                  //       roleName: '공고문관리자',
                  //       role: '2'
                  //     },
                  //     {
                  //       roleName: '홍보물관리자',
                  //       role: '3'
                  //     },
                  //   ]
                  // }
                ]
            }
        },
        methods: {
            getRequirePermission () {
              request.user.getRequestPermission()
                  .then(result => {
                      let err

                      if (result.data.result === 200) {
                          this.$log.debug('권한 요청 목록 조회 성공 ', result.data)
                          this.requestedPermission = result.data.list
                      } else {
                          err = new Error(result.data.resultMsg)
                          err.resultData = result.data
                          throw err
                      }
                  })
                  .catch(err => {
                      this.$log.error('권한 요청 목록 조회 실패 ', err)
                      alert('권한 요청 목록 조회에 실패했습니다.\n' + err.message)
                  })
            },
            getRequestedPermission(user) {
                if(this.requestedPermission && this.requestedPermission.length > 0)
                {
                  if(user.roles != null)
                  {
                    return user.roles.map(role => {
                      if(role != null)
                        return role.roleName
                      else
                        return ''}).join(',')
                  }
                  else{
                    return ''
                  }
                }
            },
            allowRole(user)
            {
              if (confirm('권한 부여 하시겠습니까?')) {
                this.$log.debug('권한 부여-> ', user.reqId, user.userName)

              let requestUser
              requestUser = { reqId: user.reqId, isAccept: 'Y'}
              request.user.allowRequestedPermission(requestUser)
                  .then(result => {
                      let err
                      if (result.data.result === 200) {
                          this.$log.debug('권한 부여 성공 ', result.data)
                          this.getRequirePermission()
                          alert('권한 요청이 수락되었습니다.')
                      } else {
                          err = new Error(result.data.resultMsg)
                          err.resultData = result.data
                          throw err
                      }
                  })
                  .catch(err => {
                      this.$log.error('권한 부여 실패 ', err)
                      alert('권한 부여에 실패했습니다.\n' + err.message)
                  })
                }
            },
            refuseRole(user)
            {
              if (confirm('권한 부여 요청을 거부 하시겠습니까?')) {

              this.$log.debug('권한 부여 요청 거부 -> ', user.reqId, user.userName)

              let requestUser
              requestUser = { reqId: user.reqId, isAccept: 'D'}
              request.user.refuseRequestedPermission(requestUser)
                  .then(result => {
                      let err
                      if (result.data.result === 200) {
                          this.$log.debug('권한 부여 요청 거부 성공 ', result.data)
                          this.getRequirePermission()
                          alert('권한 요청이 거부되었습니다.')
                      } else {
                          err = new Error(result.data.resultMsg)
                          err.resultData = result.data
                          throw err
                      }
                  })
                  .catch(err => {
                      this.$log.error('권한 부여 요청 거부 실패 ', err)
                      alert('권한 부여 요청 거부에 실패했습니다.\n' + err.message)
                  })
                }
            },
            refreshRequestPermission()
            {
              this.getRequirePermission()
            }
        },
        created () {
            this.getRequirePermission()
        }
    }
</script>

<style>
    div.chart-btn {
        margin-top: 5%;
        text-align: center;
    }

    span.list-count {
        padding-left: 18.2;
        margin-left: 15px;
        font-size: inherit;
    }

    div.card-request-permission {
        text-align: right;
    }
</style>
