                                                </div>
                                                <div class="collapse show" id="depth3 + index">
                                                  <div class="card-body">
                                                    <div data-spy="scroll" data-target="#accordionOrgan2" data-offset="0" class="scrollspy-example">
                                                        <ul class="list-group" v-for="organ3 in organ2.children">
                                                          <!-- Depth3 -->
                                                            <li  v-if="selectedListItem == organ3" class="list-group-item d-flex list-group-item-action justify-content-between align-items-center active border-warning" href="javascript:void(0);" @click="getUsers(organ3,0)">
                                                              {{organ3.name}}
                                                              <!-- ({{organ3.organId}}) -->
                                                              <span class="badge badge-warning badge-pill">{{organ3.count}}</span>
                                                            </li>
                                                            <li  v-else class="list-group-item d-flex list-group-item-action justify-content-between align-items-center" href="javascript:void(0);" @click="getUsers(organ3,0)">
                                                              {{organ3.name}}
                                                              <!-- ({{organ3.organId}}) -->
                                                              <span class="badge badge-info badge-pill">{{organ3.count}}</span>
                                                            </li>
