                        </div>
                    </div>
                </div>
                <div class="col">
                    <div class="row">
                        <div class="card" style="width: 100%;">
                            <div class="card-header">
                                <i class="icon-note"></i><strong>조직원 정보</strong>
                            </div>
                            <div class="card-body">
                                <div class="form row mb-3">
                                    <div class="col">
                                        <dropdown
                                            ref="searchTypeDropdown"
                                            :items="searchTypes"
                                            @selectedItem="function (searchType) {searchCondition.sType = searchType.value}"
                                            >
                                        </dropdown>
                                    </div>
                                    <div class="col">
                                        <input type="text" class="form-control" placeholder="검색어를 입력하세요" v-model="searchCondition.sTitle" @keyup.enter="searchUser"/>
                                    </div>
                                    <div class="col">
                                        <button class="btn btn-primary" style="width: 100px;margin-left: 10px;" @click="searchUser">
                                            <i class="fa fa-search"></i>
                                            검색
                                        </button>
                                    </div>
                                </div>
                                <div class="row mb-3 card">
                                    <div class="card-header">
                                        목록
                                        <span class="badge list-count">총개수 {{users.length}}</span>
                                    </div>
                                    <div class="card-body" style="max-height: 650px; overflow-y: auto;">
                                        <table class="table table-striped">
                                            <thead>
                                                <tr>
                                                    <!--<th><input type="checkbox"/></th>-->
                                                    <th>
