                        </div>
    </main>
</template>

<script>
import request from '../../../lib/request'
import TreeList from '../TreeList.vue'
import Dropdown from '../Dropdown.vue'

const SETTING_ALLLOW_ROLE_PAGE_ROUTE_PATH = '/user/setting'


export default {
    components: {
        TreeList,
        Dropdown
    },
    data () {
        return {
            selectedListItem: null,
            searchTypes: [
                {
                    displayName: '이름',
                    value: '0'
                },
                {
                    displayName: 'ID',
                    value: '1'
                }
            ],
            searchCondition: {
                sType: '',
                sTitle: ''
            },
            treeData: {
                name: '조직도',
                children: [
                ]
            },
            users: [],
            searchUsers: []
        }
    },
    // props: {
    //     searchCondition: {
    //         type: Object,
    //         default: null,
    //         required: false
    //     }
    // },
    methods: {
        getOrgan () {
            request.organ.getOrgan()
                .then(result => {
                    if (result.data.result === 200) {
                        //this.treeData.children = result.data.list
                        // 1 depth
                        this.treeData.children = result.data.list.filter(organ => organ.depth === 1)
                            .map(organ => {

                                //this.$log.debug('organ paring depth=1 : ', organ.organId)

                                // 2 depth
                                organ.children = result.data.list.filter(_organ => {
                                    return _organ.parentId === organ.organId
                                })
                                    .map(_organ => {

                                        //this.$log.debug('organ paring depth=2 : ', _organ.organId)

                                        // 3 depth
                                        _organ.children = result.data.list.filter(__organ => {
                                            //this.$log.debug('organ paring filter depth=3 : ', __organ.organId, __organ.parentId, _organ.organId)
                                            return __organ.parentId === _organ.organId
                                        }).map(__organ => {

                                            //this.$log.debug('organ paring depth=3 : ', __organ.organId)

                                            return __organ
                                        })

                                        //this.$log.debug('organ paring depth=3 len : ', _organ.children.length)
                                        return _organ
                                    })
                                //this.$log.debug('organ paring depth=2 len : ', organ.children.length)
                                return organ
                            })

                        this.$log.debug('organ paring depth=1 len : ', this.treeData.children.length)

                    } else {
                        throw new Error(result.data.resultMsg)
                    }
                })
                .catch(err => {
                    this.$log.error(err)
                    alert('조직도 연동을 실패했습니다.\n(' + err.message + ')')
                })

            this.$log.debug('treeData : ', this.treeData.children.length)
            this.treeData.children.forEach((organ, index) => {
                this.$log.debug('organ ', organ)
            })
        },
        getUsers (organ, isFolder) {
            if (!isFolder) {
                this.$log.debug('getUsers ', organ)
                request.organ.getUsers(organ.organId)
                    .then(result => {
                        this.$log.debug('get users', result)
                        if (result.data.result === 200) {
                            this.users = result.data.list
                            this.searchUsers = result.data.list
                        } else {
                            throw new Error(result.data.resultMsg)
                        }
                    })
                    .catch(err => {
                        alert('조직별 사원리스트 조회를 실패했습니다\n(' + err.message + ')')
                    })

                this.selectedListItem = organ
            }
        },
        getDisplayRolesIcon(user)
        {
           //this.$log.debug('권한 체크 : ', user.userId)

            let role;
            if(user.roles != null && user.roles[0] && user.roles.length > 0)
            {
                role = user.roles.map(role => role.roleName).join(',')
                if(role != '')
                {
                    return "fa fa-key"
                }
            }
            return ""
        },
        getDisplayRoles (user) {
            if(user.roles != null && user.roles[0] && user.roles.length > 0)
            {
                return user.roles.map(role => role.roleName).join(',')
            }
            else{
                return ''
            }
        },
        goAllowRole(user){
            //if (confirm('권한 부여 메뉴로 이동하시겠습니까?')) {
            this.$log.debug('권한 부여 페이지로 이동', user, this.$router.name)

            this.$router.push({
                path: SETTING_ALLLOW_ROLE_PAGE_ROUTE_PATH,
                query: {
                    userId: user.userId,
                    prevPage: this.$route.name
                }
            })
            //}
        },
        searchUser()
        {
            // this.$log.debug('getUsers ', organ)
            // request.organ.getUsers(organ.organId)
            //     .then(result => {
            //         this.$log.debug('get users', result)
            //         if (result.data.result === 200) {
            //             this.users = result.data.list
            //         } else {
            //             throw new Error(result.data.resultMsg)
            //         }
            //     })
            //     .catch(err => {
            //         alert('조직별 사원리스트 조회를 실패했습니다\n(' + err.message + ')')
            //     })

            // 리스트내 검색
            this.searchUsers = []

            this.$log.debug('searchType ', this.searchCondition.sType)
            this.$log.debug('searchTitle ', this.searchCondition.sTitle)

            if(this.searchCondition.sTitle === '')
            {
                this.users.forEach((user, index) => {
                    this.searchUsers.push(user)
                })
            }
            else
            {
                this.users.forEach((user, index) => {

                    if(this.searchCondition.sType == '0')
                    {
                        this.$log.debug('user name ', user.userName)
                        if(user.userName.indexOf(this.searchCondition.sTitle) != -1)
                        {
                            this.searchUsers.push(user)
                        }
                    }
                    else {

                        this.$log.debug('user id ', user.userId)
                        if(user.userId.indexOf(this.searchCondition.sTitle) != -1)
                        {
                            this.searchUsers.push(user)
                        }
                    }
                })
            }

        }
    },
    created () {
        this.getOrgan()
    }
}
</script>

