<template>
    <div class="sidebar">
        <nav class="sidebar-nav nav-pills">
            <ul class="nav nav-pills bg-dark" style="min-height: 65%;">
                <li v-if="subMenu !== null" v-for="menu in subMenu" class="nav-item">
                    <!--<router-link class="nav-link" :to="menu.routePath" @click.native="selectMenu(menu)" :class="{'active': selectedSubMenu.menuCode === menu.menuCode}">-->
                    <!--<router-link class="nav-link" :to="menu.routePath" @click.native="selectMenu(menu)" :class="{'active': selectedSubMenu.menuId === menu.menuId}">-->
                    <router-link class="nav-link" :to="menu.routePath" @click.native="selectMenu(menu)" :class="{'active': $store.getters['menu/selectedSubMenu'].menuId === menu.menuId}">
                        <i class="nav-icon icon-note"></i>
                        {{menu.menuName}}
                    </router-link>
                </li>
            </ul>
            <div class="tab-pane active " id="timeline" role="tabpanel">
                <div class="tab-pane p-3" id="settings" role="tabpanel">
                    <h6><i class="nav-icon icon-graph"></i>서버 상태 정보</h6>
                    <div class="text-uppercase mb-1 mt-4">
                        <small>
                            <b>CPU 사용</b>
                        </small>
                    </div>
                    <div class="progress progress-xs">
                        <div class="progress-bar bg-info" role="progressbar" :style="'width: ' + systemResources.cpu.usage + '%'" aria-valuenow="systemResources.cpu.usage" aria-valuemin="0" aria-valuemax="100"></div>
                    </div>
                    <small class="text-muted">{{systemResources.cpu.usage}}%</small>
                    <div class="text-uppercase mb-1 mt-2">
                        <small>
                            <b>메모리 사용</b>
                        </small>
                    </div>
                    <div class="progress progress-xs">
                        <div class="progress-bar bg-warning" role="progressbar" :style="'width: ' + systemResources.memory.usage + '%'" aria-valuenow="systemResources.memory.usage" aria-valuemin="0" aria-valuemax="100"></div>
                    </div>
                    <small class="text-muted">{{systemResources.memory.usage}}%, {{systemResources.memory.used}}/{{systemResources.memory.total}} GB</small>
                    <!-- <div class="text-uppercase mb-1 mt-2">
                        <small>
                            <b>디스크 C 사용</b>
                        </small>
                    </div>
                    <div class="progress progress-xs">
                        <div class="progress-bar bg-danger" role="progressbar" style="width: 95%" aria-valuenow="95" aria-valuemin="0" aria-valuemax="100"></div>
                    </div>
                    <small class="text-muted">243GB/256GB</small> -->
                    <div class="text-uppercase mb-1 mt-2">
                        <small>
                            <b>디스크 D 사용</b>
                        </small>
                    </div>
                    <div class="progress progress-xs">
                        <div class="progress-bar bg-success" role="progressbar" :style="'width: ' + systemResources.disk.usage + '%'" aria-valuenow="systemResources.disk.usage" aria-valuemin="0" aria-valuemax="100"></div>
                    </div>
                    <small class="text-muted">{{systemResources.disk.usage}}%, {{systemResources.disk.used}}/{{systemResources.disk.total}} GB</small>
                </div>
            </div>
        </nav>
        <button class="sidebar-minimizer brand-minimizer" type="button"></button>
    </div>
</template>

<script>
    import { createNamespacedHelpers } from 'vuex'

    import request from '../../lib/request'

    const { mapState } = createNamespacedHelpers('menu')

    const AUTO_UPDATE_SYSTEM_RESOURCES_INTERVAL = 30000

    export default {
        data () {
            return {
              systemResources: {
                os: {
                  name: 'windows'
                },
                cpu: {
                  name: 'CPU',
                  cores: '4',
                  usage: '0'
                },
                memory: {
                  total: '0',
                  usage: '0',
                  used: '0'
                },
                disk: {
                  total: '0',
                  usage: '0',
                  used: '0'
                },
                nic: {
                  name: 'Ethernet',
                  speed: '1000'
                }
              }
            }
        },
        methods: {
            selectMenu (menu) {
                if (menu) {
                    //this.$log.debug('select menu ', menu, location.pathname, this.$store.getters['menu/selectedSubMenu'])

                    // 수정 > 등록 메뉴로 갈 때 reload
                    switch (true) {
                        case location.pathname === '/promotion/add' && this.selectedSubMenu.routePath === '/promotion':
                        case location.pathname === '/noti/add' && this.selectedSubMenu.routePath === '/noti':
                            this.$store.dispatch('menu/reload')
                            break
                    }

                    this.selectedSubMenu = menu
                    this.$store.dispatch('menu/selectSubMenu', menu)
                }
            },
            getSystemResources()
            {
              this.$log.debug('getSystemResources -> ')

              request.systemresource.getSystemResources()
                  .then(result => {
                      let err

                      if (result.status === 200) {
                          this.$log.debug('서버 시스템 정보 조회 성공 ', result.data)

                          this.systemResources = result.data

                      } else {
                          err = new Error(result.data.resultMsg)
                          err.resultData = result.data
                          throw err
                      }
                  })
                  .catch(err => {
                      this.$log.error('서버 시스템 정보 조회 실패 ', err)
                      alert('서버 시스템 정보 조회에 실패했습니다.\n' + err.message)
                  })
            }
        },
        computed: {
            ...mapState({
//                selectedSubMenu (state) {
//                    return state.selectedSubMenu
//                },
                title (state) {
                    return state.menu && state.menu.displayName ? state.menu.displayName : ''
                },
                subMenu (state) {
                    let subMenu = null

                    try {
                        if (Array.isArray(state.menu.subMenu)) {
                            subMenu = state.menu.subMenu

                            this.selectMenu(subMenu[0])
                        } else {
                            this.$log.warn('not Array state.menu.subMenu (state.menu.subMenu : ' + state.menu.subMenu + ')')
                        }
                    } catch (err) {
                        this.$log.warn('not exist state.menu.subMenu')
                    } finally {
                        return subMenu
                    }
                }
            })
        },
        created()
        {

          this.getSystemResources()

          setInterval(
              () => {
                this.getSystemResources()
            },
            AUTO_UPDATE_SYSTEM_RESOURCES_INTERVAL)
        }
    }
</script>

<style>
    a.nav-link {
        cursor: pointer;
    }
</style>
