<template>
    <li>
        <div
            :class="{bold: isFolder}"
            @click="toggle">
            <span v-if="isArrow && isFolder" class="pic" :class="'arrow-' + (open ? 'down' : 'right')"></span>
            <input v-if="isCheckbox" type="checkbox" @change="checkModel" v-model="model.checked"/>
            {{getDisplayName(model)}}
        </div>
        <ul v-show="open" v-if="isFolder">
            <tree-list
                class="item"
                v-for="model in model.children"
                :is-checkbox="isCheckbox"
                :is-arrow="isArrow"
                @selectedItem="selected"
                @checkItem="checkBind"
                :make-display-name="makeDisplayName"
                :model="model">
            </tree-list>
        </ul>
    </li>
</template>

<script>
    import Vue from 'vue'

    export default {
        name: 'tree-list',
        props: {
            model: Object,
            makeDisplayName: {
                type: Function,
                require: false,
                default: null
            },
            isCheckbox: {
                type: Boolean,
                require: false,
                default: false
            },
            isArrow: {
                type: Boolean,
                require: false,
                default: false
            }
        },
        data: function () {
            return {
                open: false
            }
        },
        computed: {
            isFolder: function () {
                return this.model.children &&
                    this.model.children.length
            }
        },
        methods: {
            toggle () {
                if (this.isFolder) {
                    this.open = !this.open
                }

                this.$emit('selectedItem', this.model, this.isFolder)
            },
            selected (location) {
                this.$emit('selectedItem', location)
            },
            getDisplayName (model) {
                let displayName = model.name

                if (typeof this.makeDisplayName === 'function') {
                    displayName = this.makeDisplayName(model)
                }

                return displayName
            },
            checkModel () {
                this.$emit('checkItem', this.model, this.model.checked)
            },
            checkBind (location, checked) {
                this.$emit('checkItem', location, checked)
            }
        },
        created () {
            //this.$log.debug('isArrow ', this.isArrow)
        }
    }
</script>

<style>
    .item {
        cursor: pointer;
    }
    .bold {
        font-weight: bold;
    }
    ul {
        padding-left: 1em;
        line-height: 1.5em;
        list-style-type: none;
    }

    .pic {
        display: inline-block;
        vertical-align: middle;
        width: 0; 
        height: 0; 
    }

    .arrow-right {
        border-top: 5px solid transparent;
        border-bottom: 5px solid transparent;
        border-left: 5px solid #737373;
    }

    .arrow-down {
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-top: 5px solid #737373;
    }
</style>
