<template>
  <main class="main">
    <!-- Breadcrumb-->
    <ol class="breadcrumb">
      <li class="breadcrumb-item">지점관리</li>
</ol>
<div class="container-fluid">
  <div class="row mb-3">

    <!-- 지점목록 -->
    <div class="col-3">
      <div class="card">
        <div class="card-header">
          <i class="icon-note"></i><strong>지점목록</strong>
          <div class="card-header-actions">
          </div>
        </div>

        <div class="card-body" style="min-height: 620px; max-height: 620px; ">
        <!-- " overflow-y: auto; border: 1px solid lightblue;"> -->
          <div class="cd-example" >
            <div class="accordion" id="accordionOrgan">
              <div class="card" v-for="location in treeData.children">
                <div v-if="location.depth == 1" class="card-header" id="headingOne">
                  <h6 class="mb-0">
                    <button class="btn btn-link collapsed" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                      노원 구청
                    </button>
                  </h6>
                </div>
                <div id="collapseOne" class="collapse show" aria-labelledby="headingOne" data-parent="#accordionOrgan" style="min-height: 530px; max-height: 530px; overflow-y: auto;">
                  <div class="card-body">
                    <div data-spy="scroll" data-target="#accordionOrgan" data-offset="0" class="scrollspy-example">
                      <ul class="list-group">
                        <li  v-if="selectedListItem == location" class="list-group-item d-flex list-group-item-action justify-content-between align-items-center active border-warning" href="javascript:void(0);" @click="selectLocation(location)">
                          <div class="col-sm-10 col-md-10 px-1"> {{location.name}}
                          <span class="badge badge-warning badge-pill mx-1">{{location.organId}}</span>
                          </div>
                          <div class="col-sm-2 col-md-2">
                           <span class="badge badge-pill" :class="location.count > 0 ? 'badge-warning' : 'badge-warning'">{{location.count}}</span>
                          </div>
                        </li>
                        <li  v-else class="list-group-item d-flex list-group-item-action justify-content-between align-items-center" href="javascript:void(0);" @click="selectLocation(location)">
                          <div class="col-sm-10 col-md-10 px-1"> {{location.name}}
                          <span class="badge badge-dark badge-pill mx-1">{{location.organId}}</span>
                          </div>
                          <div class="col-sm-2 col-md-2">
                          <span class="badge badge-pill" :class="location.count > 0 ? 'badge-warning' : 'badge-dark'">{{location.count}}</span>
                          </div>
                        </li>
                      </ul>

                      <ul class="list-group" v-for="location2 in location.children">
                        <li v-if="selectedListItem == location2" class="list-group-item d-flex list-group-item-action justify-content-between align-items-center active border-warning" href="javascript:void(0);" @click="selectLocation(location2)" style="padding-left: 10px">
                          <div class="col-sm-10 col-md-10 px-1">{{location2.name}}
                          <span class="badge badge-warning badge-pill mx-1">{{location2.organId}}</span>
                          </div>
                          <div class="col-sm-2 col-md-2">
                          <span class="badge badge-pill" :class="location2.count > 0 ? 'badge-warning' : 'badge-warning'">{{location2.count}}</span>
                          </div>
                        </li>

                        <li v-else class="list-group-item d-flex list-group-item-action justify-content-between align-items-center" href="javascript:void(0);" @click="selectLocation(location2)" style="padding-left: 10px">
                          <div class="col-sm-10 col-md-10 px-1">{{location2.name}}
                          <span class="badge badge-dark badge-pill mx-1">{{location2.organId}}</span>
                          </div>
                          <div class="col-sm-2 col-md-2">
                          <span class="badge badge-pill" :class="location2.count > 0 ? 'badge-warning' : 'badge-dark'">{{location2.count}}</span>
                          </div>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 지점에 설치된 클라이언트 목록 -->
    <div class="col">
      <div class="row">
        <div class="card" style="width: 100%;">
          <div class="card-header">
            <i class="icon-note"></i><strong>지점에 설치된 클라이언트 목록</strong>
          </div>
          <div class="card-body" style="min-height: 620px; max-height: 620px; overflow-x: auto; overflow-y: auto;">
            <div class="row" id="clients-grop">
              <div data-spy="scroll" data-target="#clients-grop" data-offset="0" class="col">
                <div class="card-group">

                  <div class="col-sm-4 col-md-3" v-for="client in clientList">
                    <div class="card card-accent-warning">

                      <div class="card-header bg-dark">
                        {{client.clientName}}
                        <div class="card-header-actions">
                          <a class="card-header-action btn-minimize" href="javascript:void(0);" @click="delClient(client)">
                            <i class="icon-close"></i>
                          </div>
                        </div>

                        <div class="card-body row">
                          <div class="col">
                          <p class="card-title">{{getBoardComposition(client)}}</p>
                          <p class="card-text">
                            <small class="text-muted">
                              클라이언트ID : {{client.clientId}}<br/>
                              모니터수 : {{client.boards.length}}개<br/>
                              모니터형태 : {{getLayoutTypeName(client)}}<br/>
                              모니터별 해상도 : {{getResolution(client)}}<br/>
                              전체해상도 : {{getTotalResolution(client)}}<br/>
                              IP : {{client.clientIp}}<br/>
                              동작여부 : {{getStatusDisplayName(client)}}<br/>
                            </small>
                            <small v-if="client.status === 1" class="text-success">
                              {{getStatusDisplayName(client)}}<br/>
                            </small>
                            <small v-else class="text-muted">
                              {{getStatusDisplayName(client)}}<br/>
                            </small>
                          </p>

                          <div class="row justify-content-end">
                            <button class="btn btn-outline-success btn-sm mx-2" @click="settingBoard(client)">게시판설정</button>
                          </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</div>
</main>
</template>

<script>
import TreeList from '../TreeList.vue'
import request from '../../../lib/request'

import BOARD_TYPE from '../../code/boardType'
import CLIENT_STATUS from '../../code/clientStatus'

const SETTING_BOARD_PAGE_ROUTE_PATH = '/client/dboard'

export default {
  components: {
    TreeList
  },
  created () {
    this.getLocation()
  },
  data () {
    return {
      selectedListItem: null,
      treeData: {
        name: '지점목록',
        children: [
        ]
      },
      clientList: [
      ]
    }
  },
  methods: {
    delClient(client)
    {
      if (confirm('클라이언트를 삭제하시겠습니까?')) {
        // alert('클라이언트 삭제', client.clientId)
        this.$log.debug('delete Client ', client.clientId)

        request.client.deleteClient(client.clientId)
        .then(result => {
          this.$log.debug('delete Client result => ', result)

          if (result.data.result === 200) {
            this.$log.debug('delete Client result : 성공 ')
            alert('클라이언트가 삭제되었습니다')
          } else {
            throw new Error(result.data.resultMsg)
          }
        })
        .catch(err => {
          this.$log.error('delete Client error ', err)
          alert('클라이언트 삭제에 실패했습니다\n(' + err.message + ')')
        })

        // 바로 조회하면 삭제안된 기존 리스트로 응답을 받음
        // 타이머로 500ms 후에 조회하면 정상적으로 삭제된 리스트 받음
          setTimeout(
              () => {
          this.selectLocation(this.selectedListItem)
        },
        500)
      }
      else {
        this.$log.debug('클라이언트 삭제 안함 : [' + client.clinetName + ',(' + client.clientId + ')]')
      }
    },
    settingBoard (client) {
      // if (confirm('게시판 설정 메뉴로 이동하시겠습니까?')) {
        this.$log.debug('게시판 설정 페이지로 이동', client)

        this.$router.push({
          path: SETTING_BOARD_PAGE_ROUTE_PATH,
          query: {
            clientId: client.clientId
          }
        })
      // } else {
      //   this.$log.debug('공고문 수정 안함')
      // }
    },
    getStatusDisplayName (client) {
      let status = Object.keys(CLIENT_STATUS).find(status => CLIENT_STATUS[status].value === client.status)

      return status ? CLIENT_STATUS[status].displayName : ''
    },
    getLayoutTypeName (client) {
      return client.boards.length > 0 ? (client.boards[0].layoutType == 0 ? '가로형' : '세로형') : '설정된 모니터없음'
    },
    getResolution (client) {
      return client.boards.length > 0 ? (client.boards[0].width + 'x' + client.boards[0].height) : '설정된 모니터없음'
    },
    getTotalResolution (client) {
      let result, message

      if (client.boards.length > 0) {
        result = client.boards.reduce(
          (result, board) => {
            result.width += board.width

            return result
          },
          {
            width: 0,
            height: client.boards[0].height
          })

          message = result.width + 'x' + result.height
        } else {
          message = '설정된 모니터없음'
        }

        return message
      },
      getBoardComposition (client) {
        let result = client.boards.reduce(
          (result, board) => {
            switch (board.boardType) {
              case BOARD_TYPE.ANNOUNCE:
              result.announce++;
              break
              case BOARD_TYPE.PROMOTION:
              result.promotion++;
              break
            }

            return result
          },
          {
            announce: 0,
            promotion: 0
          })

          return '공고문(' + result.announce + '개), 홍보용 (' + result.promotion + '개)'
        },
        getLocation () {
          request.organ.getLocation()
          .then(result => {
            this.$log.debug('get locations ', result)

            if (result.data.result === 200) {
              //this.treeData.children = result.data.list
              // 1 depth
              this.treeData.children =result.data.list.filter(location => location.depth === 1)
              .map(location => {
                // 2 depth
                location.children = result.data.list
                .filter(_location => {
                  return _location.parentId === location.organId
                })
                .map(_location => {
                  // 3 depth
                  _location.children = result.data.list
                  .filter(__location => {
                    return __location.parantId === _location.organId
                  })

                  return _location
                })
                return location
              })
            } else {
              alert('지점 조회를 실패했습니다. (' + result.data.resultMsg + ')')
              // fail
            }
          })
          .catch(err => {
            alert('지점 조회를 실패했습니다. (' + err.message + ')')
          })
        },
        selectLocation (location) {
          this.$log.debug('select location ', location)

          request.client.getOrganClients(location.organId)
          .then(result => {
            this.$log.debug('get organ clients ', result)

            if (result.data.result === 200) {
              this.clientList = result.data.list
            } else {
              throw new Error(result.data.resultMsg)
            }
          })
          .catch(err => {
            this.$log.error('get organ clients error ', err)
            alert('클라이언트 목록을 가져오는 것을 실패했습니다\n(' + err.message + ')')
          })

          this.selectedListItem = location
        },
        makeLocationDisplayName (location) {
          return location.organId ? location.name + '(' + location.organId + ')' : location.name
        }
      }
    }
    </script>

    <style>
    div.chart-btn {
      margin-top: 5%;
      text-align: center;
    }

    span.list-count {
      padding-left: 18.2;
      margin-left: 15px;
      font-size: inherit;
    }

    div.card-client-setting {
      text-align: right;
    }
    </style>
