<template>
    <div class="card">
        <div class="card-header">
            <!--<i class="fa fa-align-justify"></i>-->
            컨텐츠 목록
            <span class="badge list-count">총개수 {{pagination.totalCount}}</span>
            <div v-if="displayContentsTypeFilter" style="display: inline;padding-left: 5%;">
                <span v-for="contentType in contentTypes" style="padding-left: 3rem;">
                    <input type="checkbox" style="margin-right: 5px;"/>
                    <i :class="getContentTypeIcon(contentType)" style="padding-left: 3px"></i>
                    {{contentType.displayName}}
                </span>
            </div>
        </div>
        <div class="card-body">
            <table class="table table-striped">
                <thead>
                    <tr>
                        <th><input type="checkbox" v-model="allChecked"/></th>
                        <th>
                            컨텐츠명
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.title) + '-circle' + ' sort-icon'" @click="changeSorting('title')" :style="sorting.reqSortingCol === 'title' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th>
                            게시시작일
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.postingStart) + '-circle' + ' sort-icon'" @click="changeSorting('postingStart')" :style="sorting.reqSortingCol === 'postingStart' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th>
                            게시종료일
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.postingEnd) + '-circle' + ' sort-icon'" @click="changeSorting('postingEnd')" :style="sorting.reqSortingCol === 'postingEnd' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th>
                            게시위치
                            <!-- <span>
                                <i class="icon-arrow-down sort-icon"></i>
                            </span> -->
                        </th>
                        <th>
                            타입
                            <!-- <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.contentType) + 'sort-icon'" @click="changeSorting('contentType')"></i>
                            </span> -->
                        </th>
                        <th>
                            게시자
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.publisher) + '-circle' + 'sort-icon'" @click="changeSorting('publisher')" :style="sorting.reqSortingCol === 'publisher' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                        <th>
                            태그
                            <!-- <span>
                                <i class="icon-arrow-down sort-icon"></i>
                            </span> -->
                        </th>
                        <th colspan="2">
                            원본파일명
                            <span>
                                <i :class="'icon-arrow-' + getSortingClass(sorting.originFile) + '-circle' + ' sort-icon'" @click="changeSorting('originFile')" :style="sorting.reqSortingCol === 'originFile' ? 'margin-left: 5px;color: orange;' : 'margin-left: 5px;'"></i>
                            </span>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="content in contentList">
                        <td><input type="checkbox" v-model="content.checked"/></td>
                        <td>
                        <a class="btn btn-ghost-normal mr-1 text-primary" :title="'생성일: ' + content.createDate" data-toggle="modal" data-target="#preview-modal"  @click="openPreviewModal(content)">
                          <i :class="getContentTypeIcon(content)" style="padding-right: 3px;color : primary"></i>
                          <strong>{{content.title}}</strong>
                        </td>
                        <td>
                          <i class="fa fa-calendar-check-o" style="padding-right: 3px"></i>
                          {{dateToFormat(content.postingStart)}}
                        </td>
                        <td>
                          <i class="fa fa-calendar-times-o" style="padding-right: 3px"></i>
                          {{dateToFormat(content.postingEnd)}}
                        </td>
                        <td>
                          <!-- <i class="fa fa-sitemap" style="padding-right: 3px" data-toggle="tooltip" title="" data-original-title={{locationDisplay}}></i> -->
                          <!-- {{content.location && content.location[0] ? content.location[0].name : ''}} -->

                          <div class="dropdown">
                          <button class="btn btn-ghost-primary dropdown-toggle col-offset-6" id="alllocations" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">{{content.location && content.location[0] ? content.location[0].name : ''}}</button>
                          <div class="dropdown-menu" x-placement="top-start" style="position: absolute; will-change: transform; top: 0px; left: 0px; transform: translate3d(0px, -356px, 0px);">
                          <!-- <a class="dropdown-item">게시위치</a> -->
                          <!-- <form class="px-4 py-3"> -->
                            <textarea class="form-control" id="textarea-input" name="textarea-input" rows="4" placeholder="게시위치" readonly>{{getLocationDisplay(content)}}</textarea>
                          <!-- </form> -->
                          </div>
                          </div>
                        </td>
                        <td>{{getDisplayContentTypeName(content)}}</td>
                        <td>{{content.publisher}}</td>
                        <td>
                          <i class="fa fa-hashtag" style="padding-right: 3px"></i>
                          {{content.tags}}
                        </td>
                        <td style="color: orange;">{{content.originFile}}</td>
                        <td>
                            <a class="btn btn-success mr-1" data-toggle="modal" data-target="#preview-modal" @click="openPreviewModal(content)">
                                <i class="fa fa-search-plus"></i>
                            </a>
                            <a class="btn btn-info mr-1" @click="updateContent(content)">
                                <i class="fa fa-edit"></i>
                            </a>
                            <a class="btn btn-danger mr-1" @click="delOneContent(content)">
                                <i class="fa fa-trash-o"></i>
                            </a>
                        </td>
                    </tr>
                </tbody>
            </table>
            <pagination ref="pagination" :pagination="pagination" @selectPage="searchContent"></pagination>
            <div>
                <button class="btn btn-danger btn-delete" style="margin-right: 1rem; width: 77px;" @click="removeContents">삭제</button>
            </div>
        </div>
    </div>
</template>

<script>

    import request from '../../../lib/request'
    import utils from '../../../lib/utils'

    import Pagination from '../Pagination.vue'

    import BOARD_TYPE from '../../code/boardType'
    import CONTENT_TYPE from '../../code/contentType'
    import CONTENT_TYPE_ALL from '../../code/contentTypeAll'
    import SORTING_TYPE from '../../code/sortingType'

    const ADD_NOTI_PAGE_ROUTE_NAME = 'notiAdd',
        UPDATE_NOTI_PAGE_ROUTE_PATH = '/noti/update'

    const ADD_PROMOTION_PAGE_ROUTE_NAME = 'promotionAdd',
        UPDATE_PROMOTION_PAGE_ROUTE_PATH = '/promotion/update/'

    const ADD_content_PAGE_ROUTE_NAME = 'contentAdd',
        UPDATE_content_PAGE_ROUTE_PATH = '/content/update/'



    export default {
        components: {
            Pagination
        },
        data () {
            return {
                allChecked: false,
                pagination: {
                    pageNum: 1,
                    pageCount: 10,
                    totalCount: 1
                },
                contentList: [],
                contentTypes: CONTENT_TYPE_ALL,
                removeCondition: {
                  contentId: 1,
                  contentType: '001'
                },
                sorting: {
                    title: SORTING_TYPE.DESC,
                    postingStart: SORTING_TYPE.DESC,
                    postingEnd: SORTING_TYPE.DESC,
                    priority: SORTING_TYPE.DESC,
                    publisher: SORTING_TYPE.DESC,
                    originFile: SORTING_TYPE.DESC,
                    reqSortingCol: '',
                    reqSortingType: SORTING_TYPE.DESC
                },
            }
        },
        watch: {
            allChecked (val) {
                this.contentList.forEach(content => {
                    content.checked = val
                })
            }
        },
        props: {
            displayContentsTypeFilter: {
                type: Boolean,
                default: false,
                required: false
            },
            searchCondition: {
                type: Object,
                default: null,
                required: false
            }
        },
        created () {
            this.searchContent(1)
        },
        methods: {
          getLocationDisplay(content)
          {
            // alert('getLocationDisplay')

            return content.location.map(location => location.name).join(',')
          },
          delOneContent (content) {
              let deleteContentList = Array.from(
                  {
                      length: 1
                  },
                  (k, v) => {
                          return {
                          contentId: 1,
                          contentType: '001'
                      }
                  })

              if (confirm('해당 컨텐츠를 삭제하시겠습니까?')) {

                this.$log.debug('컨텐츠 삭제 요청 ID :', content.contentId)

                this.removeCondition.contentId = content.contentId
                this.removeCondition.contentType = content.contentType

                deleteContentList.push(this.removeCondition)

                this.$log.debug('컨텐츠 삭제 요청 인자 :', deleteContentList)

                // if(content.contentType === '000') // 공고문
                // {
                //   request.noti.removeNoti(deleteContentList)
                //       .then(result => {
                //           if (result.data.result === 200) {
                //               // deleteContentList.forEach(contentId => this.contentList.some((content, index, contentiList) => {
                //               //     if (content.contentId === contentId) {
                //               //         contentiList.splice(index, 1)
                //               //
                //               //         return true
                //               //     }
                //               // }))
                //               //
                //               //
                //               // this.pagination.totalCount -= deleteContentList.length
                //
                //               confirm('선택한 컨텐츠를 삭제했습니다.')
                //
                //               this.searchNoti(1)  // reload
                //
                //             } else {
                //                 this.$log.error('컨텐츠 삭제를 실패했습니다. ', result)
                //                 alert(result.data.resultMsg)
                //             }
                //         })
                //         .catch(err => {
                //             this.$log.error('컨텐츠 삭제를 실패했습니다. err :', err)
                //             alert('컨텐츠 삭제를 실패했습니다.\n(' + err.message + ')')
                //         })
                // }
                // else { // 홍보물
                  request.content.removeContent(deleteContentList)
                        .then(result => {
                            if (result.data.result === 200) {
                                // deleteContentList.forEach(contentId => this.contentList.some((content, index, contentiList) => {
                                //     if (content.contentId === contentId) {
                                //         contentiList.splice(index, 1)
                                //
                                //         return true
                                //     }
                                // }))
                                //
                                // this.pagination.totalCount -= deleteContentList.length

                                confirm('선택한 컨텐츠를 삭제했습니다.')

                                this.searchContent(1)  // reload

                              } else {
                                  this.$log.error('컨텐츠 삭제를 실패했습니다. ', result)
                                  alert(result.data.resultMsg)
                              }
                          })
                          .catch(err => {
                              this.$log.error('컨텐츠 삭제를 실패했습니다. err :', err)
                              alert('컨텐츠 삭제를 실패했습니다.\n(' + err.message + ')')
                          })
                // }

              } else {
                  this.$log.debug('컨텐츠 삭제 안함')
              }
          },
            removeContents () {
              let deleteContentList = Array.from(
                  {
                      length: 1
                  },
                  (k, v) => {
                          return {
                          contentId: 1,
                          contentType: '001'
                      }
                  })

                let removeContentList

                removeContentList = this.contentList.reduce(
                    (removeContentList, content) => {
                        if (content.checked) {
                            // removeContentList.push(content.contentId)
                            removeContentList.push({
                                contentId: content.contentId,
                                contentType: content.contentType
                            })
                        }

                        return removeContentList
                    },
                    [])

                this.$log.debug('컨텐츠 삭제 요청 목록 :', removeContentList)

                // 선택된 것이 없음
                if(removeContentList.length < 1)
                {
                  alert('선택된 것이 없습니다.')
                  return
                }


                // if (confirm('선택한 항목을 삭제하시겠습니까?')) {
                   // remove contentList
                    //{{
                    request.content.removeContent(removeContentList)
                        .then(result => {
                            if (result.data.result === 200) {
                                // removeContentList.forEach(contentId => this.contentList.some((content, index, contentList) => {
                                //     if (content.contentId === contentId) {
                                //         contentList.splice(index, 1)
                                //
                                //         return true
                                //     }
                                // }))
                                //
                                // this.pagination.totalCount -= removeContentList.length

                                confirm('선택한 컨텐츠를 삭제했습니다.')

                                this.searchContent(1)  // reload
                            } else {
                                this.$log.error('컨텐츠 삭제를 실패했습니다. ', result)
                                alert(result.data.resultMsg)
                            }
                        })
                        .catch(err => {
                            this.$log.error('컨텐츠 삭제를 실패했습니다. err :', err)
                            alert('컨텐츠 삭제를 실패했습니다.\n(' + err.message + ')')
                        })
                //}
            },
            dateToFormat (date) {
                return utils.date.dateToFormat(date)
            },
            getSortingClass (sortingType) {
                let className

                switch (sortingType) {
                    case SORTING_TYPE.DESC:
                        className = 'down'
                        break
                    case SORTING_TYPE.ASCE:
                    default:
                        className = 'up'

                }

                return className
            },
            changeSorting (attrName) {
                this.sorting[attrName] = this.sorting[attrName] === SORTING_TYPE.ASCE ? SORTING_TYPE.DESC : SORTING_TYPE.ASCE

                this.sorting.reqSortingCol = attrName
                this.sorting.reqSortingType = this.sorting[attrName]

                this.searchContent(this.pagination.pageNum)
            },
            searchContent (pageNum) {
                this.$log.debug('searchCondition ', this.searchCondition)
                let condition = {
                    pageNum: pageNum,
                    pageCount: this.pagination.pageCount,

                    sortingCol: this.sorting.reqSortingCol,
                    sortingType: this.sorting.reqSortingType
                }

                if (this.searchCondition) {
                    condition = Object.assign(
                        condition,
                        // this.searchCondition.startDate ? {startDate: utils.date.dateToFormat(this.searchCondition.startDate)} : {},
                        // this.searchCondition.endDate ? {endDate: utils.date.dateToFormat(this.searchCondition.endDate)} : {},
                        this.searchCondition.startDate ? {startDate: this.searchCondition.startDate} : {},
                        this.searchCondition.endDate ? {endDate: this.searchCondition.endDate} : {},
                        this.searchCondition.tags ? {tags: this.searchCondition.tags} : {},
                        this.searchCondition.title ? {title: this.searchCondition.title} : {},
                        this.searchCondition.contentType.length > 0 ? {searchContentType: this.searchCondition.contentType.map(contentType => contentType.value)} : {}
                    )
                }
                request.content.searchContent(condition)
                    .then(result => {
                        this.$log.debug('success get content list ', result)

                        if (result.data.result === 200) {
                            this.contentList = result.data.list.map(content => {
                                content.checked = false

                                return content
                            })

                            this.$refs.pagination.updatePagination({
                                pageNum: pageNum,
                                totalCount: result.data.totalCount
                            })
                        } else {
                            alert(result.data.resultMsg)
                        }
                    })
                    .catch(err => {
                        alert('컨텐츠 목록을 조회 해오는 것을 실패했습니다.\n(' + err.message + ')')
                    })
            },
            updateContent (content) {
                //if (confirm('해당 컨텐츠를 수정하시겠습니까?')) {
                    this.$log.debug('컨텐츠 수정 페이지로 이동')

                    if(content.contentType === '005') // 공고문
                    {
                        this.$router.push({
                            path: UPDATE_NOTI_PAGE_ROUTE_PATH,
                            query: {
                                contentId: content.contentId
                            }
                        })
                    }
                    else  // 홍보물
                    {
                        this.$router.push({
                            path: UPDATE_PROMOTION_PAGE_ROUTE_PATH,
                            query: {
                                contentId: content.contentId
                            }
                        })
                    }
                // } else {
                //     this.$log.debug('컨텐츠 수정 안함')
                // }
            },
            openPreviewModal (data) {
                this.$log.debug('openPreviewModal ', data)

                if(data.contentType === '005') // 공고문
                {
                  this.$store.dispatch(
                      'preview/setData',
                      {
                          title: '공고문 미리보기 : ' + data.title,
                          contentPath: data.contentPath,
                          contents: data.contents,
                          boardType: BOARD_TYPE.ANNOUNCE
                      })
                }
                else{
                this.$store.dispatch(
                    'preview/setData',
                    {
                        title: '홍보물 컨텐츠 미리보기 : ' + data.title,
                        boardType: BOARD_TYPE.PROMOTION,
                        contentType: data.contentType,
                        contentPath: data.contentType === CONTENT_TYPE_ALL[3].value ? data.contentInfo : data.contentPath + '/' + data.contentName
                    })
                }
            },
            getContentTypeIcon(data)
            {
              //this.$log.debug('contentList -> getContentTypeIcon() -> contentType ', data.contentType)

              switch (data.contentType) {
                case CONTENT_TYPE_ALL[0].value:
                  return "fa fa-file-image-o" // 이미지
                  break;
                case CONTENT_TYPE_ALL[1].value:
                  return "fa fa-file-movie-o" // 동영상
                  break;
                case CONTENT_TYPE_ALL[2].value:
                  return "fa fa-file-text-o" // 자막
                  break;
                case CONTENT_TYPE_ALL[3].value:
                  return "fa fa-file-word-o" // web url
                  break;
                case CONTENT_TYPE_ALL[4].value:
                  return "fa fa-file-pdf-o" // 공고문
                  break;
                default:
                  return "fa fa-file-image-o"
              }
            },
            getDisplayContentTypeName (content) {
                //this.$log.debug('CONTENT_TYPE ', content.contentType)
                let contentType = CONTENT_TYPE_ALL.find(contentType => contentType.value == content.contentType)

                return contentType ? contentType.displayName : ''
            }
        }
    }
</script>

<style>
    span.list-count {
        padding-left: 18.2;
        margin-left: 15px;
        font-size: inherit;
    }

    a.list-title {
        cursor: pointer;
    }

    i.sort-icon {
        cursor: pointer;
    }

    img.update-icon {
        margin-left: 5px;
        cursor: pointer;
    }

    a.page-link {
        cursor: pointer;
    }
</style>
