<template>
    <div class="modal fade" ref="vuemodal" id="preview-modal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">{{title}}</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <!--<div class="modal-body modal-body-preview">-->
                <div class="modal-body">
                    <div style="height:650px">
                        <div class="col">
                            <div v-if="boardType === BOARD_TYPE.ANNOUNCE && contents.length > 0" id="carouselExampleIndicators" class="carousel slide" data-ride="carousel" data-interval="false">
                                <ol class="carousel-indicators">
                                    <span class="badge rounded-pill bg-warning">{{contents.length}}</span>
                                    <li v-for="(content, index) in contents" data-target="#carouselExampleIndicators" :data-slide-to="index"></li>
                                </ol>
                                <div class="carousel-inner">
                                    <div v-for="content in contents" class="carousel-item" :class="{active: content.isActive}">
                                        <img class="d-block w-100" :src="contentPath + '/' + content.fileName">
                                    </div>
                                </div>
                                <a v-if="boardType === BOARD_TYPE.ANNOUNCE && contents.length > 1" class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                                    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                                    <span class="sr-only">Previous</span>
                                </a>
                                <a v-if="boardType === BOARD_TYPE.ANNOUNCE && contents.length > 1" class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                                    <span class="carousel-control-next-icon" aria-hidden="true"></span>
                                    <span class="sr-only">Next</span>
                                </a>
                            </div>
                            <!-- 이미지 -->
                            <div v-else-if="boardType === BOARD_TYPE.PROMOTION && contentType === CONTENT_TYPE[0].value" id="carouselExampleIndicators" class="carousel slide" data-ride="carousel" data-interval="false">
                                <img class="img-fluid img-thumbnail" style="display:block;width: 100%; height: auto; max-height: 660px;" :src="contentPath">
                            </div>
                            <!-- 동영상 -->
                            <div v-else-if="boardType === BOARD_TYPE.PROMOTION && contentType === CONTENT_TYPE[1].value" id="carouselExampleIndicators" class="col-6 carousel slide" data-ride="carousel" data-interval="false">
                                <video id="previewVideo" ref="vuevideo" class="d-block embed-responsive embed-responsive-16by9" style="display:block;width: 410px; height: auto" :src="contentPath" controls autoplay loop/>
                            </div>
                            <!-- 자막 -->
                            <div v-else-if="boardType === BOARD_TYPE.PROMOTION && contentType === CONTENT_TYPE[2].value" id="carouselExampleIndicators" class="col-6 carousel slide" data-ride="carousel" data-interval="false">
                                <div class="row col" style="display:block; width: 410px; height: 610px">{{contentPath}}</div>
                            </div>
                            <!-- 웹URL -->
                            <div v-else-if="boardType === BOARD_TYPE.PROMOTION && contentType === CONTENT_TYPE[3].value" id="carouselExampleIndicators" class="col-6 carousel slide" data-ride="carousel" data-interval="false">
                                <iframe :src="contentPath" style="display:block; width: 410px; height: 610px"/>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-primary" data-dismiss="modal">닫기</button>
                </div>
                </div>
            </div>
        </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'

import BOARD_TYPE from '../../components/code/boardType'
import CONTENT_TYPE from '../../components/code/contentType'

const { mapState } = createNamespacedHelpers('preview')

export default {
    data () {
        return {
            BOARD_TYPE: BOARD_TYPE,
            CONTENT_TYPE: CONTENT_TYPE,
        }
    },
    created () {
    },
    computed: {
        ...mapState({
            title (state) {
                return state.title
            },
            contentPath (state) {
                if(this.boardType === BOARD_TYPE.ANNOUNCE )
                {
                }else {
                    // 이미지 캐시를 막기 위해 경로뒤에 변화되는 값인 getTime()을 붙여줌
                    this.$log.debug('preview::contentPath() : contentPath -> ', state.contentPath)
                    this.$log.debug('preview::contentPath() : contentPath -> ', state.contentPath.indexOf('?'))
                    if(this.contentType === CONTENT_TYPE[0].value && state.contentPath.indexOf('?') == -1) // 이미지이면
                    {
                        var tmpDate = new Date();
                        state.contentPath = state.contentPath + '?' + tmpDate.getTime()

                        this.$log.debug('preview::contentPath() : contentPath -> add time ', state.contentPath)
                    }
                }
                return state.contentPath
            },
            contents (state) {
                state.contents.forEach((content, index) => {
                    if (index === 0) {
                        content.isActive = true
                    } else {
                        content.isActive = false
                    }

                    this.$log.debug('preview::contents (state) called : contentName ', content.fileName)

                    if(this.boardType === BOARD_TYPE.ANNOUNCE )
                    {
                        // 이미지 캐시를 막기 위해 경로뒤에 변화되는 값인 getTime()을 붙여줌
                        {
                            this.$log.debug('preview::contents (state) : contentName ', content.fileName)

                            var tmpDate = new Date();
                            content.fileName = content.fileName + '?' + tmpDate.getTime()
                            this.$log.debug('preview::contents (state) : contentName -> add time ', content.fileName)
                        }
                    }
                })


                return state.contents
            },
            boardType (state) {
                return state.boardType
            },
            contentType (state) {
                return state.contentType
            }
        })
    },
    methods: {
        doSomethingOnHidden () {
            //alert('modal closed')
            console.log('modal closed ', this.$store)
            //this.title = null
            //this.contentPath = null
            //this.contents = null
//            if (this.boardType === BOARD_TYPE.PROMOTION && this.contentType === CONTENT_TYPE[1].value) {
//                this.$log.debug('!@#@!#', this.$refs)
//
//                //this.$refs.vuevideo.stop()
//            }

            this.$store.dispatch('preview/clear')

            //$(this.$refs.vuevideo).trigger('stop')

            $(this).removeData('bs.modal');
            $(this).data('modal', null)

        }
    },
    mounted() {
        $(this.$refs.vuemodal).on("hide.bs.modal", this.doSomethingOnHidden)

        // $(this.$refs.vuemodal).on("hidden.bs.modal", this.doSomethingOnHidden)

        // $('body').on('hidden', function () {
        //   $(this).removeData('modal');
        //   });
    }
}
</script>

<style>
div.modal-body.modal-body-preview {
    text-align: center;
}

.carousel-control-next,
.carousel-control-prev {
    filter: invert(100%);
}
</style>
