export default {
    namespaced: true,
    state: {
        menu: null,
        selectedSubMenu: {},
        isReload: false
    },
    getters: {
        selectedMenu (state, getters) {
            return state.menu
        },
        selectedSubMenu (state, getters) {
            return state.selectedSubMenu
        },
        isReload (state) {
            return state.isReload
        }
    },
    mutations: {
        selectMenu (state, menu) {
            if (menu) {
                state.menu = menu
            }
        },
        selectSubMenu (state, subMenu) {
            if (subMenu) {
                state.selectedSubMenu = subMenu
            }
        },
        reload (state) {
            console.log('reload')
            state.isReload = true

            setTimeout(() => state.isReload = false, 500)
        }
    },
    actions: {
        selectMenu (context, menu) {
            context.commit('selectMenu', menu)
        },
        selectSubMenu (context, subMenu) {
            context.commit('selectSubMenu', subMenu)
        },
        reload (context) {
            context.commit('reload')
        }
    }
}
