<template>
  <main class="main">
    <!-- Breadcrumb-->
    <ol class="breadcrumb">
        <li class="breadcrumb-item">디지털 게시판 설정</li>
    </ol>

<div class="container-fluid">
  <div class="row">
    <div class="col-sm-10">
      <div class="card">
        <div class="card-header">
          <i class="icon-note"></i><strong>디지털 게시판</strong>
        </div>
        <div class="card-body">
        <!-- 지점 ID -->
          <div class="row">
            <div class="col-sm-12">
              <div class="form-group">
                <label for="name">지점 ID</label>
                <div class="input-group">
                  <span class="input-group-prepend">
                    <span class="input-group-text">
                      <i class="fa fa-sitemap"></i>
                    </span>
                  </span>
                  <input type="text" class="form-control" placeholder="지점 ID" readonly v-model="client.organId"/>
                </div>
              </div>
            </div>
          </div>
          <!-- 클라이언트 명 -->
          <div class="row">
            <div class="col-sm-12">
              <div class="form-group">
                <label for="name">클라이언트 명</label>
                <div class="input-group">
                  <span class="input-group-prepend">
                    <span class="input-group-text">
                      <i class="fa fa-newspaper-o"></i>
                    </span>
                  </span>
                  <input type="text" class="form-control" placeholder="클라이언트명을 입력하세요" v-model="client.clientName"/>
                </div>
              </div>
            </div>
          </div>
          <!-- 클라이언트 ID -->
          <div class="row">
            <div class="col-sm-12">
              <div class="form-group">
                <label for="name">클라이언트 ID</label>
                <div class="input-group">
                  <span class="input-group-prepend">
                    <span class="input-group-text">
                      <i class="fa fa-credit-card"></i>
                    </span>
                  </span>
                  <input type="text" class="form-control" placeholder="클라이언트 ID" readonly v-model="client.clientId"/>
                </div>
              </div>
            </div>
          </div>
          <!-- 설치 위치 -->
          <div class="row">
            <div class="col-sm-12">
              <div class="form-group">
                <label for="name">설치 위치</label>
                <div class="input-group">
                  <span class="input-group-prepend">
                    <span class="input-group-text">
                      <i class="fa fa-map-pin"></i>
                    </span>
                  </span>
                  <input type="text" class="form-control" placeholder="설치 위치" readonly v-model="client.organName"/>
                </div>
              </div>
            </div>
          </div>
          <!-- 디지털 게시판 설정 -->
          <div class="card" style="margin-bottom: 0.7rem">
      		    <div class="card-header">
      			  <i class="icon-layers"></i><strong>디지털 게시판 설정</strong>
        			<div class="card-header-actions">
        				<a class="card-header-action btn-minimize" href="#" data-toggle="collapse" data-target="#digiBoard" aria-expanded="true">
        				<i class="icon-arrow-up"></i>
        				</a>
        			</div>
      		    </div>


      		    <div class="collapse show" id="digiBoard">
      			    <div class="card-body">
                  <!-- 모니터수 -->
                  <div class="row">
                    <div class="col-sm-12">
                      <div class="form-group">
                        <label for="name">모니터수</label>
                        <div class="input-group">
                          <span class="input-group-prepend">
                            <span class="input-group-text">
                              <i class="fa fa-laptop"></i>
                            </span>
                          </span>
                          <dropdown ref="monitorCountDropdown" :items="monitorCounts" @selectedItem="selectMonitorCount"></dropdown>
                        </div>
                      </div>
                    </div>
                  </div>
                  <!-- 해상도 -->
                  <div class="row">
                    <div class="col-3">
                      <div class="form-group">
                        <label for="name">해상도 타입</label>
                        <div class="col">
                            <input type="radio" name="resolutionType" value="1" v-model="monitorResolutionType"/> 세로
                        </div>
                        <div class="col">
                            <input type="radio" name="resolutionType" value="0" v-model="monitorResolutionType"/> 가로
                        </div>
                      </div>
                    </div>
                    <div class="col">
                      <div class="form-group">
                        <label for="name">해상도</label>
                        <div class="input-group">
                          <span class="input-group-prepend">
                            <span class="input-group-text">
                              <i class="fa fa-laptop"></i>
                            </span>
                          </span>
                          <dropdown :items="resolutions" display-attr="info" ref="resolutionDropdown" @selectedItem="selectMonitorResolution"></dropdown>
                        </div>
                      </div>
                    </div>
                  </div>
                  <!-- 디지털게시판 모니터별 타입 할당 -->
                  <div class="row" style="height: 150px;">
                      <div v-for="(board, index) in boards_init" v-show="board.show" class="col-2 row client-monitor">
                          <div class="col" style="text-align: center;">
                              <dropdown
                                  ref="contentTypeDropdown"
                                  :items="contentTypes"
                                  style="padding-top: 10px;"
                                  @selectedItem="function (contentType) {if(client.boards[index] != null) client.boards[index].boardType = contentType.value}"
                                  ></dropdown>
                          </div>
                      </div>
                  </div>
      				    </div>
      			    </div>
      		    </div>

            <div class="row" style="border-top: thin solid; padding-bottom: 10px;">
            </div>
              <div class="row">
                <div class="col-sm-3">
                  <button v-show="client.clientId" class="btn btn-primary" @click="updateClient">
                    <i class="fa fa-send" style="padding-right : 5px;">설정완료</button>
                </div>
            </div>

      	    </div>


        </div>
    </div>
  </div>
</div>
</main>
</template>


<script>
    import request from '../../../lib/request'

    import Dropdown from '../Dropdown.vue'

    export default {
        data () {
            return {
                monitorCounts: Array.from(
                    {
                        length: 6
                    },
                    (k, v) => {
                        let count = v + 1

                        return {
                            displayName: count,
                            value: count
                        }
                    }),
                resolutions: [
                ],
                contentTypes: [
                    {
                        displayName: '공고문',
                        value: '001'
                    },
                    {
                        displayName: '홍보용',
                        value: '002'
                    }
                ],
                boards_init:[{
                  boardType: '001',
                  show: true
                },
                {
                  boardType: '001',
                  show: false
                },
                {
                  boardType: '001',
                  show: false
                },
                {
                  boardType: '001',
                  show: false
                },
                {
                  boardType: '001',
                  show: false
                },
                {
                  boardType: '001',
                  show: false
                }],
                monitorResolutionType: '1',
                client: {
                    organId: '',
                    clientName: '',
                    clientId: '',
                    boards:[{
                      boardType: '001',
                    },
                    {
                      boardType: '001',
                    },
                    {
                      boardType: '001',
                    },
                    {
                      boardType: '001',
                    },
                    {
                      boardType: '001',
                    },
                    {
                      boardType: '001',
                    }],
                    organName: '',
                    monitorCount: 0,
                    resolution: null
                }
            }
        },
        components: {
            Dropdown
        },
        watch: {
            monitorResolutionType (oldVal, newVal) {
              // 해상도 타입 변경
              this.client.boards.forEach((board, index) => {
                // resolutionType
                board.layoutType = this.monitorResolutionType
              })

              // 해상도 정보 다시 받기
              this.getResolution()
            }
        },
        methods: {
            selectMonitorCount (monitorCount) {
                if (this.client.boards.length < monitorCount.value) {
                    // add
                    this.client.boards.splice(
                        this.client.boards.length,
                        0,
                        ...Array.from(
                            {
                                length: monitorCount.value - this.client.boards.length
                            },
                            (v, k) => {
                                return {
                                    boardType: '001',
                                    boardIndex: this.client.boards.length + k,
                                    resolutionId: 0,
                                    layoutType: 0
                                }
                            }))

                            this.boards_init.forEach((board, index) => {
                              if(index < monitorCount.value)
                                  board.show = true
                                else
                                  board.show = false

                              //this.$log.debug('selectMonitorCount -> board_Init : ', monitorCount.value, index, board.show)
                            })
                } else {
                    if (this.client.boards.length > monitorCount.value) {

                      this.boards_init.forEach((board, index) => {
                        if(index < monitorCount.value)
                            board.show = true
                          else
                            board.show = false

                            //this.$log.debug('selectMonitorCount -> board_Init : ', monitorCount.value, index, board.show)
                      })

                        // del
                        this.client.boards.splice(this.client.boards.length - (this.client.boards.length - monitorCount.value), this.client.boards.length - monitorCount.value)
                    }
                }
            },
            selectMonitorResolution (monitorResolution) {
                this.client.resolution = monitorResolution
            },
            getResolution () {

                this.$log.debug('getResolution () : monitorResolutionType ', this.monitorResolutionType)

                request.code.getResolution(parseInt(this.monitorResolutionType))
                    .then(result => {
                        this.$log.debug('getResolution ', result.data)

                        //this.resolutions = result.data.list
                        this.resolutions.splice(0, this.resolutions.length)
                        this.resolutions.splice(0, 0, ...result.data.list)
                        this.$log.debug('getResolution ', this.resolutions)

                        let def_resolution = this.resolutions[0];
                        if(this.monitorResolutionType == 0) // 가로
                        {
                          this.resolutions.forEach(resolution => {

                            if(resolution.width == 1920 & resolution.height == 1080)
                            {
                                def_resolution = resolution
                            }
                          })

                        }
                        else { // 세로
                          this.resolutions.forEach(resolution => {

                            if(resolution.width == 1080 & resolution.height == 1920)
                            {
                                def_resolution = resolution
                            }
                          })
                        }
                        this.$refs.resolutionDropdown.selectItem(def_resolution) //this.resolutions[0])
                    })
                    .catch(err => this.$log.error('getResolution error ', err))
            },
            updateClient () {
              // Check validation
              //{{
              if (this.client.clientName === '') {
                alert('클라이언트명을 입력하세요')
                return;
              }

              //}}

                // this.client.clientIp = '192.168.0.100'
                // this.client.clientInfo = '설치되었습니다.'


                this.client.boards.forEach((board, index) => {
                  // resolutionType
                  board.layoutType = this.monitorResolutionType
                  board.resolutionId = this.client.resolution.resolutionId
                })




                //if (confirm('디지털 게시판 설정을 저장하시겠습니까?')) {
                  request.client.updateClient(this.client)
                      .then(result => {
                          this.$log.debug('updateClient result ', result)
                          if (result.data.result === 200)
                          {
                            confirm('디지털 게시판을 수정했습니다.')
                          }
                          else {
                              throw new Error(result.data.resultMsg)
                          }
                      })
                      .then()
                      .catch(err => {
                          this.$log.error('updateClient fail ', err)
                          alert('디지털 게시판을 수정하지 못했습니다. (' + err.message + ')')
                      })
                //}
            }
        },
        created () {
            this.$log.debug('clientId ', this.$route.query.clientId)

            this.getResolution()

            if(this.$route.query.clientId != null)
            {
            request.client.getClient(this.$route.query.clientId)
                .then(result => {
                  this.$log.debug('clientId ', this.$route.query.clientId)
                  // this.$log.debug('get client ', result)

                    if (result.data.result === 200) {

                        this.client.organId = result.data.data.organId
                        this.client.clientName = result.data.data.clientName
                        this.client.clientId = result.data.data.clientId
                        this.client.boards = result.data.data.boards
                        this.client.organName = result.data.data.organName

                        this.$refs.monitorCountDropdown.selectItem(this.monitorCounts[result.data.data.boards.length - 1])

                        if(this.client.boards)
                        {
                        // setTimeout(
                        //     () => {
                                result.data.data.boards.forEach((board, index) => {

                                  // resolutionType
                                  this.monitorResolutionType = board.layoutType == 0 ? '0' : '1'

                                  //this.$log.debug('board.layoutType ', index, board.layoutType, this.monitorResolutionType)

                                  // resolution
                                  let def_resolution = this.resolutions[0];
                                  if(board.layoutType == 0) // 가로
                                  {
                                    this.resolutions.forEach(resolution => {

                                      if(resolution.width == board.width & resolution.height == board.height)
                                      {
                                          def_resolution = resolution
                                      }
                                    })

                                  }
                                  else { // 세로
                                    this.resolutions.forEach(resolution => {

                                      if(resolution.width == board.width & resolution.height == board.height)
                                      {
                                          def_resolution = resolution
                                      }
                                    })
                                  }
                                  this.$refs.resolutionDropdown.selectItem(def_resolution) //this.resolutions[0])

                                  // this.$log.debug('client.boards : ', this.client.boards.length, this.$refs.contentTypeDropdown.length)
                                  this.boards_init[index].show = true
                                  this.$refs.contentTypeDropdown[index].selectItem(this.contentTypes.find(contentType => contentType.value === board.boardType))
                                })
                            // },
                            // 1000)
                        }
                        else {
                          this.boards_init[0].show = false
                          // 1개의 데이터를 넣어줌
                          //this.client.boards.push({boardType: contentTypes[0].value, boardIndex: 0, resolutionId: 0, layoutType: 0})
                        }

                      } else {
                        throw new Error(result.data.resultMsg)
                    }
                })
                .catch(err => {
                    this.$log.error('클라이언트 정보 조회를 실패했습니다. ', err)

                    alert('클라이언트 정보 조회를 실패했습니다.\n(' + err.message + ')')
                })
              }
              else {
                // alert('선택된 클라이언트 정보가 없습니다.')
              }
        }
    }
</script>

<style>
    div.client-monitor {
        border-style: solid;
        border-width: thin;
        padding: 0px;
        margin: 0px;
    }
</style>
